import { createSelector } from 'reselect'
import { initialState } from './reducer'

const selectGlobal = (state) => state.global || initialState

const makeSelectUser = () =>
  createSelector(selectGlobal, (state) => state.user)

const makeSelectOperatTypes = () =>
  createSelector(selectGlobal, (state) => state.operatTypes)

const makeSelectEkw = () =>
  createSelector(selectGlobal, state => state.ekw)

const makeSelectPublicSettings = () =>
  createSelector(selectGlobal, state => state.publicSettings)

export {
  selectGlobal,
  makeSelectUser,
  makeSelectOperatTypes,
  makeSelectEkw,
  makeSelectPublicSettings
}
