import { callForHeader } from '../utils/HeadersCall'

const apiUrl = process.env.REACT_APP_API_URL

export const landRegisterService = {
  get,
  check
}

async function get (code) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  const url = encodeURIComponent(`${apiUrl}/LandRegister/data/${code}`)
  return await fetch(url, requestOptions).then((response) => handleResponse(response))
}

async function check (code) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return await fetch(`${apiUrl}/LandRegister/check/${code}`, requestOptions).then((response) => handleResponse(response))
}

function handleResponse (response) {
  return response.text().then(text => {
    const data = text && JSON.parse(text)
    if (!response.ok) {
      const error = (data && data) || response.statusText
      return Promise.reject(error)
    }
    return data
  })
}
