import { callForHeader } from '../utils/HeadersCall'
const apiUrl = process.env.REACT_APP_API_URL

export const operatorFilesService = {
  getOperatorFiles,
  sendOperatorFile,
  removeOperatorFileByName,
  getFileBlob,
  getFileByFilename
}

async function getFileBlob (url, filename) {
  const requestOptions = {
    method: 'GET',
    responseType: 'blob'
  }
  return window.fetch(url, requestOptions).then(resp => handleDownloadResponse(resp, filename))
}

async function getFileByFilename (id, filename) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader()
  }
  return fetch(`${apiUrl}/OperatorFiles/id/${id}/filename/${filename}`, requestOptions).then(handleResponse)
}

async function sendOperatorFile (id, data, fileName = null) {
  const url = fileName ? `${apiUrl}/OperatorFiles/upload/${id}/filename/${fileName}` : `${apiUrl}/OperatorFiles/upload/${id}`
  const formData = new FormData()

  formData.append('file', data.file, data.file.name)

  const requestOptions = {
    method: 'POST',
    headers: await callForHeader(),
    body: formData
  }

  return await window.fetch(url, requestOptions).then(handleResponse)
}

async function getOperatorFiles (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader()
  }

  return await window.fetch(`${apiUrl}/OperatorFiles/id/${id}`, requestOptions).then(handleResponse)
}

async function removeOperatorFileByName (id, name) {
  const requestOptions = {
    method: 'DELETE',
    headers: await callForHeader()
  }

  return await window.fetch(`${apiUrl}/OperatorFiles/id/${id}/filename/${name}`, requestOptions).then(handleResponse)
}

function handleResponse (response) {
  return response.text().then(text => {
    const data = text && JSON.parse(text)
    if (!response.ok) {
      if (response.status === 404) {
        return []
      }
      if (response.status === 500) {
        return []
      }

      if (response.status === 400) {
        return [data]
      }

      const error = (data && data.message) || response.statusText
      return Promise.reject(error)
    }
    return data
  })
}

function handleDownloadResponse (response, filename) {
  return response.blob()
    .then((blob) => {
      // Create blob link to download
      const url = window.URL.createObjectURL(
        new Blob([blob])
      )
      const link = document.createElement('a')
      link.href = url
      link.setAttribute(
        'download',
        filename
      )

      // Append to html link element page
      document.body.appendChild(link)

      // Start download
      link.click()

      // Clean up and remove the link
      link.parentNode.removeChild(link)
    }
    )
}
