import React, { memo, useEffect } from 'react'
import { useTranslation } from 'react-i18next'

import { Route, Switch, useHistory } from 'react-router-dom'

import {
  MsalProvider,
  AuthenticatedTemplate,
  UnauthenticatedTemplate,
  useMsal
} from '@azure/msal-react'
import { CustomNavigationClient } from '../../utils/NavigationClient'
import { EventType, InteractionType } from '@azure/msal-browser'
import { b2cPolicies } from '../../authConfig'

import 'bootstrap/dist/css/bootstrap.css'
import 'assets/scss/paper-dashboard.scss?v=1.2.0'
import 'assets/demo/demo.css'
import 'assets/css/styles.css'
import 'perfect-scrollbar/css/perfect-scrollbar.css'

import GeneralLayout from '../../layouts/GeneralLayout'
import Login from '../Login'
import LoadingModal from '../Modal/LoadingModal'
import { createStructuredSelector } from 'reselect'
import { connect } from 'react-redux'
import { compose } from 'redux'
import { useInjectReducer } from 'utils/injectReducer'
import reducer from './reducer'
import { useInjectSaga } from 'utils/injectSaga'
import saga from './saga'
import { ReactNotifications } from 'react-notifications-component'
import 'react-notifications-component/dist/theme.css'
import Chat from 'views/Chat/Chat'
import { makeSelectUser } from './selectors'
import { loadEkwData, loadOperatTypes, loadPublicSettings, loadUser } from './actions'
import * as ReactGA from "react-ga";
import CookieConsent, { Cookies } from 'react-cookie-consent'

const key = 'global'
const App = (props) => {
  useInjectReducer({ key, reducer })
  useInjectSaga({ key, saga })
  // The next 3 lines are optional. This is how you configure MSAL to take advantage of the router's navigate functions when MSAL redirects between pages in your app
  const history = useHistory()
  const navigationClient = new CustomNavigationClient(history)
  props.pca.setNavigationClient(navigationClient)
  return (
    <MsalProvider instance={props.pca}>
      <ReactNotifications />
      <MainContent
        pca={props.pca} handleLoadUser={props.handleLoadUser}
        handleLoadOperatTypes={props.handleLoadOperatTypes}
        handleLoadPublicSettings={props.handleLoadPublicSettings}
        handleLoadEkwData={props.handleLoadEkwData}
      />
    </MsalProvider>
  )
}

const MainContent = (props) => {
  const { instance } = useMsal()
  useInjectReducer({ key, reducer })
  useInjectSaga({ key, saga })
  const { t } = useTranslation('common')
  /**
   * Using the event API, you can register an event callback that will do something when an event is emitted.
   * When registering an event callback in a react component you will need to make sure you do 2 things.
   * 1) The callback is registered only once
   * 2) The callback is unregistered before the component unmounts.
   * For more, visit: https://github.com/AzureAD/microsoft-authentication-library-for-js/blob/dev/lib/msal-react/docs/events.md
   */
  useEffect(() => {
    const callbackId = instance.addEventCallback((event) => {
      if (event.eventType === EventType.LOGIN_FAILURE) {
        if (
          event.error &&
          event.error.errorMessage.indexOf('AADB2C90118') > -1
        ) {
          if (event.interactionType === InteractionType.Redirect) {
            instance.loginRedirect(b2cPolicies.authorities.forgotPassword)
          } else if (event.interactionType === InteractionType.Popup) {
            instance
              .loginPopup(b2cPolicies.authorities.forgotPassword)

              .catch((e) => {

              })
          }
        }
        if (
          event.error &&
          event.error.errorMessage.indexOf('AADB2C90091') > -1
        ) {
          if (event.interactionType === InteractionType.Redirect) {
            instance.loginRedirect(b2cPolicies.authorities.signUpSignIn)
          } else if (event.interactionType === InteractionType.Popup) {
            instance
              .loginPopup(b2cPolicies.authorities.signUpSignIn)
              .catch((e) => {

              })
          }
        }
      }

      if (event.eventType === EventType.LOGIN_SUCCESS) {
        if (event?.payload) {
          const accounts = props.pca.getAllAccounts()
          if (accounts.length > 0) {
            props.pca.setActiveAccount(accounts[0])
            props.handleLoadUser(accounts[0].idTokenClaims.oid)
            props.handleLoadEkwData()
            props.handleLoadPublicSettings()
            props.handleLoadOperatTypes()
          }
          /**
           * We need to reject id tokens that were not issued with the default sign-in policy.
           * "acr" claim in the token tells us what policy is used (NOTE: for new policies (v2.0), use "tfp" instead of "acr").
           * To learn more about B2C tokens, visit https://docs.microsoft.com/en-us/azure/active-directory-b2c/tokens-overview
           */
          if (
            event.payload.idTokenClaims.tfp ===
            b2cPolicies.names.forgotPassword
          ) {
            window.alert(
              'Password has been reset successfully. \nPlease sign-in with your new password'
            )
            return instance.logout()
          }
        }
      }
    })

    return () => {
      if (callbackId) {
        instance.removeEventCallback(callbackId)
      }
    }
    // eslint-disable-next-line
  }, [instance])

  const initializeGoogleAnalytics = (id) => {
    if (process.env.NODE_ENV === 'production') {
      ReactGA.initialize(id);
    }
  };
  
  const handleAcceptCookies = () => {
    if (process.env.REACT_APP_GOOGLE_ANALYTICS_ID) {
      initializeGoogleAnalytics(process.env.REACT_APP_GOOGLE_ANALYTICS_ID)
    }
  }
  
  const handleDeclineCookies = () => {
    //remove google analytics cookies
    Cookies.remove('_ga');
    Cookies.remove('_gat');
    Cookies.remove('_gid');
  };

  return (
    
    <div className='App'>
      <CookieConsent 
        enableDeclineButton
        flipButtons
        onAccept={handleAcceptCookies}
        onDecline={handleDeclineCookies}
        declineButtonText={t('cookies.declineButtonText')}
        buttonText={t('cookies.acceptButtonText')}
        buttonClasses='btn'
        declineButtonClasses='btn'
        buttonStyle={{color:'#FFFFFF', backgroundColor:'#6bd098'}}
        declineButtonStyle={{color:'#FFFFFF', backgroundColor:'#ef8157'}}
      >
        {t('cookies.consentText')} <a href="https://aoperat.pl/privacy-policy#cookie-section" rel="noreferrer" target="_blank">{t('cookies.learnMoreText')}</a>
      </CookieConsent>


      <AuthenticatedTemplate>
        <Pages />
      </AuthenticatedTemplate>

      <UnauthenticatedTemplate>
        <Login />
      </UnauthenticatedTemplate>
    </div>
  )
}

function Pages (props) {
  const { instance } = useMsal()
  const { t } = useTranslation('common')

  const activeAccount = () => {
    const acc = instance.getActiveAccount()
    return acc
  }

  return (
    <>
      <Switch>
        <Route path='/' render={(props) => <GeneralLayout t={t} {...props} account={activeAccount()} />} />
      </Switch>
      <LoadingModal />
      <Chat />
    </>
  )
}

const mapStateToProps = createStructuredSelector({
  user: makeSelectUser()
})

export function mapDispatchToProps (dispatch) {
  return {
    handleLoadOperatTypes: () => dispatch(loadOperatTypes()),
    handleLoadEkwData: () => dispatch(loadEkwData()),
    handleLoadPublicSettings: () => dispatch(loadPublicSettings()),
    handleLoadUser: (id) => dispatch(loadUser(id))
  }
}

const withConnect = connect(mapStateToProps, mapDispatchToProps)

export default compose(withConnect, memo)(App)
