import { modalTypeConstants } from '../../constants'
import { call, put, select, takeEvery } from 'redux-saga/effects'
import { callForHeader } from 'utils/HeadersCall'
import request from 'utils/request'
import { makeSelectUser } from 'views/App/selectors'
import { hideLoadingScreen, showLoadingScreen } from 'views/Modal/actions'

import { loadOrders, loadOrdersError, loadPropositions, loadPropositionsError, orderReplaced, orderSaved, ordersLoaded, propositionReplaced, propositionsLoaded, replaceOrder, saveOrderError } from './actions'
import { LOAD_ORDERS, LOAD_PROPOSITIONS, RESERVE_ORDER_AND_PROPOSITION, SAVE_ORDER, SAVE_PROPOSITION, UNRESERVE_PROPOSITION, REPLACE_ORDER, REMOVE_ORDER, PAY_FOR_OPERAT } from './constants'
import {
  makeSelectActiveOrder,
  makeSelectOrders,
  makeSelectPropositionId,
  makeSelectPropositions
} from './selectors'
import { handleRequestWithTimeout } from 'views/Modal/timeout'

export function * getOrders () {
  const orders = yield select(makeSelectOrders())
  let requestUrl = `/Order/user?pageNumber=${orders.pageNumber}`
  if (orders.filters) {
    requestUrl += `&locationPostCode=${orders.filters.locationPostCode}`
  }
  if (orders.sortFieldName) {
    requestUrl += `&fieldName=${orders.sortFieldName}&direction=${orders.sortDirection}`
  }
  const headers = yield call(callForHeader)
  const options = { method: 'GET', headers }
  try {
    const orders = yield call(request, requestUrl, options)
    yield put(ordersLoaded(orders))
  } catch (err) {
    yield put(loadOrdersError(err))
  }
}

export function * saveOrder ({ payload: order }) {
  const { address, landRegisterCode, latitudeLongitude, ...restOfOrder } = order
  const user = yield select(makeSelectUser())

  const body = {
    location: address,
    ekw: landRegisterCode,
    clientId: user.user.id,
    latitudeLongitude: `${latitudeLongitude.lat},${latitudeLongitude.lng}`,
    ...restOfOrder
  }

  const requestUrl = '/Order'
  const headers = yield call(callForHeader, 'json')
  const options = { method: 'POST', headers, body: JSON.stringify(body) }
  try {
    yield call(handleRequestWithTimeout, requestUrl, options)
    yield put(orderSaved())
    yield put(loadOrders())
    yield put(showLoadingScreen(modalTypeConstants.CONFIRM_SUCCESS, { message: 'Zlecenie zostało stworzone' }))
  } catch (err) {
    yield put(saveOrderError(err))
  }
}

export function * replaceOrderSaga () {
  const activeOrder = yield select(makeSelectActiveOrder())
  const requestUrl = `/Order/id/${activeOrder.id}`
  const headers = yield call(callForHeader)
  const options = { method: 'GET', headers }
  try {
    const order = yield call(request, requestUrl, options)
    yield put(orderReplaced(order))
  } catch (err) { }
}

export function * getPropositions () {
  const activeOrder = yield select(makeSelectActiveOrder())
  const propositions = yield select(makeSelectPropositions())
  let requestUrl = `/OrderProposition/orderId/${activeOrder.id}/by-user?pageNumber=1`
  if (propositions.sortFieldName) {
    requestUrl += `&fieldName=${propositions.sortFieldName}&direction=${propositions.sortDirection}`
  }
  if (propositions.filters) {
    if (propositions.filters.priceFrom !== '') { requestUrl += `&priceFrom=${propositions.filters.priceFrom}` }
    if (propositions.filters.priceTo !== '') { requestUrl += `&priceTo=${propositions.filters.priceTo}` }
    if (propositions.filters.upTillDateFrom !== '') { requestUrl += `&upTillDateFrom=${propositions.filters.upTillDateFrom}` }
    if (propositions.filters.upTillDateTo !== '') { requestUrl += `&upTillDateTo=${propositions.filters.upTillDateTo}` }
  }
  const headers = yield call(callForHeader)
  const options = { method: 'GET', headers }
  try {
    const propositions = yield call(handleRequestWithTimeout, requestUrl, options)
    yield put(propositionsLoaded(propositions))
  } catch (err) {
    yield put(loadPropositionsError(err))
  }
}

export function * saveProposition () {
  const activeOrder = yield select(makeSelectActiveOrder())
  const user = yield select(makeSelectUser())
  const formElement = document.querySelector('#new-offer')
  const data = new FormData(formElement)
  const body = {
    orderId: activeOrder.id,
    description: data.get('description') !== '' ? data.get('description') : `${data.get('exampleNumber').value} PLN do dnia ${data.get('date').value}`,
    operatorId: user.user.id,
    price: data.get('exampleNumber'),
    upTillDate: data.get('date')
  }

  const requestUrl = '/OrderProposition?pageNumber=1'
  const headers = yield call(callForHeader, 'json')
  const options = { method: 'POST', headers, body: JSON.stringify(body) }
  try {
    yield call(handleRequestWithTimeout, requestUrl, options)
    yield put(loadPropositions())
  } catch (err) {
    console.log(err)
  }
}

export function * reserveOrderAndProposition () {
  const propositionId = yield select(makeSelectPropositionId())
  const requestURL = `/OrderProposition/id/${propositionId}/reserve`
  yield call(replaceProposition, requestURL)
}

function * replaceProposition (requestURL) {
  const headers = yield call(callForHeader)
  const options = { method: 'PUT', headers }
  try {
    const proposition = yield call(handleRequestWithTimeout, requestURL, options)
    yield put(propositionReplaced(proposition))
    yield put(replaceOrder())
  } catch (err) {
    console.log(err)
  }
}

export function * unreserveProposition () {
  const propositionId = yield select(makeSelectPropositionId())
  const requestURL = `/OrderProposition/id/${propositionId}/unreserve`
  yield call(replaceProposition, requestURL)
}

export function * removeOrder () {
  const activeOrder = yield select(makeSelectActiveOrder())
  const requestURL = `/Order/id/${activeOrder.id}`
  const headers = yield call(callForHeader)
  const options = { method: 'DELETE', headers }
  try {
    yield call(handleRequestWithTimeout, requestURL, options)
    yield put(loadOrders())
  } catch (err) {
    console.log(err)
  }
}

export function * payForOperat () {
  const activeOrder = yield select(makeSelectActiveOrder())
  const requestUrl = `/Order/id/${activeOrder.id}/pay`
  const headers = yield call(callForHeader)
  const options = { method: 'PUT', headers }
  try {
    yield put(showLoadingScreen(modalTypeConstants.LOADING, { message: 'payingForOperat', translate: true }))
    yield call(handleRequestWithTimeout, requestUrl, options)
    yield put(loadOrders())
    yield put(hideLoadingScreen())
  } catch (err) {
    console.log(err)
    yield put(hideLoadingScreen())
  }
}

export default function * dashboardData () {
  yield takeEvery(LOAD_ORDERS, getOrders)
  yield takeEvery(SAVE_ORDER, saveOrder)
  yield takeEvery(REPLACE_ORDER, replaceOrderSaga)
  yield takeEvery(LOAD_PROPOSITIONS, getPropositions)
  yield takeEvery(SAVE_PROPOSITION, saveProposition)
  yield takeEvery(RESERVE_ORDER_AND_PROPOSITION, reserveOrderAndProposition)
  yield takeEvery(UNRESERVE_PROPOSITION, unreserveProposition)
  yield takeEvery(REMOVE_ORDER, removeOrder)
  yield takeEvery(PAY_FOR_OPERAT, payForOperat)
}
