//TODO: zmoienic rozszerzenie na js
import React, { memo, useEffect, useState } from 'react'
import BootstrapTable from 'react-bootstrap-table-next'
import { useTranslation } from 'react-i18next'
import {
  Col, Row, Button, Collapse, Card, CardBody
} from 'reactstrap'
import { userRolesConstants } from '../../constants'
import HistoryRow from './HistoryRow'
import Pagination from '@mui/material/Pagination'
import { useHistory } from 'react-router'
import { createStructuredSelector } from 'reselect'
import { makeSelectOperatOrder, makeSelectOperats } from './selectors'
import { createOperat, loadOperatOrder, loadOperats, removeOperat, selectOperatType, sendPdf, setActiveOperat, setOperatFilters, setOperatSortParameters, setPageNumber, uploadOwnGeneratedOperat, uploadSignedOperat } from './actions'
import { connect, useSelector } from 'react-redux'
import { compose } from 'redux'
import { makeSelectOperatTypes } from 'views/App/selectors'
import { useInjectReducer } from 'utils/injectReducer'
import reducer from './reducer'
import saga from './saga'
import { useInjectSaga } from 'utils/injectSaga'
import LoadingIndicator from 'components/LoadingIndicator'
import NewOperatForm from 'components/NewOperatForm'
import OperatFilters from 'components/Filters/Operat'
import ShowIdCheckbox from 'components/ShowIdCheckbox'
const key = 'operatsPage'
function History (props) {
  useInjectReducer({ key, reducer })
  useInjectSaga({ key, saga })
  const { t } = useTranslation('common')
  const history = useHistory()
  const user = useSelector(state => state.global.user)
  const [isIdHidden, setIsIdHidden] = useState(true)
  const [isFiltersVisible, setIsFiltersVisible] = useState(false)
  const [isNewOperatFormVisible, setIsNewOperatFormVisible] = useState(false)
  const isAdmin = user.userType === userRolesConstants.ADMIN
  useEffect(() => {
    const search = new URLSearchParams(history.location.search)
    if (!search.get('pageNumber')) { props.handleSetPageNumber('1') }
    // eslint-disable-next-line
  }, [])
  useEffect(() => {
    props.handleLoadOperats()
    // eslint-disable-next-line
  }, [history.location.search, props.operats.filters, props.operats.sortDirection, props.operats.sortFieldName])
  const sortable = {
    onSort: (field, order) => {
      props.handleSetOperatSortParameters(field)
    },
    sortFunc: (a, b, order, dataField, rowA, rowB) => {

    },
    sort: true
  }
  const columns = [
    {
      dataField: 'id',
      text: 'ID',
      hidden: isIdHidden
    },
    {
      dataField: 'inProcess',
      text: t('history.table.headers.inProgress'),
      editable: false,
      headerStyle: { width: '15%' },
      align: 'center',
      headerAlign: 'center',
      formatter: (cellContent, row) => (
        <label>
          <input type='checkbox' defaultChecked={row.inProcess} />
        </label>
      )
    },
    {
      dataField: 'createdDateTime',
      text: t('history.table.headers.applicationDate'),
      align: 'center',
      headerAlign: 'center',
      editable: false,
      ...sortable,
      sort: true,
      formatter: (cellContent, row) => <div>{cellContent.split('T')[0]}</div>,
      headerStyle: (colum, colIndex) => {
        return { width: '15%', textAlign: 'center' }
      }
    },
    {
      dataField: 'name',
      ...sortable,
      sort: true,
      text: t('history.table.headers.informations'),
      headerAlign: 'center',
      align: 'center',
      editable: false
    },
    {
      dataField: 'author',
      sort: false,
      text: t('history.table.headers.author'),
      headerAlign: 'center',
      align: 'center',
      editable: false
    }
  ]

  const expandRow = () => {
    return {
      onlyOneExpanding: true,
      onExpand: (row, isExpand, rowIndex, e) => {
        props.handleSetActiveOperat(row)
      },
      renderer: (row) => (
        <HistoryRow
          row={row} history={props.history}
          handleRemoveOperat={props.handleRemoveOperat} handleLoadOperats={props.handleLoadOperats}
          handleLoadOperatOrder={props.handleLoadOperatOrder}
          operatOrder={props.operatOrder}
          handleSendPdf={props.handleSendPdf}
          handleSetActiveOperat={props.handleSetActiveOperat}
          handleUploadOwnGeneratedOperat={props.handleUploadOwnGeneratedOperat}
          handleUploadSignedOperat={props.handleUploadSignedOperat}
        />
      )
    }
  }
  const handleChangePageNumber = (event, newPage) => {
    const search = new URLSearchParams(history.location.search)
    search.set('pageNumber', newPage.toString())
    history.push({
      pathname: '/history',
      search: search.toString()
    })
    props.handleSetPageNumber(newPage.toString())
    props.handleLoadOperats()
  }

  return (
    <div className='content'>
      <div className='container'>
        {isAdmin && <ShowIdCheckbox setIsIdHidden={(e) => setIsIdHidden(e)} isIdHidden={isIdHidden} label={t('history.showOperatId')} />}
        <Row>
          <div>
            <Button onClick={() => setIsNewOperatFormVisible(isNewOperatFormVisible => !isNewOperatFormVisible)}>{t('history.table.buttons.createOperat')}</Button>
            <Collapse isOpen={isNewOperatFormVisible}>
              <Card>
                <CardBody>
                  <NewOperatForm
                    operatTypes={props.operatTypes}
                    handleCreateOperat={props.handleCreateOperat}
                    handleSelectOperatType={props.handleSelectOperatType}
                  />
                </CardBody>
              </Card>
            </Collapse>
          </div>
          <Col md='12'>
            <Button color='success' onClick={() => setIsFiltersVisible(v => !v)}>{t('history.table.buttons.filters')}</Button>
            <Collapse isOpen={isFiltersVisible}>
              <OperatFilters handleSetOperatFilters={props.handleSetOperatFilters} />
            </Collapse>
            <Pagination count={localStorage.getItem('pageCount')} color='primary' page={(new URLSearchParams(history.location.search)).get('pageNumber') ? parseInt((new URLSearchParams(history.location.search)).get('pageNumber')) : 1} onChange={handleChangePageNumber} />
            {
              props.operats.loading
                ? <LoadingIndicator />
                : <BootstrapTable
                    bootstrap4
                    keyField='id'
                    data={props.operats.operats}
                    columns={columns}
                    striped
                    hover
                    condensed
                    bordered={false}
                    expandRow={expandRow()}
                  />
            }
          </Col>
        </Row>
      </div>
    </div>
  )
}

const mapStateToProps = createStructuredSelector({
  operats: makeSelectOperats(),
  operatTypes: makeSelectOperatTypes(),
  operatOrder: makeSelectOperatOrder()
})

export function mapDispatchToProps (dispatch) {
  return {
    handleLoadOperats: () => dispatch(loadOperats()),
    handleRemoveOperat: () => dispatch(removeOperat()),
    handleSetActiveOperat: operat => dispatch(setActiveOperat(operat)),
    handleCreateOperat: () => dispatch(createOperat()),
    handleSelectOperatType: (operatType) => dispatch(selectOperatType(operatType)),
    handleSetPageNumber: (pageNumber) => dispatch(setPageNumber(pageNumber)),
    handleLoadOperatOrder: () => dispatch(loadOperatOrder()),
    handleSendPdf: (id, emails, sendToPrincipal) => dispatch(sendPdf(id, emails, sendToPrincipal)),
    handleUploadOwnGeneratedOperat: (id, file) => dispatch(uploadOwnGeneratedOperat(id, file)),
    handleUploadSignedOperat: (id, file) => dispatch(uploadSignedOperat(id, file)),
    handleSetOperatFilters: (filters) => dispatch(setOperatFilters(filters)),
    handleSetOperatSortParameters: fieldName => dispatch(setOperatSortParameters(fieldName))
  }
}

const withConnect = connect(mapStateToProps, mapDispatchToProps)

export default compose(withConnect, memo)(History)
