import { ADD_TRAINING, LOAD_OPERATOR_PROFILE_DATA, LOAD_OPERATOR_PROFILE_DATA_ERROR, LOAD_TRAININGS, LOAD_TRAININGS_ERROR, OPERATOR_PROFILE_DATA_LOADED, REMOVE_TRAINING, TRAININGS_LOADED } from './constants'

export function loadTrainings () {
  return {
    type: LOAD_TRAININGS
  }
}
export function trainingsLoaded (trainings) {
  return {
    type: TRAININGS_LOADED,
    payload: trainings
  }
}
export function loadTrainingsError (err) {
  return {
    type: LOAD_TRAININGS_ERROR,
    payload: err
  }
}

export function loadOperatorProfileData (operatorId) {
  return {
    type: LOAD_OPERATOR_PROFILE_DATA,
    payload: operatorId
  }
}

export function operatorProfileDataLoaded (payload) {
  return {
    type: OPERATOR_PROFILE_DATA_LOADED,
    payload
  }
}

export function loadOperatorProfileDataError (payload) {
  return {
    type: LOAD_OPERATOR_PROFILE_DATA_ERROR,
    payload
  }
}

export function addTraining () {
  return {
    type: ADD_TRAINING
  }
}

export function removeTraining (id) {
  return {
    type: REMOVE_TRAINING,
    id
  }
}
