import { createSelector } from 'reselect'
import { initialState } from './reducer'

const selectUser = (state) => state.user || initialState

const makeSelectTrainings = () =>
  createSelector(selectUser, (state) => state.trainings)

const makeSelectOperatorProfileData = () =>
  createSelector(selectUser, (state) => state.operatorProfileData)

export {
  selectUser,
  makeSelectTrainings,
  makeSelectOperatorProfileData
}
