import React, { useState } from "react";
import {
  Modal,
  ModalHeader,
  ModalBody,
  Col,
  Row,
  Container,
  Card,
  CardImgOverlay,
  CardImg,
} from "reactstrap";

import ImageCarousel from "./ImageCarousel";
import { useSelector } from "react-redux";

function ReactstrapImageGallery (props) {
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [currentIndex, setCurrentIndex] = useState(0)
  const operatState = useSelector(state => state.operat)
  const images = props.hasOwnProperty("images") ? props.images : []
  const imageLimit = props.hasOwnProperty("limit") ? props.limit : 6
  const visibleImages = images.slice(0,imageLimit)
  const hasMore = images.length !== 0 ? images.length - imageLimit : 0
  console.log(visibleImages)
  const handleToggleModal = () => {
    setIsModalOpen(!isModalOpen)
  }
  const handleShowImage = (imageIndex) => {
    setCurrentIndex(imageIndex)
    handleToggleModal()
  }

  return (
  <Container>
    <Row>
      <Col md={{size:10,offset:1}} className="thumbnail-gallery">
        <h5 className="text-center my-3">Galeria zdjęć </h5>
        <Row>
          {visibleImages.map((image, index) => (
            <Col
              md="3"
              className="my-2"
              key={index}
              onClick={() => handleShowImage(index)}
            >
              <Card className="image-card">
                <CardImg src={image.url?.includes('blob.core.windows.net') ?`${image.url}&date=${operatState.randomString}` : image.url} />
                <p>{image.title}</p>
                {hasMore !== 0 && index === visibleImages - 1 ? (
                  <CardImgOverlay className="overlay">
                    <h2 className="mb-0">{hasMore}</h2>
                    <small> More </small>
                  </CardImgOverlay>
                ) : null}
              </Card>
            </Col>
          ))}
        </Row>
      </Col>
    </Row>
    <Modal
      className="modal-xl"
      isOpen={isModalOpen}
      toggle={handleToggleModal}
    >
      <ModalHeader>Galeria zdjęć</ModalHeader>
      <ModalBody>
        <Row>
          <Col md="12">
            <ImageCarousel images={images} currentIndex={currentIndex} />
          </Col>
        </Row>
      </ModalBody>
    </Modal>
  </Container>)
}

export default ReactstrapImageGallery;