import { createSelector } from 'reselect'
import { initialState } from './reducer'

const selectModal = (state) => state.modal || initialState

const makeSelectShowModal = () =>
  createSelector(selectModal, (state) => state.showModal)

const makeSelectType = () =>
  createSelector(selectModal, (state) => state.type)

const makeSelectData = () =>
  createSelector(selectModal, (state) => state.data)

export {
  selectModal,
  makeSelectShowModal,
  makeSelectType,
  makeSelectData
}
