export function loadOperatInput (dispatch, url, useCache) {
  operatFilesService.getFile(url, useCache).then(
    input => {
      dispatch({
        type: FILES_GET_INPUT,
        payload: {
          input: input.input
        }
      })
    }
  ).then(() => {
    dispatch(hideLoadingScreen())
  })
}

