export function generateNewRowForCSV (keys) {
  const newRow = { id: uuidv4() }
  keys.forEach(x => { x === 'Data' ? newRow[x] = formatDate(new Date()) : newRow[x] = '' })

  return newRow
}

export function generateNewRowsForCSVFromCSV (rows) {
  const lines = []
  rows.forEach(element => {
    const row = element.split(';')
    lines.push({
      Data: row[0],
      CenaTransakcyjna: row[1],
      RodzajRynku: row[2],
      UdzialWPrawie: row[3],
      PowierzchniaLokalu: row[4],
      Adres: row[5],
      KondygnacjaLokalu: row[6],
      KondygnacjaBudynku: row[7],
      LiczbaIzb: row[8].replace('/r', ''),
      id: uuidv4()
    })
  })
  return lines
}

export function csvJSON (csv) {
  const lines = csv.split('\n')
  const result = []
  const headers = lines[0].split(',')[0].split(';')

  for (let i = 1; i < lines.length; i++) {
    const obj = {}
    const currentline = lines[i].split(';')
    for (let j = 0; j < headers.length; j++) {
      obj[headers[j].trim()] = currentline[j]
    }
    obj.id = uuidv4()
    result.push(obj)
  }

  return result
}

