import GoogleMapReact from 'google-map-react'
import React, { useEffect, useState } from 'react'
import { Button } from 'reactstrap'
function MapWithMarker (props) {
  const [center, setCenter] = useState({
    lat: 52.2492292821819,
    lng: 21.0120537940507
  })
  const [loadedCoords, setLoadedCoords] = useState(false)

  useEffect(() => {
    if (props.locationInfo.latlng.lat !== 0) {
      setCenter({ lat: props.locationInfo.latlng.lat, lng: props.locationInfo.latlng.lng })
      setLoadedCoords(true)
    } else {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(position => {
          setCenter({ lat: position.coords.latitude, lng: position.coords.longitude })
          setLoadedCoords(true)
        }, () => setLoadedCoords(true))
      } else { setLoadedCoords(true) }
    }
  }, [props.locationInfo.latlng.lat, props.locationInfo.latlng.lng])

  function geocodeAddress (map, maps) {
    const address = `${props.locationInfo.address} ${props.locationInfo.locationPostCode}`

    const geocoder = new maps.Geocoder()
    const infowindow = new maps.InfoWindow()

    geocoder
      .geocode({ address: address })
      .then(({ results }) => {
        if (results[0]) {
          map.setZoom(11)
          map.setCenter(results[0].geometry.location)

          const marker = new maps.Marker({
            draggable: true,
            map,
            position: results[0].geometry.location
          })
          makeMarkerDraggable(marker)
          infowindow.setContent(results[0].formatted_address)
          infowindow.open(map, marker)
          props.setCoords({ lat: marker.position.lat(), lng: marker.position.lng() })

          return marker
        } else {

        }
      })
  }

  const handleApiLoaded = (map, maps) => {
    if (props.locationInfo.address !== '') {
      return geocodeAddress(map, maps)
    } else {
      return geocodeFromLocation(maps, map)
    }
  }

  return (
    // Important! Always set the container height explicitly  
    <div>
      <div style={{display: 'flex'}}>
        <Button onClick={props.close} color='danger' className='mb-3' style={{marginLeft: 'auto', fontWeight: 'bold', fontSize: '16px'}}>X</Button>
      </div>
      <div style={{ height: '75vh', width: '75vh' }}>
        
        {loadedCoords &&
          <GoogleMapReact
            bootstrapURLKeys={{ key: process.env.REACT_APP_GOOGLE_API_KEY }}
            defaultCenter={center}
            defaultZoom={11}
            yesIWantToUseGoogleMapApiInternals
            onGoogleApiLoaded={({ map, maps }) => handleApiLoaded(map, maps)}
          />}
      </div>
    </div>
  )

  function geocodeFromLocation (maps, map) {
    const infowindow = new maps.InfoWindow()

    const marker = new maps.Marker({
      position: center,
      map,
      draggable: true
    })
    makeMarkerDraggable(marker)
    infowindow.setContent('Przeciągnij mnie')
    infowindow.open(map, marker)
    props.setCoords({ lat: marker.position.lat(), lng: marker.position.lng() })

    return marker
  }

  function makeMarkerDraggable (marker) {
    marker.addListener('mouseup', () => {
      props.setCoords({ lat: marker.position.lat(), lng: marker.position.lng() })
      // console.log({ lat: marker.position.lat(), lng: marker.position.lng() })
    })
  }
}

export { MapWithMarker }
