import { useMsal } from '@azure/msal-react'
import React, { memo, useEffect, useRef, useState } from 'react'
import BootstrapTable from 'react-bootstrap-table-next'
import { useTranslation } from 'react-i18next'
import {
  Button, Card, CardBody, Col, Collapse, Input, Row, UncontrolledTooltip
} from 'reactstrap'
import { connect, useSelector } from 'react-redux'
import { userRolesConstants } from '../../constants'
import { useHistory } from 'react-router'
import Pagination from '@mui/material/Pagination'
import UserInfoUncontrolledCollapse from '../Common/OrderInfoUncontrolledCollapse'
import AnnouncedOrder from './AnnouncedOrder'
import NewOrderForm from 'components/NewOrderForm'
import PaidOrder from './PaidOrder'
import ReservedOrder from './ReservedOrder'
import SignedOrder from './SignedOrder'
import MarketFilters from 'components/Filters/Market'
import { createStructuredSelector } from 'reselect'
import { makeSelectActiveOrder, makeSelectOrders, makeSelectPropositions, makeSelectSaveOrder } from './selectors'
import { loadOrders, loadPropositions, removeOrder, replaceOrder, reserveOrderAndProposition, saveOrder, saveProposition, setActiveOrder, setOrdersPageNumber, setPropositionId, unReserveProposition, setOrderSortParameters, setOrderFilters, setPropositionsSortParameters, setPropositionFilters, clearPropositionFilters, payForOperat } from './actions'
import { compose } from 'redux'
import { useInjectReducer } from 'utils/injectReducer'
import reducer from './reducer'
import saga from './saga'
import { useInjectSaga } from 'utils/injectSaga'
import LoadingIndicator from 'components/LoadingIndicator'
import { makeSelectOperatTypes, makeSelectUser, makeSelectEkw } from 'views/App/selectors'
import { loadOperatTypes, loadUser, loadEkwData, loadPublicSettings } from 'views/App/actions'
import ShowIdCheckbox from 'components/ShowIdCheckbox'
import { showConfirmBox } from 'utils/showConfirmBox'
import UserInfo from 'views/Common/UserInfo'
const key = 'marketPage'
function Market (props) {
  useInjectReducer({ key, reducer })
  useInjectSaga({ key, saga })
  const { t } = useTranslation('common')
  const user = useSelector(state => state.global.user)
  const [newOrder, setNewOrder] = useState(false)
  const newOrderButton = useRef(null)
  const { accounts } = useMsal()
  const [isFiltersOpen, setIsFiltersOpen] = useState(false)
  const [isIdHidden, setIsIdHidden] = useState(true)
  const [operatorContactOpen, setOperatorContactOpen] = useState(false)
  const isAdmin = user.userType === userRolesConstants.ADMIN
  const isClient = user.userType === userRolesConstants.CLIENT
  const sortable = {
    onSort: (field, order) => {
      props.handleSetOrderSortParameters(field)
    },
    sortFunc: (a, b, order, dataField, rowA, rowB) => {

    },
    sort: true
  }
  const columns = () => {
    return [
      {
        dataField: 'id',
        text: 'ID',
        hidden: isIdHidden
      },
      {
        dataField: 'inProcess',
        text: t('market.table.headers.inProgress'),
        editable: false,
        align: 'center',
        sort: true,
        headerAlign: 'center',
        formatter: (cellContent, row) => (
          <Input type='checkbox' checked={row.status.toLowerCase() === 'inprogress'} disabled />
        ),
        headerStyle: (colum, colIndex) => {
          return { width: '10%', textAlign: 'center' }
        }
      },
      {
        dataField: 'createdDateTime',
        text: t('market.table.headers.applicationDate'),
        align: 'center',
        headerAlign: 'center',
        editable: false,
        ...sortable,
        formatter: (cellContent, row) => <div id={row.id}>{cellContent.split('T')[0]}</div>,
        headerStyle: (colum, colIndex) => {
          return { width: '15%', textAlign: 'center' }
        }
      },
      {
        dataField: 'name',
        text: t('market.table.headers.name'),
        align: 'center',
        headerAlign: 'center',
        ...sortable,
        editable: false,
        formatter: (cellContent, row) => (
          <div>
            <div id={`title-${row.id}`}>
              {cellContent.length > 30
                ? cellContent.substring(0, 30) + '...'
                : cellContent}
            </div>
            <UncontrolledTooltip placement='top' target={`title-${row.id}`}>
              {cellContent}
            </UncontrolledTooltip>
          </div>
        )
      },
      {
        dataField: 'locationPostCode',
        text: t('market.table.headers.postCode'),
        align: 'center',
        headerAlign: 'center',
        ...sortable,
        editable: false,
        formatter: (cellContent) => (
          <div>{cellContent}</div>
        )
      },
      {
        dataField: 'status',
        text: t('market.table.headers.status'),
        align: 'center',
        headerAlign: 'center',
        editable: false,
        formatter: (cellContent) => (
          <div>{t(`market.table.statuses.${cellContent}`)}</div>
        )
      }
    ]
  }

  const renderSwitch = (row) => {
    switch (row.status) {
      case 'announced':
        return (
          <AnnouncedOrder
            handleRemoveOrder={props.handleRemoveOrder} activeOrder={props.activeOrder}
            handleReserveOrderAndProposition={props.handleReserveOrderAndProposition}
            handleUnreserveProposition={props.handleUnreserveProposition}
            handleSetPropositionId={props.handleSetPropositionId}
            propositions={props.propositions}
            handleSaveProposition={props.handleSaveProposition}
            handleLoadPropositions={props.handleLoadPropositions}
            user={props.user}
            handleSetPropositionsSortParameters={props.handleSetPropositionsSortParameters}
            handleSetPropositionFilters={props.handleSetPropositionFilters}
          />
        )
      case 'reserved':
        return (
          <ReservedOrder
            activeOrder={props.activeOrder}
            handleReserveOrderAndProposition={props.handleReserveOrderAndProposition}
            handleUnreserveProposition={props.handleUnreserveProposition}
            handleSetPropositionId={props.handleSetPropositionId}
            propositions={props.propositions}
            handleLoadPropositions={props.handleLoadPropositions}
            handleReplaceOrder={props.handleReplaceOrder}
            handleSetPropositionsSortParameters={props.handleSetPropositionsSortParameters}
            handleSetPropositionFilters={props.handleSetPropositionFilters}
            handlePayForOperat={props.handlePayForOperat}
          />
        )
      case 'paid':
        return  <>
          { (isClient || isAdmin) && 
            <>
              <Button
              color='info' style={{ width: '272px', marginBottom: '1rem' }} onClick={() => {
                props.activeOrder.operators && history.push({ pathname: '/users-profile', search: '?operatorId=' + props.activeOrder.operators[0] })
              }}
              >
                Profil rzeczoznawcy
              </Button>
              <div>
                <Button
                  style={{ width: '272px', marginBottom: '1rem' }}
                  color='info'
                  onClick={() => setOperatorContactOpen(!operatorContactOpen)}
                >
                  Napisz do rzeczoznawcy
                </Button>
                <Collapse isOpen={operatorContactOpen} style={{ marginTop: '1rem' }}>
                  <Card>
                    <CardBody>
                      {operatorContactOpen && <UserInfo id={props.activeOrder.operators[0]} />}
                    </CardBody>
                  </Card>
                </Collapse> 
              </div>    
            </>
          }
            {!isClient && (row.operators[0] === accounts[0].idTokenClaims.oid) &&<PaidOrder activeOrder={props.activeOrder} operatTypes={props.operatTypes} />} 
        </>
      case 'signed':
        return <SignedOrder activeOrder={props.activeOrder} />
      default:
        break
    }
  }
  const [expanded, setExpanded] = useState('')
  const history = useHistory()
  const expandRow = () => {
    return {
      onlyOneExpanding: true,
      expanded: [expanded],
      renderer: (row) => (
        <div style={{ wordBreak: 'break-all' }}>
          <div>
            <Button
              className='long-btn'
              color='success'
              id='orderData'
              style={{ marginBottom: '1rem' }}
            >
              {t('market.table.buttons.orderData')}
            </Button>
            <UserInfoUncontrolledCollapse row={row} />
          </div>
          {renderSwitch(row)}
          <br />
          <Button
            className='long-btn'
            color='danger'
            disabled={!userRolesConstants.ADMIN === user.userType && props.activeOrder.clientId !== props.user.user.id}
            onClick={() => showConfirmBox('Czy na pewno chcesz usunąć zlecenie?', 'Ostrzeżenie', () => props.handleRemoveOrder())}
          >
            {t('market.table.buttons.deleteOrder')}
          </Button>
        </div>
      ),
      onExpand: (row) => {
        expanded === row.id ? setExpanded('') : setExpanded(row.id)
        const search = new URLSearchParams(history.location.search)
        search.set('id', row.id)
        history.push({
          pathname: '/market',
          search: search.toString()
        })
        props.handleSetActiveOrder(row)
      }
    }
  }

  useEffect(() => {
    const search = new URLSearchParams(history.location.search)
    if (!search.get('pageNumber')) { props.handleSetOrdersPageNumber('1') }
    // eslint-disable-next-line
  }, [])

  useEffect(() => {
    props.loadOrders()

    // eslint-disable-next-line
  }, [props.orders.filters])

  useEffect(() => {
    const search = new URLSearchParams(history.location.search)
    if (search.get('pageNumber')) { props.handleSetOrdersPageNumber(search.get('pageNumber')) }
    if (search.get('id')) { setExpanded(search.get('id')) }
    // eslint-disable-next-line
  }, [history.location.search])

  const handleChangePageNumber = (event, newPage) => {
    const search = new URLSearchParams(history.location.search)
    search.set('pageNumber', newPage.toString())
    history.push({
      pathname: '/market',
      search: search.toString()
    })
    props.handleSetOrdersPageNumber(newPage.toString())
    props.loadOrders()
  }

  return (
    <div className='content'>
      <div className='container'>
        <Button
          color='primary'
          onClick={() => {
            setNewOrder(!newOrder)
          }}
          innerRef={newOrderButton}
        >
          {t('market.table.buttons.newOrder')}
        </Button>
        <Collapse isOpen={newOrder}>
          <Card>
            <CardBody>
              <NewOrderForm
                saveOrder={props.saveOrder}
                onSubmit={props.handleSaveOrder}
                ekwData={props.ekwData}
              />
            </CardBody>
          </Card>
        </Collapse>
        <br />
        <Button color='success' onClick={() => setIsFiltersOpen(!isFiltersOpen)}>{t('market.table.buttons.filters')}</Button>
        <Collapse isOpen={isFiltersOpen}>
          <MarketFilters
            handleSetOrderFilters={props.handleSetOrderFilters}
          />
        </Collapse>

        {isAdmin && <ShowIdCheckbox setIsIdHidden={setIsIdHidden} label={t('market.showOrderId')} isIdHidden={isIdHidden} />}
        <Row>
          <Col md='12'>
            <Pagination count={localStorage.getItem('pageCount')} color='primary' page={(new URLSearchParams(history.location.search)).get('pageNumber') ? parseInt((new URLSearchParams(history.location.search)).get('pageNumber')) : 1} onChange={handleChangePageNumber} />
            {props.orders.loading
              ? <LoadingIndicator />
              : <BootstrapTable
                  bootstrap4
                  keyField='id'
                  data={props.orders.orders}
                  columns={columns()}
                  striped
                  hover
                  condensed
                  bordered={false}
                  expandRow={expandRow()}
                />}
          </Col>
        </Row>
      </div>
    </div>
  )
}
const mapStateToProps = createStructuredSelector({
  orders: makeSelectOrders(),
  saveOrder: makeSelectSaveOrder(),
  operatTypes: makeSelectOperatTypes(),
  activeOrder: makeSelectActiveOrder(),
  user: makeSelectUser(),
  propositions: makeSelectPropositions(),
  ekwData: makeSelectEkw()
})

export function mapDispatchToProps (dispatch) {
  return {
    loadOrders: () => dispatch(loadOrders()),
    handleSaveOrder: (order) => dispatch(saveOrder(order)),
    handleSetPropositionId: id => dispatch(setPropositionId(id)),
    handleReserveOrderAndProposition: () => dispatch(reserveOrderAndProposition()),
    handleUnreserveProposition: () => dispatch(unReserveProposition()),
    handleSetOrdersPageNumber: pageNumber => dispatch(setOrdersPageNumber(pageNumber)),
    handleLoadPropositions: () => dispatch(loadPropositions()),
    handleSaveProposition: () => dispatch(saveProposition()),
    handleSetActiveOrder: order => {
      dispatch(setActiveOrder(order))
      dispatch(clearPropositionFilters())
    },
    handleRemoveOrder: () => dispatch(removeOrder()),
    handleReplaceOrder: () => dispatch(replaceOrder()),
    handleLoadOperatTypes: () => dispatch(loadOperatTypes()),
    handleLoadUser: (userId) => dispatch(loadUser(userId)),
    handleSetOrderSortParameters: (fieldName) => {
      dispatch(setOrderSortParameters(fieldName))
      dispatch(loadOrders())
    },
    handleSetOrderFilters: filters => dispatch(setOrderFilters(filters)),
    handleSetPropositionsSortParameters: fieldName => dispatch(setPropositionsSortParameters(fieldName)),
    handleSetPropositionFilters: filters => dispatch(setPropositionFilters(filters)),
    handlePayForOperat: () => dispatch(payForOperat()),
    handleLoadEkwData: () => dispatch(loadEkwData()),
    handleLoadPublicSettings: () => dispatch(loadPublicSettings())
  }
}
const withConnect = connect(mapStateToProps, mapDispatchToProps)

export default compose(withConnect, memo)(Market)
