import React, { useState } from 'react'
import PropositionList from './PropositionList'
import UserInfo from 'views/Common/UserInfo'
import { Card, Button, Collapse, CardBody } from 'reactstrap'
import { modalTypeConstants, userRolesConstants } from '../../constants'
import { useMsal } from '@azure/msal-react'
import { useHistory } from 'react-router'
import { useDispatch, useSelector } from 'react-redux'
import { showLoadingScreen } from 'views/Modal/actions'
function ReservedOrder (props) {
  const dispatch = useDispatch()
  const { accounts } = useMsal()
  const user = useSelector(state => state.global.user)
  const history = useHistory()
  const [userInfo, setUserInfo] = useState(false)
  const [sendedOffers, setSendedOffers] = useState(false)
  const isAdmin = () => { return user.userType === userRolesConstants.ADMIN }
  const isClient = () => { return user.userType === userRolesConstants.CLIENT }
  return (
    <div>
      {((isClient() && props.activeOrder.clientId === user.user.id) || isAdmin()) && (
        <Button
          color='primary'
          className='long-btn'
          id='payButton'
          style={{ marginBottom: '1rem' }}
          onClick={() => {
            if (isClient()) dispatch(showLoadingScreen(modalTypeConstants.PAY_FOR_OPERAT, { orderId: props.activeOrder.id }))
            if (isAdmin()) props.handlePayForOperat()
          }}
        >
          zapłać
        </Button>
      )}
      <div>
        <Button
          color='secondary'
          className='long-btn proposition-list-toggler'
          onClick={() => setSendedOffers(!sendedOffers)}
          style={{ marginBottom: '1rem' }}
        >
          wysłane oferty
        </Button>
        <Collapse isOpen={sendedOffers}>
          <Card>
            <CardBody>
              {sendedOffers && <PropositionList
                handleReserveOrderAndProposition={props.handleReserveOrderAndProposition}
                handleUnreserveProposition={props.handleUnreserveProposition}
                handleSetPropositionId={props.handleSetPropositionId}
                propositions={props.propositions}
                handleLoadPropositions={props.handleLoadPropositions}
                activeOrder={props.activeOrder}
                handleSetPropositionsSortParameters={props.handleSetPropositionsSortParameters}
                handleSetPropositionFilters={props.handleSetPropositionFilters}
                               />}
            </CardBody>
          </Card>
        </Collapse>
      </div>
      {accounts[0].idTokenClaims.oid !== props.activeOrder.clientId &&
        <div>
          <Button
            color='info'
            className='long-btn'
            onClick={() => setUserInfo(!userInfo)}
            style={{ marginBottom: '1rem' }}
          >
            informacje o zleceniodawcy
          </Button>
          <Collapse isOpen={userInfo}>
            <Card>
              <CardBody>
                {userInfo && <UserInfo id={props.activeOrder.clientId} />}
              </CardBody>
            </Card>
          </Collapse>
        </div>}
      <div>
        <Button
          color='info' style={{ width: '272px', marginBottom: '1rem' }} onClick={() => {
            props.activeOrder.operators && history.push({ pathname: '/users-profile', search: '?operatorId=' + props.activeOrder.operators[0] })
          }}
        >Dane o rzeczoznawcy
        </Button>
      </div>
    </div>
  )
}

export default ReservedOrder
