import React, { useEffect, useState } from 'react'
import { userService, adminService, clientService, operatorService } from '../../services'
import { useTranslation } from 'react-i18next'

import { userRolesConstants, modalTypeConstants } from '../../constants'
import { useDispatch, useSelector } from 'react-redux'
import { showLoadingScreen, hideLoadingScreen } from 'views/Modal/actions'
import { Button } from 'reactstrap'
import { setCurrentlyChatWith, setChatIsOpen } from '../Chat/actions.js'
function UserInfo (props) {
  const [user, setUser] = useState(null)
  const { t } = useTranslation('common')
  const loggedInUser = useSelector(state => state.global.user)
  const dispatch = useDispatch()
  const isAdmin = loggedInUser.userType === userRolesConstants.ADMIN

  useEffect(() => {
    dispatch(showLoadingScreen(modalTypeConstants.LOADING, { message: 'loadingData', translate: true }))
    userService.getUserType(props.id).then((result) => {
      if (result === userRolesConstants.OPERATOR) {
        operatorService.getOperatorData(props.id).then((operator) => {
          operator.role = userRolesConstants.OPERATOR
          setUser(operator)
          dispatch(hideLoadingScreen())
        }).catch(err => {})
      }
      if (result === userRolesConstants.CLIENT) {
        clientService.getClientData(props.id).then((client) => {
          client.role = userRolesConstants.CLIENT
          setUser(client)
          dispatch(hideLoadingScreen())
        }).catch(err => {})
      }
      if (result === userRolesConstants.ADMIN) {
        adminService.getAdminDataById(props.id).then((admin) => {
          admin.role = userRolesConstants.ADMIN
          setUser(admin)
          dispatch(hideLoadingScreen())
        }).catch(err => {})
      }
      if (result === '') {
        dispatch(hideLoadingScreen())
      }
    }).catch(err => {}).finally(() => dispatch(hideLoadingScreen()))
  }, [props.id, dispatch])

  const createRow = (label, data) => data &&
    <div>
      {label + ': ' + data}
    </div>

  const createMailTo = (label, data) => data &&
    <div>
      {label + ': '}
      <a href={'mailto:' + data}>{data}</a>
    </div>
  return (
    <div>
      {user
        ? <div>
          {isAdmin && createRow(t('common.userInfo.role'), user.role)}
          {isAdmin && createRow('Id', user.id)}
          {createMailTo(t('common.userInfo.mail'), user.mail)}
          {createRow(t('common.userInfo.phone'), user.phone)}
          {createRow(t('common.userInfo.nameWithTitle'), user.nameWithTitle)}
          {createRow(t('common.userInfo.address'), user.address.replaceAll('undefined, ', '').replaceAll('undefined', ''))}
          <div><Button color='info' onClick={() => { dispatch(setCurrentlyChatWith(user)); dispatch(setChatIsOpen(true)) }}>{t('common.userInfo.sendMessage')}</Button></div>
          </div>
        : <div>
          {t('common.userInfo.noPayerInfo')}
        </div>}
    </div>
  )
}

export default UserInfo
