import request from 'utils/request'
import { call, delay, race } from 'redux-saga/effects'

const delayTime = 2 * 60 * 1000

export function * handleRequestWithTimeout (requestUrl, requestOptions) {
  const { result, timeout } = yield race({
    result: call(request, requestUrl, requestOptions),
    timeout: delay(delayTime)
  })

  if (timeout) {
    throw new Error('Timeout of 120 sec exceeded')
  }

  return result
}
