import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import {
  Card,
  CardBody,
  Input,
  Button,
  Form
} from 'reactstrap'

export const MarketFilters = ({ handleSetOrderFilters }) => {
  const { t } = useTranslation('common')
  const [locationPostCode, setLocationPostCode] = useState('')
  const handleClear = () => {
    setLocationPostCode('')
    handleSetOrderFilters({ locationPostCode: '' })
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    handleSetOrderFilters({ locationPostCode })
  }
  return (
    <Card>
      <CardBody>
        <Form onSubmit={handleSubmit}>
          <label for='filter-locationPostCode'>{t('market.filters.postalCode')}</label>
          <Input
            placeholder='' id='filter-locationPostCode' name='filter-locationPostCode' onChange={(e) => {
              setLocationPostCode(e.target.value)
            }} value={locationPostCode}
          />
          <Button onClick={handleClear}>{t('market.table.buttons.clearFilters')}</Button>
          <Button color='success'>{t('market.table.buttons.applyFilters')}</Button>
        </Form>
      </CardBody>
    </Card>
  )
}

export default MarketFilters
