import React, { useCallback, useState } from 'react'
import { useDropzone } from 'react-dropzone'
import { useMsal } from '@azure/msal-react'
import { v4 as uuidv4 } from 'uuid'
import { Table, Button } from 'reactstrap'
import { operatorFilesService } from '../../services'
import { modalTypeConstants } from '../../constants'
import confirm from 'reactstrap-confirm'
import { useDispatch } from 'react-redux'
import { hideLoadingScreen, showLoadingScreen } from 'views/Modal/actions'

function UserImageDropzone (props) {
  const baseStyle = {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: '20px',
    borderWidth: 2,
    borderRadius: 20,
    borderColor: '#c2b7b7',
    borderStyle: 'dashed',
    color: '#0e0c0c',
    outline: 'none'
  }

  const style = {
    ...baseStyle
  }

  const dispatch = useDispatch()
  const [uploadedFile, setUploadedFile] = useState([])
  const { accounts } = useMsal()

  const toBigWarning = async () => {
    await confirm({
      title: <>Ostrzeżenie</>,
      message: 'Twój plik jest za duży.',
      cancelText: 'OK'
    })
  }

  const status = { toSend: 'do wysłania', sended: 'wysłany' }

  const onDrop = useCallback((acceptedFiles) => {
    if (acceptedFiles.length > 0) {
      setUploadedFile((prevState) => prevState.length < props.maxLength
        ? [
            ...prevState,
            { id: uuidv4(), file: acceptedFiles[0], condition: status.toSend }
          ]
        : [...prevState])
    } else {
      toBigWarning()
    }
  }, [status.toSend, props.maxLength])

  const { getRootProps, getInputProps } = useDropzone({
    onDrop,
    multiple: false,
    accept: props.acceptedExt,
    minSize: 0,
    maxSize: 5120000
  })

  const send = (exactFile) => {
    dispatch(showLoadingScreen(modalTypeConstants.LOADING, { message: 'loadingData', translate: true }))
    switch (props.fileType) {
      case 'stamp': sendFile(exactFile, 'stamp.png'); break
      case 'pdf': sendFile(exactFile); break
      case 'diploma': sendFile(exactFile, 'diploma.pdf'); break
      case 'oc': sendFile(exactFile, 'oc.pdf'); break
      default:
    }
  }

  const handleResponse = (resp, exactFile) => {
    if (resp[0] !== 'Too many files to send') {
      setUploadedFile(uploadedFile.map(item => {
        if (item.id === exactFile.id) {
          item.condition = status.sended
        }
        return item
      }))
      dispatch(hideLoadingScreen())
    } else {
      dispatch(showLoadingScreen(modalTypeConstants.TOO_MUCH_OPERATOR_FILES, {}))
    }
  }

  const sendFile = (exactFile, filename) => {
    operatorFilesService.sendOperatorFile(
      accounts[0].idTokenClaims.oid,
      exactFile,
      filename
    ).then((resp) => handleResponse(resp, exactFile))
  }

  const generateRows = () => {
    return uploadedFile.map((item, idx) => (
      <tbody key={idx}>
        <tr>
          <th scope='row'>{idx + 1}</th>
          <td>{item.file.name}</td>
          <td>{item.condition}</td>
          <td>
            <Button
              color='primary'
              onClick={() => {
                send(item)
              }}
              disabled={item.condition === status.sended}
            >
              Wyślij
            </Button>
            {'    '}
            <Button
              color='warning'
              onClick={() => {
                setUploadedFile(
                  uploadedFile.filter((obj) => obj.id !== item.id)
                )
              }}
            >
              Usuń
            </Button>
          </td>
        </tr>
      </tbody>
    ))
  }

  const files = () => (
    <Table>
      <thead>
        <tr>
          <th>#</th>
          <th>nazwa pliku</th>
          <th>Status</th>
          <th>Akcja</th>
        </tr>
      </thead>
      {generateRows()}
    </Table>
  )

  return (
    <div>
      <section className='container'>
        <div {...getRootProps({ className: 'dropzone', style })}>
          <input {...getInputProps()} />
          <p>Upuść lub kliknij aby wczytac pliki.</p>
          <p>Akceptowane formaty pliku to: <b>{props.acceptedExt.replaceAll('.', '').toLowerCase()}</b>.</p>
          <p>Maksymalna wielkość pliku to 5 MB.</p>
        </div>
        <aside />
      </section>
      {uploadedFile.length > 0 && files()}
    </div>
  )
}

export default UserImageDropzone
