/* eslint-disable default-param-last */
import produce from 'immer'
import { capitalizeFirstLetter } from 'utils/capitalizeFirstLetter'
import {
  LOAD_ORDERS, SET_ORDERS_PAGE_NUMBER, ORDERS_LOADED,
  LOAD_ORDERS_ERROR,
  SAVE_ORDER, ORDER_SAVED, SAVE_ORDER_ERROR, ORDER_REPLACED,
  LOAD_PROPOSITIONS, PROPOSITIONS_LOADED, LOAD_PROPOSITIONS_ERROR,
  SET_PROPOSITION_ID, PROPOSITION_REPLACED, SET_ACTIVE_ORDER, ORDER_REMOVED,
  SET_ORDER_SORT_PARAMETERS, SET_ORDER_FILTERS, SET_PROPOSITIONS_SORT_PARAMETERS,
  SET_PROPOSITIONS_FILTER,
  CLEAR_PROPOSITION_FILTERS
} from './constants'

export const initialState = {
  activeOrder: {
    clientId: '',
    status: '',
    description: '',
    operators: [],
    hasOperator: false,
    location: '',
    locationPostCode: '',
    lattitudeLongtitude: '',
    requeiresZbpList: false,
    isPrivate: false,
    shouldBeValidate: false,
    name: '',
    createdDateTime: '',
    id: ''
  },
  orders: {
    loading: false,
    error: false,
    pageNumber: 1,
    sortFieldName: null,
    sortDirection: 'desc',
    filters: {
      locationPostCode: ''
    },
    orders: []
  },
  propositions: {
    loading: false,
    error: false,
    sortFieldName: null,
    sortDirection: 'desc',
    filters: {
      priceFrom: '',
      priceTo: '',
      upTillDateFrom: '',
      upTillDateTo: ''
    },
    propositions: []
  },
  saveOrder: {
    loading: false,
    error: false
  },
  propositionId: ''
}

/* eslint-disable default-case, no-param-reassign */
const marketPageReducer = (state = initialState, action) =>
  produce(state, (draft) => {
    switch (action.type) {
      case SET_ORDER_SORT_PARAMETERS:
        draft.orders.sortFieldName = capitalizeFirstLetter(action.fieldName)
        draft.orders.sortDirection = draft.orders.sortDirection === 'asc' ? 'desc' : 'asc'
        break
      case LOAD_ORDERS:
        draft.orders.orders = []
        draft.orders.loading = true
        draft.orders.error = false
        break
      case ORDERS_LOADED:
        draft.orders.loading = false
        draft.orders.orders = action.payload
        draft.orders.error = false
        break
      case LOAD_ORDERS_ERROR:
        draft.orders.loading = false
        draft.orders.error = action.payload
        break
      case SET_ORDERS_PAGE_NUMBER:
        draft.orders.pageNumber = action.payload
        break
      case SAVE_ORDER:
        draft.saveOrder.error = false
        draft.saveOrder.loading = true
        break
      case ORDER_SAVED:
        draft.saveOrder.error = false
        draft.saveOrder.loading = false
        break
      case SAVE_ORDER_ERROR:
        draft.saveOrder.error = action.payload
        draft.saveOrder.loading = false
        break
      case ORDER_REPLACED:
        draft.activeOrder = action.payload
        draft.orders.orders = [action.payload].concat(state.orders.orders.filter(x => x.id !== action.payload.id))
        break
      case LOAD_PROPOSITIONS:
        draft.propositions.loading = true
        draft.propositions.error = false
        draft.propositions.propositions = []
        break
      case PROPOSITIONS_LOADED:
        draft.propositions.loading = false
        draft.propositions.error = false
        draft.propositions.propositions = action.payload
        break
      case LOAD_PROPOSITIONS_ERROR:
        draft.propositions.loading = false
        draft.propositions.error = action.payload
        break
      case SET_PROPOSITION_ID:
        draft.propositionId = action.payload
        break
      case PROPOSITION_REPLACED:
        draft.propositions.propositions = state.propositions.propositions.filter(x => x.id !== action.payload.id).concat(action.payload)
        break
      case ORDER_REMOVED:
        draft.orders.orders = state.orders.orders.filter(x => x.id !== action.payload)
        break
      case SET_ACTIVE_ORDER:
        draft.activeOrder = action.payload
        break
      case SET_ORDER_FILTERS:
        draft.orders.filters = action.payload
        break
      case SET_PROPOSITIONS_SORT_PARAMETERS:
        draft.propositions.sortFieldName = capitalizeFirstLetter(action.payload)
        draft.propositions.sortDirection = draft.propositions.sortDirection === 'asc' ? 'desc' : 'asc'
        break
      case SET_PROPOSITIONS_FILTER:
        draft.propositions.filters = action.payload
        break
      case CLEAR_PROPOSITION_FILTERS:
        draft.propositions.filters = initialState.propositions.filters
        break
    }
  })
export default marketPageReducer
