/* eslint-disable default-param-last */
import produce from 'immer'
import { capitalizeFirstLetter } from 'utils/capitalizeFirstLetter'
import {
  LOAD_USERS, USERS_LOADED, LOAD_USERS_ERROR, SET_USER_TYPE, SET_USER_SORT_PARAMETERS, SET_USERS_PAGE_NUMBER, SET_USER_FILTERS
} from './constants'

export const initialState = {
  userType: 'client',
  users: {
    loading: false,
    error: false,
    pageNumber: 1,
    sortFieldName: null,
    sortDirection: 'desc',
    users: [],
    filters: {
      mail: '',
      name: ''
    }
  }
}

/* eslint-disable default-case, no-param-reassign */
const usersPageReducer = (state = initialState, action) =>
  produce(state, (draft) => {
    switch (action.type) {
      case SET_USER_TYPE:
        draft.userType = action.payload
        break
      case LOAD_USERS:
        draft.users.loading = true
        draft.users.error = false
        break
      case USERS_LOADED:
        draft.users.loading = false
        draft.users.users = action.payload
        break
      case LOAD_USERS_ERROR:
        draft.users.loading = false
        draft.users.error = action.payload
        break
      case SET_USER_SORT_PARAMETERS:
        draft.users.sortFieldName = capitalizeFirstLetter(action.payload)
        draft.users.sortDirection = draft.users.sortDirection === 'asc' ? 'desc' : 'asc'
        break
      case SET_USERS_PAGE_NUMBER:
        draft.users.pageNumber = action.payload
        break
      case SET_USER_FILTERS:
        draft.users.filters = action.payload
        break
    }
  })

export default usersPageReducer
