import { callForHeader } from 'utils/HeadersCall'
const apiUrl = process.env.REACT_APP_API_URL

export const adminService = {
  getAdminDataById,
  promote,
  updateUserInfo
}

async function updateUserInfo (body) {
  const requestOptions = {
    method: 'PUT',
    headers: await callForHeader('json'),
    body: JSON.stringify(body)
  }

  return await window.fetch(`${apiUrl}/Admin/by-repo`, requestOptions).then(handleResponse)
}

async function getAdminDataById (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/Admin/id/${id}`, requestOptions).then(handleResponse)
}

async function promote (operatorId) {
  const requestOptions = {
    method: 'PUT',
    headers: await callForHeader()
  }

  return await window.fetch(`${apiUrl}/Admin/promote/operatorid/${operatorId}`, requestOptions).then((response) => handleResponse(response, true))
}

function handleResponse (response, promotion = false) {
  return response.text().then(text => {
    const data = text && JSON.parse(text)
    if (!response.ok) {
      if (response.status === 400 && promotion) {
        return response.status
      }

      const error = (data && data.message) || response.statusText
      return Promise.reject(error)
    }

    return data
  })
}
