import { globalConstants, modalTypeConstants } from '../../constants'
import { call, put, select, takeLatest } from 'redux-saga/effects'
import request from 'utils/request'
import { makeSelectUser } from 'views/App/selectors'
import { loadOperatOrderError, loadOperats, loadOperatsError, operatOrderLoaded, operatsLoaded } from './actions'
import { CREATE_OPERAT, LOAD_OPERAT_ORDER, LOAD_OPERATS, REMOVE_OPERAT, SEND_PDF, UPLOAD_OWN_GENERATED, UPLOAD_SIGNED_OPERAT } from './constants'

import { makeSelectActiveOperat, makeSelectOperats, makeSelectPageNumber, makeSelectSelectedOperatType } from './selectors'
import { showLoadingScreen, hideLoadingScreen } from 'views/Modal/actions'
import { callForHeader } from 'utils/HeadersCall'
import { addNotification } from 'utils/addNotification'
import { handleRequestWithTimeout } from 'views/Modal/timeout'
import handleRawParameters from 'utils/parametersMapper'

export function * getOperats () {
  const pageNumber = yield select(makeSelectPageNumber())
  const operats = yield select(makeSelectOperats())
  let requestUrl
  requestUrl = '/Operat/my?pageNumber=' + pageNumber
  if (operats.filters.createdDateTimeFrom !== '') {
    requestUrl += `&createdDateTimeFrom=${operats.filters.createdDateTimeFrom}`
  }
  if (operats.filters.createdDateTimeTo !== '') {
    requestUrl += `&createdDateTimeTo=${operats.filters.createdDateTimeTo}`
  }
  if (operats.sortFieldName) {
    requestUrl += `&fieldName=${operats.sortFieldName}&direction=${operats.sortDirection}`
  }
  const headers = yield call(callForHeader)
  const options = { method: 'GET', headers }
  try {
    const operats = yield call(request, requestUrl, options)
    yield put(operatsLoaded(operats))
  } catch (err) {
    yield put(loadOperatsError(err))
  }
}

export function * removeOperat () {
  const activeOperat = yield select(makeSelectActiveOperat())
  const requestUrl = `/Operat/id/${activeOperat.id}`
  const headers = yield call(callForHeader)
  const options = { method: 'DELETE', headers }
  try {
    yield call(request, requestUrl, options)
    yield put(loadOperats())
  } catch (err) {
    console.log(err)
  }
}

export function * saveOperat () {
  yield put(showLoadingScreen(modalTypeConstants.LOADING, { message: 'Trwa tworzenie operatu...' }))
  const createOperatRequestUrl = '/Operat'
  const selectedOperatType = yield select(makeSelectSelectedOperatType())
  const parameters = handleRawParameters(selectedOperatType.parameters)
  const user = yield select(makeSelectUser())
  const userId = user.user.id
  const headers = yield call(callForHeader, 'json')
  const timeElapsed = Date.now()
  const today = new Date(timeElapsed)
  const formElement = document.querySelector('#new-operat-form')
  const data = new FormData(formElement)
  const body = {
    name: data.get('operat-name') === '' ? `${selectedOperatType.name + ' : ' + today.toLocaleString()}` : data.get('operat-name'),
    orderId: globalConstants.EMPTY_GUID,
    operatorId: userId,
    operatTypeId: selectedOperatType.id
  }
  const creatOperatRequestOptions = { method: 'POST', headers, body: JSON.stringify(body) }
  try {
    const response = yield call(request, createOperatRequestUrl, creatOperatRequestOptions)
    const formData = new FormData()
    const blob = new Blob([JSON.stringify({ weights: parameters.weights })], { type: 'application/json' })
    formData.append('file', blob, 'weights.json')
    const saveWeightsRequestOptions = {
      method: 'POST',
      headers: yield call(callForHeader),
      body: formData
    }
    const saveWeightsRequestUrl = `/OperatFiles/upload/${response.id}/convert`
    yield call(handleRequestWithTimeout, saveWeightsRequestUrl, saveWeightsRequestOptions)
    yield put(loadOperats())
    yield put(hideLoadingScreen())
  } catch (err) {
    if (err.response.status === 402) {
      yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'unactiveSubscription', translate: true }))
    } else {
      yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'createOperatFailed', translate: true }))
    }
  }
}

export function * getOperatOrder () {
  const activeOperat = yield select(makeSelectActiveOperat())
  if (activeOperat.orderId === globalConstants.EMPTY_GUID) {
    yield put(operatOrderLoaded({}))
    return
  }
  const requestUrl = '/Order/id/' + activeOperat.orderId
  const headers = {
    Authorization: `Bearer ${localStorage.getItem('idToken')}`
  }
  const options = { method: 'GET', headers }
  try {
    const order = yield call(request, requestUrl, options)
    yield put(operatOrderLoaded(order))
  } catch (err) {
    yield put(loadOperatOrderError(err))
  }
}

export function * sendPdf (action) {
  let requestUrl = `/Operat/id/${action.id}/send?sendToPrincipal=${action.sendToPrincipal}`
  for (let i = 0; i < action.emails.length; i++) {
    requestUrl += `&emails=${action.emails[i]}`
  }
  const headers = {
    'Content-type': 'application/json',
    Authorization: `Bearer ${localStorage.getItem('idToken')}`
  }
  const options = { method: 'POST', headers }
  try {
    yield put(showLoadingScreen(modalTypeConstants.LOADING, { message: 'sendingOperat', translate: true }))
    yield call(handleRequestWithTimeout, requestUrl, options)
    yield put(hideLoadingScreen())
  } catch (err) {
    yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'sendingFailed', translate: true }))
    console.log(err)
  }
}

export function * uploadOperat (requestUrl, action) {
  const headers = {
    Authorization: `Bearer ${localStorage.getItem('idToken')}`
  }
  const data = new FormData()
  data.append('file', action.file)
  const options = { method: 'POST', headers, body: data }
  yield put(showLoadingScreen(modalTypeConstants.LOADING, { message: 'loadingOperat', translate: true }))
  yield call(handleRequestWithTimeout, requestUrl, options)
  yield put(loadOperats())
  yield put(hideLoadingScreen())
  yield call(addNotification, 'Informacja', 'Operat został pomyślnie wgrany na platformę Aoperat.', 'info')
}

export function * uploadOwnGeneratedOperat (action) {
  const requestUrl = `/OperatFiles/id/${action.id}/own`
  try {
    yield call(uploadOperat, requestUrl, action)
  } catch (err) {
    yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'uploadOperatFailed', translate: true }))
  }
}

export function * uploadSignedOperat (action) {
  const requestUrl = `/OperatFiles/id/${action.id}/upload-signed`
  try {
    yield call(uploadOperat, requestUrl, action)
  } catch (err) {
    yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'uploadSignedOperatFailed', translate: true }))
  }
}

export default function * globalData () {
  yield takeLatest(LOAD_OPERATS, getOperats)
  yield takeLatest(REMOVE_OPERAT, removeOperat)
  yield takeLatest(CREATE_OPERAT, saveOperat)
  yield takeLatest(LOAD_OPERAT_ORDER, getOperatOrder)
  yield takeLatest(SEND_PDF, sendPdf)
  yield takeLatest(UPLOAD_OWN_GENERATED, uploadOwnGeneratedOperat)
  yield takeLatest(UPLOAD_SIGNED_OPERAT, uploadSignedOperat)
}
