import { createSelector } from 'reselect'
import { initialState } from './reducer'

const selectOperatsPage = (state) => state.operatsPage || initialState

const makeSelectActiveOperat = () =>
  createSelector(selectOperatsPage, (state) => state.activeOperat)

const makeSelectOperats = () =>
  createSelector(selectOperatsPage, (state) => state.operats)

const makeSelectSelectedOperatType = () =>
  createSelector(selectOperatsPage, (state) => state.selectedOperatType)
const makeSelectPageNumber = () =>
  createSelector(selectOperatsPage, (state) => state.pageNumber)

const makeSelectOperatOrder = () =>
  createSelector(selectOperatsPage, (state) => state.operatOrder)

export {
  selectOperatsPage,
  makeSelectActiveOperat,
  makeSelectOperats,
  makeSelectSelectedOperatType,
  makeSelectPageNumber,
  makeSelectOperatOrder
}
