
import React, { Component } from 'react'
import { TwitterPicker } from 'react-color';

class FixedPlugin extends Component {
  constructor(props) {
    super(props)
    this.state = {
      classes: 'dropdown',
      isColorPickerOpen: false
    }
    this.handleClick = this.handleClick.bind(this)
  }

  handleClick() {
    if (this.state.classes === 'dropdown') {
      this.setState({ classes: 'dropdown show' })
    } else {
      this.setState({ classes: 'dropdown' })
    }
  }

  toggleColorPicker = () => {
    this.setState({
      isColorPickerOpen: !this.state.isColorPickerOpen
    })
  }
  render() {
    return (
      <div className='fixed-plugin' style={{ marginBottom: '78vh' }}>
        <div className={this.state.classes}>
          <div onClick={this.handleClick}>
            <i className='fa fa-cog fa-2x' />
          </div>
          <ul className='dropdown-menu show'>
          {
          this.state.isColorPickerOpen && <TwitterPicker
            triangle="hide"
            hidden={true}
            color={this.props.mainBgColor}
            onBlur={() => this.setState({isColorPickerOpen: false})}
            onChangeComplete={(color) => this.props.handleMainBgClick(color.hex)}
          />
        }
            <li className='header-title'>{this.props.labels && this.props.labels.mainBg && this.props.labels.mainBg !== '' ? this.props.labels.mainBg : "Główne tło"}</li>
            <li className='adjustments-line'>
              <div className='badge-colors text-center'>
                <span
                  className={
                    this.props.mainBgColor === '#f4f3ef'
                      ? 'badge filter badge-dark active'
                      : 'badge filter badge-dark'
                  }
                  style={{ backgroundColor: '#f4f3ef' }}
                  data-color='#f4f3ef'
                  onClick={() => {
                    this.props.handleMainBgClick('#f4f3ef')
                    this.setState({isColorPickerOpen: false})
                  }}
                />
                <span
                  className={
                    this.props.mainBgColor !== '#f4f3ef'
                      ? 'badge filter badge-light active'
                      : 'badge filter badge-light'
                  }
                  style={{ backgroundColor: this.props.mainBgColor !== '#f4f3ef' ? this.props.mainBgColor : 'white'}}
                  onClick={() => {
                    this.toggleColorPicker()
                  }}
                />
              </div>
            </li>
            <li className='header-title'>{this.props.labels && this.props.labels.menuBg && this.props.labels.menuBg !== '' ? this.props.labels.menuBg : "Kolor tła w menu"}</li>
            <li className='adjustments-line'>
              <div className='badge-colors text-center'>
                <span
                  className={
                    this.props.bgColor === 'black'
                      ? 'badge filter badge-dark active'
                      : 'badge filter badge-dark'
                  }
                  style={{ backgroundColor: '#2c2c2c' }}
                  data-color='black'
                  onClick={() => {
                    this.props.handleBgClick('black')
                  }}
                />
                <span
                  className={
                    this.props.bgColor === 'white'
                      ? 'badge filter badge-light active'
                      : 'badge filter badge-light'
                  }
                  data-color='white'
                  onClick={() => {
                    this.props.handleBgClick('white')
                  }}
                />
              </div>
            </li>
            <li className='header-title'>{this.props.labels && this.props.labels.activeColor && this.props.labels.activeColor !== '' ? this.props.labels.activeColor : "Kolor zaznaczania w menu"}</li>
            <li className='adjustments-line'>
              <div className='badge-colors text-center'>
                <span
                  className={
                    this.props.activeColor === 'primary'
                      ? 'badge filter badge-primary active'
                      : 'badge filter badge-primary'
                  }
                  data-color='primary'
                  onClick={() => {
                    this.props.handleActiveClick('primary')
                  }}
                />
                <span
                  className={
                    this.props.activeColor === 'info'
                      ? 'badge filter badge-info active'
                      : 'badge filter badge-info'
                  }
                  data-color='info'
                  onClick={() => {
                    this.props.handleActiveClick('info')
                  }}
                />
                <span
                  className={
                    this.props.activeColor === 'success'
                      ? 'badge filter badge-success active'
                      : 'badge filter badge-success'
                  }
                  data-color='success'
                  onClick={() => {
                    this.props.handleActiveClick('success')
                  }}
                />
                <span
                  className={
                    this.props.activeColor === 'warning'
                      ? 'badge filter badge-warning active'
                      : 'badge filter badge-warning'
                  }
                  data-color='warning'
                  onClick={() => {
                    this.props.handleActiveClick('warning')
                  }}
                />
                <span
                  className={
                    this.props.activeColor === 'danger'
                      ? 'badge filter badge-danger active'
                      : 'badge filter badge-danger'
                  }
                  data-color='danger'
                  onClick={() => {
                    this.props.handleActiveClick('danger')
                  }}
                />
              </div>
            </li>
          </ul>
        </div>
      </div>
    )
  }
}

export default FixedPlugin
