import React, { useState } from 'react'
import {
  Card,
  CardBody,
  Input,
  Button,
  Form
} from 'reactstrap'

export const OperatFilters = ({ handleSetOperatFilters }) => {
  const [createdDateTimeFrom, setCreatedDateTimeFrom] = useState('')
  const [createdDateTimeTo, setCreatedDateTimeTo] = useState('')
  const handleClear = () => {
    setCreatedDateTimeFrom('')
    setCreatedDateTimeTo('')
    handleSetOperatFilters({ createdDateTimeFrom: '', createdDateTimeTo: '' })
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    handleSetOperatFilters({ createdDateTimeFrom, createdDateTimeTo })
  }
  return (
    <Card>
      <CardBody>
        <Form onSubmit={handleSubmit}>
          <label for='filter-operat-created-date-time-from'>
            Data utworzenia od
          </label>
          <Input
            type='date' placeholder='Data utworzenia od' id='filter-operat-created-date-time-from' name='filter-operat-created-date-time-from' onChange={(e) => {
              setCreatedDateTimeFrom(e.target.value)
            }} value={createdDateTimeFrom}
          />
          <label for='filter-operat-created-date-time-to'>
            Data utworzenia do
          </label>
          <Input
            type='date' placeholder='Data utworzenia do' id='filter-operat-created-date-time-to' name='filter-operat-created-date-time-to' onChange={(e) => {
              setCreatedDateTimeTo(e.target.value)
            }} value={createdDateTimeTo}
          />
          <Button onClick={handleClear}>Wyczyść</Button>
          <Button type='submit' color='success'>Filtruj</Button>
        </Form>
      </CardBody>
    </Card>
  )
}

export default OperatFilters
