import { userRolesConstants } from '../../constants'
import { call, put, select, takeLatest } from 'redux-saga/effects'
import request from 'utils/request'
import { loadUsers, loadUsersError, usersLoaded } from './actions'
import { DELETE_USER, LOAD_USERS, PROMOTE_USER } from './constants'
import { makeSelectUsers, makeSelectUserType } from './selectors'

export function * getUsers () {
  const userType = yield select(makeSelectUserType())
  const users = yield select(makeSelectUsers())
  let requestUrl
  switch (userType) {
    case userRolesConstants.CLIENT:
      requestUrl = '/User/promote/clients'
      break
    case userRolesConstants.OPERATOR:
      requestUrl = '/User/promote/operators'
      break
    case userRolesConstants.ADMIN:
      requestUrl = '/User/promote/admins'
      break
    default:
      yield put(loadUsersError('Incorrect user type'))
      return
  }
  requestUrl += `?pageNumber=${users.pageNumber}&pageSize=5`
  requestUrl += `&name=${users.filters.name}&mail=${users.filters.mail}`
  if (users.sortFieldName) {
    requestUrl += `&fieldName=${users.sortFieldName}&direction=${users.sortDirection}`
  }
  const headers = {
    Authorization: `Bearer ${localStorage.getItem('idToken')}`
  }
  const options = { method: 'GET', headers }
  try {
    const users = yield call(request, requestUrl, options)
    yield put(usersLoaded(users))
  } catch (err) {
    yield put(loadUsersError(err))
  }
}

export function * promoteUser (action) {
  const userType = yield select(makeSelectUserType())
  let requestUrl
  switch (userType) {
    case userRolesConstants.CLIENT:
      requestUrl = `/Operator/promote/clientid/${action.userId}`
      break
    case userRolesConstants.OPERATOR:
      requestUrl = `/Admin/promote/operatorid/${action.userId}`
      break
    default:
      return
  }
  const headers = {
    Authorization: `Bearer ${localStorage.getItem('idToken')}`
  }
  const options = { method: 'PUT', headers }
  try {
    yield call(request, requestUrl, options)
    yield put(loadUsers())
  } catch (err) {}
}

export function * deleteUser (action) {
  const userType = yield select(makeSelectUserType())
  let requestUrl
  switch (userType) {
    case userRolesConstants.CLIENT:
      requestUrl = `/Client/id/${action.userId}`
      break
    case userRolesConstants.OPERATOR:
      requestUrl = `/Operator/id/${action.userId}`
      break
    default:
      return
  }
  const headers = {
    Authorization: `Bearer ${localStorage.getItem('idToken')}`
  }
  const options = { method: 'DELETE', headers }
  try {
    yield call(request, requestUrl, options)
    yield put(loadUsers())
  } catch (err) {}
}

export default function * watch () {
  yield takeLatest(LOAD_USERS, getUsers)
  yield takeLatest(PROMOTE_USER, promoteUser)
  yield takeLatest(DELETE_USER, deleteUser)
}
