import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import { Button, Form, FormGroup, Input, Label, UncontrolledTooltip, PopoverBody, Col, UncontrolledAlert, FormFeedback, Popover } from 'reactstrap'
import { MapWithMarker } from '../../views/Maps'
import InputMask from 'react-input-mask'
import LoadingIndicator from 'components/LoadingIndicator'
import { validateLandRegisterCode } from 'utils/validateLandRegisterCode'
export const NewOrderForm = (props) => {
  const { t } = useTranslation('common')

  const [order, setOrder] = useState({
    name: '',
    description: '',
    address: '',
    requiresZbpList: false,
    latitudeLongitude: { lat: 0, lng: 0 },
    locationPostCode: '',
    shouldBeValidated: false,
    orderSubject: '',
    valuationScope: '',
    landRegisterCode: '',
    propertyIdentification: '',
    isEkwInvalid: false,
    ekwErrorMessage: ''
  })
  const [mapPopoverOpen, setMapPopoverOpen] = useState(false)
  const toggleMapPopover = () => setMapPopoverOpen(!mapPopoverOpen)

  const handleOnChange = (e) => {
    const newOrder = {
      ...order,
      [e.target.name]: e.target.type === 'checkbox'
        ? e.target.checked
        : e.target.value
    }
    setOrder(newOrder)
  }

  const setLatitudeLongitude = (cords) => {
    const newOrder = {
      ...order,
      latitudeLongitude: cords
    }
    setOrder(newOrder)
  }

  const addOrder = (e) => {
    e.preventDefault()
    const ekwErrorMessage = order.landRegisterCode !== '' ? validateLandRegisterCode(order.landRegisterCode, props.ekwData) : ''
    const isEkwInvalid = ekwErrorMessage !== ''
    const newOrder = {
      ...order,
      isEkwInvalid,
      ekwErrorMessage
    }
    setOrder(newOrder)

    if (isEkwInvalid) {
      return
    }
    props.onSubmit(newOrder)
  }

  const subjectOptions = [
    { value: 'Lokal mieszkalny', label: 'Lokal mieszkalny' },
    { value: 'Działka rolna', label: 'Działka rolna' }
  ]

  const valuationScopeOptions = [
    { value: 'Na potrzeby sprzedaży', label: 'Na potrzeby sprzedaży' },
    { value: 'Prawo użytkowania wieczystego do gruntu', label: 'Prawo użytkowania wieczystego do gruntu' }
  ]

  function checkAllRequiresFields () {
    return order.name === '' || order.locationPostCode === '' || order.orderSubject === '' || order.valuationScope === ''
  }

  return (
    <div>
      <Form
        onSubmit={(e) => addOrder(e)}
        id='new-order-form'
      >
        <input type='hidden' value={`${order.latitudeLongitude.lat},${order.latitudeLongitude.lng}`} name='latlng-value' />
        <FormGroup>
          <Label for='order-name'>{t('market.table.headers.name')}</Label>
          <Input
            type='text'
            name='name'
            id='order-name'
            value={order.name}
            onChange={handleOnChange}
            required
          />
          <UncontrolledTooltip
            placement='top'
            target='order-name'
          >
            {t('market.newOrder.namePlaceholder')}
          </UncontrolledTooltip>
        </FormGroup>
        <FormGroup>
          <Label for='description'>{t('market.table.headers.description')}</Label>
          <Input
            type='textarea'
            name='description'
            id='description'
            value={order.description}
            onChange={handleOnChange}
          />
          <UncontrolledTooltip
            placement='top'
            target='description'
          >
            {t('market.newOrder.descriptionPlaceholder')}
          </UncontrolledTooltip>
        </FormGroup>
        <FormGroup>
          <Label for='postal-code'>{t('market.newOrder.postalCode')}</Label>
          <InputMask
            mask='99-999'
            value={order.locationPostCode}
            onChange={handleOnChange}
          >
            {(inputProps) => <Input
              {...inputProps}
              type='text'
              name='locationPostCode'
              id='new-order-postal-code'
              pattern='\d{2}-\d{3}$'
              required
                             />}
          </InputMask>
        </FormGroup>
        <FormGroup>
          <Label for='address'>{t('common.userInfo.address')}</Label>
          <Input
            type='text'
            name='address'
            id='address'
            value={order.address}
            onChange={handleOnChange}
          />
          <UncontrolledTooltip
            placement='top'
            target='address'
          >
            {t('market.newOrder.addressPlaceholder')}
          </UncontrolledTooltip>
        </FormGroup>
        <FormGroup>
          <Button
            onClick={() => setMapPopoverOpen(true)}
            className='long-btn' id='UncontrolledPopoverMAP'
            type='button' color='success'
          >Dodaj lokalizację na mapie
          </Button>
          <Popover
            isOpen={mapPopoverOpen}
            toggle={toggleMapPopover}
            placement='right'
            target='UncontrolledPopoverMAP'
            trigger='legacy'
          >
            <PopoverBody>
              <MapWithMarker
                close={() => setMapPopoverOpen(false)}
                locationInfo={{ address: order.address, locationPostCode: order.locationPostCode, latlng: order.latitudeLongitude }}
                setCoords={setLatitudeLongitude}
              />
            </PopoverBody>
          </Popover>
        </FormGroup>
        <FormGroup>
          <Label for='order-subject'>Co chcesz wycenić?</Label>
          <Input
            type='select'
            id='order-subject'
            name='orderSubject'
            style={{ lineHeight: '1.0' }}
            value={order.orderSubject}
            onChange={handleOnChange}
            autoComplete='off'
            required
          >
            <option value='' disabled hidden>Wybierz...</option>
            {subjectOptions.map((obj) => <option key={obj.label} value={obj.value}>{obj.label}</option>)}
          </Input>
        </FormGroup>
        <FormGroup>
          <Label for='valuation-scope'>Zakres wyceny</Label>
          <Input
            type='select'
            id='valuation-scope'
            name='valuationScope'
            style={{ lineHeight: '1.0' }}
            value={order.valuationScope}
            onChange={handleOnChange}
            autoComplete='off'
            required
          >
            <option value='' disabled hidden>Wybierz...</option>
            {valuationScopeOptions.map((obj) => <option key={obj.label} value={obj.value}>{obj.label}</option>)}
          </Input>
        </FormGroup>
        <FormGroup>
          <Label for='order-land-register-code'>Nr księgi wieczystej (pole bedzie tylko widoczne dla rzeczoznawcy wykonującego) - pole opcjonalne</Label>
          <InputMask
            mask='aa9a/99999999/9'
            value={order.landRegisterCode}
            name='landRegisterCode'
            onChange={handleOnChange}
            invalid={order.isEkwInvalid}

          >
            {(inputProps) => <Input
              {...inputProps}
              type='text'
              style={{ textTransform: 'uppercase' }}
              id='order-land-register-code'
                             />}
          </InputMask>
          <FormFeedback>
            <div
              dangerouslySetInnerHTML={{ __html: order.ekwErrorMessage }}
            />
          </FormFeedback>
        </FormGroup>
        <FormGroup>
          <Label for='property-identification'>Identyfikacja nieruchomości np. numer nieruchomości. Pole to będzie widoczne tylko dla rzeczoznawcy wykonującego - pole opcjonalne</Label>
          <Input
            id='property-identification'
            name='propertyIdentification'
            type='text'
            value={order.propertyIdentification}
            onChange={handleOnChange}
          />
        </FormGroup>
        <FormGroup>
          <Col sm={{ size: 10 }}>
            <FormGroup>
              <Label for='requires-zbp-list' sm={10}>
                <Input
                  type='checkbox'
                  name='requiresZbpList'
                  id='requires-zbp-list'
                  checked={order.requiresZbpList}
                  onChange={handleOnChange}
                />
                {t('market.table.headers.requiresZbpList')}
              </Label>
            </FormGroup>
          </Col>
        </FormGroup>
        <FormGroup>
          <Col sm={{ size: 10 }}>
            <FormGroup>
              <Label for='aoperatValidation' sm={10}>
                <Input
                  type='checkbox'
                  name='shouldBeValidated'
                  id='aoperat-validation'
                  checked={order.shouldBeValidated}
                  onChange={handleOnChange}
                />
                {t('market.table.headers.aoperatValidation')}
              </Label>
            </FormGroup>
          </Col>
        </FormGroup>
        <Button
          type='submit'
          color='success'
          disabled={checkAllRequiresFields()}
        >
          {t('common.add')}
        </Button>
      </Form>
      {props.saveOrder.error &&
        <UncontrolledAlert
          color='danger'
        >
          <div
            dangerouslySetInnerHTML={{ __html: props.saveOrder.error.message }}
          />
        </UncontrolledAlert>}
      {props.saveOrder.loading && <LoadingIndicator />}
    </div>
  )
}

export default NewOrderForm
