import {
  LOAD_USERS, LOAD_USERS_ERROR, USERS_LOADED,
  SET_USER_TYPE, PROMOTE_USER, SET_USER_SORT_PARAMETERS,
  SET_USERS_PAGE_NUMBER, SET_USER_FILTERS, DELETE_USER
} from './constants'

export function setUserType (payload) {
  return {
    type: SET_USER_TYPE,
    payload
  }
}
export function loadUsers () {
  return {
    type: LOAD_USERS
  }
}

export function usersLoaded (payload) {
  return {
    type: USERS_LOADED,
    payload
  }
}

export function loadUsersError (payload) {
  return {
    type: LOAD_USERS_ERROR,
    payload
  }
}

export function promoteUser (userId) {
  return {
    type: PROMOTE_USER,
    userId
  }
}

export function deleteUser (userId) {
  return {
    type: DELETE_USER,
    userId
  }
}

export function setUsersSortParameters (payload) {
  return {
    type: SET_USER_SORT_PARAMETERS,
    payload
  }
}

export function setUsersPageNumber (payload) {
  return {
    type: SET_USERS_PAGE_NUMBER,
    payload
  }
}

export function setUserFilters (payload) {
  return {
    type: SET_USER_FILTERS,
    payload
  }
}
