import { MODAL_HIDE_LOADING_SCREEN, MODAL_SHOW_LOADING_SCREEN } from './constants'

export function showLoadingScreen (type, data) {
  return {
    type: MODAL_SHOW_LOADING_SCREEN,
    payload: { showModal: true, type, data }
  }
}

export function hideLoadingScreen () {
  return {
    type: MODAL_HIDE_LOADING_SCREEN
  }
}
