import React, { useEffect, useState } from 'react'
import BootstrapTable from 'react-bootstrap-table-next'
import {
  Button,
  Collapse,
  UncontrolledTooltip
} from 'reactstrap'
import { useHistory } from 'react-router'
import { useTranslation } from 'react-i18next'
import { useMsal } from '@azure/msal-react'
import { userRolesConstants } from '../../constants'
import ReactStars from 'react-rating-stars-component'
import LoadingIndicator from 'components/LoadingIndicator'
import { useSelector } from 'react-redux'
import OrderPropositionFilters from 'components/Filters/OrderProposition'
import ShowIdCheckbox from 'components/ShowIdCheckbox'
export const isButtonDisabled = (row, propositions, accounts, clientId, isOperator) => {
  const isAnyReserved = propositions.some(x => x.reservationStatus.toLowerCase() === 'reserved')
  const isReserved = row.reservationStatus.toLowerCase() === 'reserved'
  const oid = accounts[0].idTokenClaims.oid
  const isMineOrder = clientId === accounts[0].idTokenClaims.oid
  if (isOperator && !isReserved && row.operatorId === oid) {
    return true
  }
  if (isMineOrder) {
    return isAnyReserved ? !isReserved : isReserved
  }
  return isOperator
    ? !(isReserved && row.operatorId === oid)
    : ((isAnyReserved && !isReserved) || row.operatorId !== oid)
}
function PropositionList (props) {
  const [isIdHidden, setIsIdHidden] = useState(true)
  const [isFiltersVisible, setIsFiltersVisible] = useState(false)
  const { accounts } = useMsal()
  const user = useSelector(state => state.global.user)
  const isAdmin = user.userType === userRolesConstants.ADMIN
  const isOperator = user.userType === userRolesConstants.OPERATOR
  const { t } = useTranslation('common')
  const history = useHistory()
  const sortable = {
    onSort: (field, order) => {
      props.handleSetPropositionsSortParameters(field)
    },
    sortFunc: (a, b, order, dataField, rowA, rowB) => {

    },
    sort: true
  }
  useEffect(() => {
    props.handleLoadPropositions()
  // eslint-disable-next-line
  }, [props.propositions.sortFieldName, props.propositions.sortDirection, props.propositions.filters])

  const columns = [
    {
      dataField: 'id',
      text: 'ID',
      hidden: isIdHidden
    },
    {
      dataField: 'description',
      text: t('market.table.headers.description'),
      editable: false,
      ...sortable,
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'price',
      text: t('market.table.headers.price'),
      editable: false,
      ...sortable,
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'reservationStatus',
      text: t('market.table.headers.status'),
      editable: false,
      align: 'center',
      headerAlign: 'center',
      formatter: (cellContent, row) => <div>{row.reservationStatus.toLowerCase() === 'unknown' ? '' : row.reservationStatus}</div>
    },
    {
      dataField: 'upTillDate',
      text: t('market.table.headers.upTillDate'),
      align: 'center',
      headerAlign: 'center',
      ...sortable,
      formatter: (cellContent, row) => <div>{cellContent && cellContent.split('T')[0]}</div>,
      headerStyle: (colum, colIndex) => {
        return { width: '16%', textAlign: 'center' }
      }
    },
    {
      text: 'rzeczoznawca',
      align: 'center',
      headerAlign: 'center',
      editable: true,
      formatter: (cellContent, row) => <Button
        color='primary' onClick={() => {
          history.push({ pathname: '/users-profile', search: '?operatorId=' + row.operatorId })
        }}
                                       >Profil rzeczoznawcy
      </Button>
    },
    {
      text: 'ocena rzeczoznawcy',
      align: 'center',
      headerAlign: 'center',
      editable: true,
      formatter: (cellContent, row) => row.avgOperatorRating
        ? <div className='d-flex justify-content-center' id={'avg-rating-' + row.id}>
          <ReactStars
            count={5}
            edit={false}
            isHalf
            value={Math.ceil(row.avgOperatorRating)}
            size={24}
            activeColor='#ffd700'
          />
          <UncontrolledTooltip
            flip
            target={'avg-rating-' + row.id}
          >
            {row.avgOperatorRating.toFixed(3)}
          </UncontrolledTooltip>
        </div>
        : 'Brak danych'
    },
    {
      text: t('market.table.headers.accept-resign'),
      align: 'center',
      headerAlign: 'center',
      editable: true,
      formatter: (cellContent, row) => {
        return (
          <Button
            disabled={isButtonDisabled(row, props.propositions.propositions, accounts, props.activeOrder.clientId, isOperator)}
            color={row.reservationStatus.toLowerCase() === 'reserved' ? 'danger' : 'success'}
            onClick={() => {
              props.handleSetPropositionId(row.id)
              row.reservationStatus.toLowerCase() === 'unknown' || row.reservationStatus.toLowerCase() === 'unreserved'
                ? props.handleReserveOrderAndProposition()
                : props.handleUnreserveProposition()
            }}
          >
            {row.reservationStatus.toLowerCase() === 'reserved' ? t('market.table.buttons.resign') : t('market.table.buttons.accept')}
          </Button>
        )
      }
    }
  ]

  return (
    <div className='content'>
      <Button onClick={() => setIsFiltersVisible(v => !v)} color='success'>Filtry</Button>
      <Collapse isOpen={isFiltersVisible}>
        <OrderPropositionFilters handleSetPropositionFilters={props.handleSetPropositionFilters} />
      </Collapse>
      {isAdmin && <ShowIdCheckbox setIsIdHidden={setIsIdHidden} label={t('market.showPropositionId')} isIdHidden={isIdHidden} />}
      {
      props.propositions.loading ? <LoadingIndicator />
        : <BootstrapTable
            bootstrap4
            keyField='id'
            data={props.propositions.propositions} // .sort((a, b) => parseInt(a.description.split(' ')[0]) < parseInt(b.description.split(' ')[0]) ? 1 : -1)
            columns={columns}
            striped
            hover
            bordered={false}
          />
      }
    </div>
  )
}

export default PropositionList
