import { callForHeader } from 'utils/HeadersCall'
const apiUrl = process.env.REACT_APP_API_URL

export const certificateService = {
  generateCertificate
}

async function generateCertificate (body) {
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader('json'),
    body: JSON.stringify(body)
  }

  return await window.fetch(`${apiUrl}/Certificate/generate`, requestOptions).then(handleResponse)
}

function handleResponse (response) {
  return response.text().then(text => {
    const data = text && JSON.parse(text)
    if (!response.ok) {
      return Promise.reject(data || response.statusText)
    }

    return data
  })
}
