import { useMsal } from '@azure/msal-react'
import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import {
  Button, Card, CardBody, Collapse
} from 'reactstrap'
import { userRolesConstants } from '../../constants'
import UserInfo from '../../views/Common/UserInfo'
import NewOffer from 'components/NewOfferForm'
import PropositionList from './PropositionList'
import { useSelector } from 'react-redux'
function AnnouncedOrder (props) {
  const user = useSelector(state => state.global.user)
  const [sendedOffers, setSendedOffers] = useState(false)
  const [userInfo, setUserInfo] = useState(false)
  const [offer, setOffer] = useState(false)
  const { t } = useTranslation('common')
  const { accounts } = useMsal()

  return (
    <div>
      {(userRolesConstants.ADMIN === user.userType ||
        (user.userType === userRolesConstants.OPERATOR && accounts[0].idTokenClaims.oid !== props.activeOrder.clientId)) && (
          <div>
            <Button
              color='primary'
              className='long-btn'
              onClick={() => {
                setOffer(!offer)
              }} style={{ marginBottom: '1rem' }}
            >
              {t('market.table.buttons.submittOffer')}
            </Button>{' '}
            <Collapse isOpen={offer}>
              {offer && <NewOffer onSubmit={props.handleSaveProposition} hide={() => setOffer(false)} />}
            </Collapse>
          </div>
      )}
      <div>
        <Button
          className='long-btn proposition-list-toggler'
          color='secondary'
          style={{ marginBottom: '1rem' }}
          onClick={() => {
            setSendedOffers(!sendedOffers)
          }}
        >
          {t('market.table.buttons.submittedOffers')}
        </Button>
        <Collapse isOpen={sendedOffers}>
          <Card>
            <CardBody>
              {sendedOffers && <PropositionList
                handleReserveOrderAndProposition={props.handleReserveOrderAndProposition}
                handleUnreserveProposition={props.handleUnreserveProposition}
                handleSetPropositionId={props.handleSetPropositionId}
                propositions={props.propositions}
                handleLoadPropositions={props.handleLoadPropositions}
                activeOrder={props.activeOrder}
                handleSetPropositionsSortParameters={props.handleSetPropositionsSortParameters}
                handleSetPropositionFilters={props.handleSetPropositionFilters}
                               />}
            </CardBody>
          </Card>
        </Collapse>
      </div>
      {
        accounts[0].idTokenClaims.oid !== props.activeOrder.clientId &&
          <div>
            <Button
              color='info'
              className='long-btn'
              onClick={() => {
                setUserInfo(!userInfo)
              }}
              style={{ marginBottom: '1rem' }}
            >
              {t('market.table.buttons.payerInformation')}
            </Button>

            <Collapse isOpen={userInfo}>
              <Card>
                <CardBody>
                  {userInfo && <UserInfo id={props.activeOrder.clientId} />}
                </CardBody>
              </Card>
            </Collapse>
          </div>
      }
    </div>
  )
}

export default AnnouncedOrder
