import React, { useState } from 'react'
import { Row, Col, Form, Input, Label, Button } from 'reactstrap'

const margin5px = { margin: '5px' }
const saveButtonStyle = { margin: '5px', float: 'right' }

function SubscriptionRow ({ subscription, handleUpdateSubscription }) {

  const [subscriptionState, setSubscriptionState] = useState({
    isActive: subscription.isActive,
    canRenew: subscription.canRenew,
    nrOfOperats: subscription.nrOfOperats,
    maxNrOfOperats: subscription.maxNrOfOperats,
    nrOfGenerations: subscription.nrOfGenerations,
    maxNrOfGenerations: subscription.maxNrOfGenerations
  })

  const handleOnChange = (e) => {
    const newState = {
      ...subscriptionState,
      [e.target.name]: e.target.type === 'checkbox' 
        ? e.target.checked 
        : e.target.value
    }
    setSubscriptionState(newState)
  }

  function submitUpdateSubscriptionForm () {
    const editedSubscription = {
      id: subscription.id,
      createdDateTime: subscription.createdDateTime,
      ...subscriptionState
    }
    handleUpdateSubscription(editedSubscription)
  }

  function updateSubscriptionForm () {
    return (
      <Form
        id='update-subscription-form' onSubmit={e => {
          e.preventDefault()
          submitUpdateSubscriptionForm()
        }}
      >
        <Row>
          <Col>
            <Label for='subscription-is-active'>
              <b>Aktywna:</b>
            </Label>
            <Input style={margin5px} id='subscription-is-active' name='isActive' type='checkbox' checked={subscriptionState.isActive} onChange={handleOnChange} />
          </Col>
          <Col>
            <Label for='subscription-can-renew'>
              <b>Odnowi się w następnym miesiącu:</b>
            </Label>
            <Input style={margin5px} id='subscription-can-renew' name='canRenew' type='checkbox' checked={subscriptionState.canRenew} onChange={handleOnChange} />
          </Col>
        </Row>
        <Row>
          <Col>
            <Label for='subscription-nr-of-operats'>
              <b>Liczba stworzonych operatów:</b>
            </Label>
            <Input style={margin5px} id='subscription-nr-of-operats' name='nrOfOperats' type='number' step='1' min='0' value={subscriptionState.nrOfOperats} onChange={handleOnChange} />
          </Col>
          <Col>
            <Label for='subscription-max-nr-of-operats'>
              <b>Limit tworzenia operatów:</b>
            </Label>
            <Input style={margin5px} id='subscription-max-nr-of-operats' name='maxNrOfOperats' type='number' step='1' min='1' value={subscriptionState.maxNrOfOperats} onChange={handleOnChange} />
          </Col>
        </Row>
        <Row>
          <Col>
            <Label for='subscription-nr-of-generations'>
              <b>Liczba wygenerowanych pdf:</b>
            </Label>
            <Input style={margin5px} id='subscription-nr-of-generations' name='nrOfGenerations' type='number' step='1' min='0' value={subscriptionState.nrOfGenerations} onChange={handleOnChange} />
          </Col>
          <Col>
            <Label for='subscription-max-nr-of-generations'>
              <b>Limit generowania pdf:</b>
            </Label>
            <Input style={margin5px} id='subscription-max-nr-of-generations' name='maxNrOfGenerations' type='number' step='1' min='1' value={subscriptionState.maxNrOfGenerations} onChange={handleOnChange} />
          </Col>
        </Row>
        <Row>
          <Col>
            <Button color='success' type='submit' style={saveButtonStyle}>Zapisz</Button>
          </Col>
        </Row>
      </Form>
    )
  }

  return (
    <div>
      {updateSubscriptionForm()}
    </div>
  )
}

export default SubscriptionRow
