import React, { useState, useRef } from 'react'
import NotificationAlert from 'react-notification-alert'
import { useHistory } from 'react-router-dom'
import { operatService } from '../../services'
import { modalTypeConstants, loadingMessagesConstants } from '../../constants'

import UserInfo from 'views/Common/UserInfo'

import {
  Button,
  Card,
  CardBody,
  Collapse,
  Input
} from 'reactstrap'
import { useDispatch } from 'react-redux'
import { showLoadingScreen, hideLoadingScreen } from 'views/Modal/actions'

function PaidOrder (props) {
  const dispatch = useDispatch()
  const [operatType, setOperatType] = useState('')
  const [userInfo, setUserInfo] = useState(false)
  const history = useHistory()
  const notifyRegister = useRef(null)

  const createOperat = (row) => {
    const timeElapsed = Date.now()
    const today = new Date(timeElapsed)
    const body = {
      name: `${row.name + ', ' + operatType.name + ' : ' + today.toLocaleString()}`,
      orderId: row.id,
      operatorId: row.operators[0],
      operatTypeId: operatType.id
    }

    dispatch(showLoadingScreen(modalTypeConstants.LOADING, { body, message: loadingMessagesConstants.SAVE_OPERAT }))
    operatService.createOperat(body).then(() => {
      dispatch(hideLoadingScreen())
      setOperatType('')
      history.push('/history')
    })
  }

  const dropDown = () => {
    return (
      <div>
        <Input
          type='select' className='dark-select' id='operat-type-select' value={operatType.id ? operatType.id : ''} onChange={(e) => {
            if (e.target.value === 'CLEAR') {
              setOperatType('')
            } else {
              setOperatType(props.operatTypes.operatTypes.find(x => x.id === e.target.value))
            }
          }} style={{ width: '272px' }} placeholder='wybierz typ operatu'
        >
          <option value='' disabled>Wybierz rodzaj operatu</option>
          {props.operatTypes.operatTypes.map((element) => {
            return (
              <option key={element.id} value={element.id}>{element.name}</option>
            )
          })}
          <option value='CLEAR'>---Kasuj wybór---</option>
        </Input>
      </div>
    )
  }

  return (
    <div>
      <NotificationAlert ref={notifyRegister} />
      <div>
        {dropDown()}
        <div className='d-flex justify-content-between'>
          <Button
            style={{ width: '272px', marginBottom: '1rem' }}
            color='primary'
            onClick={() => createOperat(props.activeOrder)}
            disabled={operatType === ''}
          >
            Stwórz operat
          </Button>

        </div>
        <Button
          style={{ width: '272px', marginBottom: '1rem' }}
          color='info'
          onClick={() => setUserInfo(!userInfo)}
        >
          informacje o zleceniodawcy
        </Button>
        <Collapse isOpen={userInfo} style={{ marginTop: '1rem' }}>
          <Card>
            <CardBody>
              {userInfo && <UserInfo id={props.activeOrder.clientId} />}
            </CardBody>
          </Card>
        </Collapse>
      </div>
    </div>
  )
}

export default PaidOrder
