/* eslint-disable default-param-last */
import produce from 'immer'
import { capitalizeFirstLetter } from 'utils/capitalizeFirstLetter'
import {
  LOAD_OPERATS, LOAD_OPERATS_ERROR, LOAD_OPERAT_ORDER,
  LOAD_OPERAT_ORDER_ERROR, OPERATS_LOADED, OPERAT_ORDER_LOADED,
  SELECT_OPERAT_TYPE, SET_ACTIVE_OPERAT, SET_PAGE_NUMBER,
  SET_OPERATS_FILTERS, SET_OPERATS_SORT_PARAMETERS
} from './constants'

export const initialState = {
  activeOperat: {

  },
  operatOrder: {
    loading: false,
    error: false,
    operatOrder: {}
  },
  selectedOperatType: {

  },
  operats: {
    loading: false,
    error: false,
    sortFieldName: 'CreatedDateTime',
    sortDirection: 'desc',
    filters: {
      createdDateTimeFrom: '',
      createdDateTimeTo: ''
    },
    operats: []
  },
  pageNumber: 1

}

/* eslint-disable default-case, no-param-reassign */
const operatsPageReducer = (state = initialState, action) =>
  produce(state, (draft) => {
    switch (action.type) {
      case LOAD_OPERATS:
        draft.operats.operats = []
        draft.operats.loading = true
        draft.operats.error = false
        break
      case LOAD_OPERATS_ERROR:
        draft.operats.loading = false
        draft.operats.error = action.payload
        break
      case OPERATS_LOADED:
        draft.operats.operats = action.payload
        draft.operats.loading = false
        draft.operats.error = false
        break
      case SET_ACTIVE_OPERAT:
        draft.activeOperat = action.payload
        break
      case SELECT_OPERAT_TYPE:
        draft.selectedOperatType = action.payload
        break
      case SET_PAGE_NUMBER:
        draft.pageNumber = action.payload
        break
      case LOAD_OPERAT_ORDER:
        draft.operatOrder.operatOrder = {}
        draft.operatOrder.loading = true
        draft.operatOrder.error = false
        break
      case OPERAT_ORDER_LOADED:
        draft.operatOrder.operatOrder = action.payload
        draft.operatOrder.error = false
        draft.operatOrder.loading = false
        break
      case LOAD_OPERAT_ORDER_ERROR:
        draft.operatOrder.loading = false
        draft.operatOrder.error = action.payload
        break
      case SET_OPERATS_FILTERS:
        draft.operats.filters = action.payload
        break
      case SET_OPERATS_SORT_PARAMETERS:
        draft.operats.sortFieldName = capitalizeFirstLetter(action.payload)
        draft.operats.sortDirection = draft.operats.sortDirection === 'asc' ? 'desc' : 'asc'
        break
    }
  })

export default operatsPageReducer
