function OperatList () {
  const [operats, setOperats] = useState([])
  const dispatch = useDispatch()
  const data = useSelector(state => state.modal.data)
  const currentOperat = data.operat
  const operatTypeId = useSelector(state => state.operat.operatTypeId)

  // eslint-disable-next-line
  useEffect(async () => {
    const requestUrl = `/Operat/my?pageNumber=1&pageSize=9999999&operatTypeId=${operatTypeId}`
    const requestOptions = {
      method: 'GET',
      headers: await callForHeader()
    }
    request(requestUrl, requestOptions).then((res) => setOperats(res)).catch((err) => { console.log(err) })
  // eslint-disable-next-line
  }, [])

  const listGroup = (
    <ListGroup style={{ overflow: 'scroll', maxHeight: '67vh' }}>
      {operats.map((operat) => (
        <ListGroupItem
          key={operat.id}
          action
          onClick={async () => {
            const result = await confirm({
              title: 'Ostrzeżenie',
              message:
                'Czy na pewno chcesz skopiować dane z tego operatu? Dane zostaną nadpisane',
              confirmText: 'Kopiuj',
              cancelText: 'Wróć',
              confirmColor: 'success',
              cancelColor: 'link text-primary'
            })
            if (result) {
              const handleResponse = () => {
                dispatch(hideLoadingScreen())
                dispatch(getFormatedFiles())
              }
              dispatch(
                showLoadingScreen(modalTypeConstants.LOADING, {
                  message: 'Trwa kopiowanie danych z operatu'
                })
              )
              switch (data.dataType) {
                case 'params':
                  operatFilesService
                    .copyOperatParams(operat.id, currentOperat.id)
                    .then(() => {
                      handleResponse()
                    })
                  break
                case 'csv':
                  operatFilesService
                    .copyOperatCsv(operat.id, currentOperat.id)
                    .then(() => {
                      handleResponse()
                    })
                  break
                case 'photos':
                  operatFilesService
                    .copyOperatPhotos(operat.id, currentOperat.id)
                    .then(() => {
                      handleResponse()
                    })
                  break
                case 'weights':
                  operatFilesService
                    .copyOperatWeights(operat.id, currentOperat.id)
                    .then(() => {
                      handleResponse()
                    })
                  break
                default:
              }
            }
          }}
          tag='a'
        >
          <p>Tytuł: {operat.name}</p>
          <p>Data stworzenia: {operat.createdDateTime.split('T')[0]}</p>
        </ListGroupItem>
      ))}
    </ListGroup>
  )
  return (
    <>
      <ModalHeader>Wybierz operat, z którego chcesz skopiować dane</ModalHeader>
      <ModalBody>
        {operats.length > 0
          ? listGroup
          : 'Brak operatów, z których można by skopiować dane'}
      </ModalBody>
      {createModalFooter(dispatch, true)}
    </>
  )
}

