function AttachmentsOptions () {
  const dispatch = useDispatch()
  const [showSentAttachments, setShowSentAttachments] = useState(false)
  const data = useSelector(state => state.modal.data)

  async function handleJoinFiles () {
    const url = `/Operat/id/${data.id}/join-attachments`
    const requestOptions = {
      method: 'POST',
      headers: await callForHeader()
    }

    dispatch(
      showLoadingScreen(modalTypeConstants.LOADING, {
        message: 'Trwa łączenie plików'
      })
    )

    request(url, requestOptions).then(_ => {
      dispatch(
        showLoadingScreen(modalTypeConstants.CONFIRM_SUCCESS, {
          message: 'Pliki zostały połączone z operatem.'
        })
      )
    }).catch((err) => {
      dispatch(
        showLoadingScreen(modalTypeConstants.CONFIRM_ERROR, {
          message: err.message
        })
      )
    })
  }

  return (
    <>
      <ModalHeader>Zarządzaj załącznikami</ModalHeader>
      <ModalBody>
        <Row>
          <Button
            color='info'
            onClick={() => { dispatch(showLoadingScreen(modalTypeConstants.ADD_ATTACHMENTS, { id: data.id })) }}
            style={{ width: '100%' }}
          >
            Prześlij załączniki
          </Button>
        </Row>
        <Row>
          <Button color='info' onClick={() => setShowSentAttachments(!showSentAttachments)}>
            Pokaż przesłane załączniki
          </Button>
        </Row>
        <Row>
          <Collapse isOpen={showSentAttachments}>
            <Card>
              <CardBody>
                {showSentAttachments && <SentAttachmentsTable id={data.id} />}
              </CardBody>
            </Card>
          </Collapse>
        </Row>
        <Row>
          <Button color='info' onClick={handleJoinFiles}>
            Połącz pliki
          </Button>
        </Row>
      </ModalBody>
      <ModalFooter>
        <Button
          color='danger'
          onClick={() => {
            dispatch(hideLoadingScreen())
          }}
        >
          Anuluj
        </Button>
      </ModalFooter>
    </>
  )
}

function AddAttachments () {
  const dispatch = useDispatch()
  const [isLoading, setIsLoading] = useState(true)
  const [files, setFiles] = useState([])
  const [includeOc, setIncludeOc] = useState(false)
  const [includeDiploma, setIncludeDiploma] = useState(false)
  const [sentFiles, setSentFiles] = useState([])
  const maxFileCount = 20
  const data = useSelector(state => state.modal.data)

  useEffect(() => {
    async function loadFiles () {
      setIsLoading(true)
      const url = `/OperatFiles/id/${data.id}`
      const requestOptions = {
        method: 'GET',
        headers: await callForHeader('json')
      }

      request(url, requestOptions).then(response => {
        const filteredFiles = response.filter(x => x.item1.includes('attachment')).map(x => {
          const item1 = x.item1.replace('attachment/', '')
          return ({ item1, item2: x.item2 })
        })
        setSentFiles(filteredFiles)
      }).catch(_ => {
      }).finally(() => {
        setIsLoading(false)
      })
    }

    loadFiles()
  }, [data.id])

  const toBigWarning = async () => {
    await confirm({
      title: <>Ostrzeżenie</>,
      message: 'Minimum jeden z plików jest za duży.',
      buttonsComponent: ({ onClose }) => (
        <div>
          <Button color='info' onClick={() => onClose(true)}>Ok</Button>
        </div>
      )
    })
  }

  const fileLimitExceededWarning = async () => {
    await confirm({
      title: <>Ostrzeżenie</>,
      message: 'Limit plików przekroczony.',
      buttonsComponent: ({ onClose }) => (
        <div>
          <Button color='info' onClick={() => onClose(true)}>Ok</Button>
        </div>
      )
    })
  }

  const showUploadedFiles = () => (
    <Table>
      <thead>
        <tr>
          <th>#</th>
          <th>Nazwa pliku</th>
          <th />
        </tr>
      </thead>
      {generateRows()}
    </Table>
  )

  const generateRows = () => {
    return files.map((item, id) =>
      (
        <tbody key={id}>
          <tr>
            <th scope='row'>{id + 1}</th>
            <td>{item.file.name}</td>
            <td>
              <Button
                color='danger'
                onClick={() => {
                  const filteredFiles = files.filter(x => x.id !== item.id)
                  setFiles(filteredFiles)
                }}
              >
                Usuń
              </Button>
            </td>
          </tr>
        </tbody>
      ))
  }

  async function handleSendAttachments () {
    dispatch(
      showLoadingScreen(modalTypeConstants.LOADING, {
        message: 'Trwa wysyłanie załączników'
      })
    )

    const filesWithoutIds = files.map(x => x.file)
    const formData = new FormData()
    formData.append('includeoc', includeOc)
    formData.append('includediploma', includeDiploma)
    filesWithoutIds.forEach(x => formData.append('additionalattachments', x))

    const url = `/OperatFiles/id/${data.id}/upload-attachments`
    const requestOptions = {
      method: 'POST',
      headers: await callForHeader(),
      body: formData
    }

    request(url, requestOptions).then(_ => {
      dispatch(
        showLoadingScreen(modalTypeConstants.CONFIRM_SUCCESS, {
          message: 'Załączniki zostały przesłane.'
        })
      )
    }).catch((err) => {
      dispatch(
        showLoadingScreen(modalTypeConstants.CONFIRM_ERROR, {
          message: err.message
        })
      )
    })
  }

  const onDrop = useCallback((acceptedFiles, fileRejections, sentFiles, files, includeOc, includeDiploma) => {
    if (fileRejections.length > 0) {
      toBigWarning()
      return
    }

    if (acceptedFiles.length > 0) {
      let uniqueFilesCount = sentFiles.length
      const newFilesCount = files.filter(x => !sentFiles.some(y => y.item1 === x.file.name)).length
      uniqueFilesCount = uniqueFilesCount + newFilesCount

      if (!sentFiles.some(x => x.item1 === 'oc.pdf') && includeOc) {
        uniqueFilesCount = uniqueFilesCount + 1
      }

      if (!sentFiles.some(x => x.item1 === 'diploma.pdf') && includeDiploma) {
        uniqueFilesCount = uniqueFilesCount + 1
      }

      const filesToAdd = acceptedFiles.filter(x => !sentFiles.some(y => y.item1 === x.name))
      uniqueFilesCount = uniqueFilesCount + filesToAdd.length

      if (uniqueFilesCount > maxFileCount) {
        fileLimitExceededWarning()
        return
      }

      const filesToAddWithIds = filesToAdd.map(x => { return { id: uuidv4(), file: x } })
      setFiles(prevState => [...prevState, ...filesToAddWithIds])
    }
  }, [])

  const baseStyle = {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: '20px',
    borderWidth: 2,
    borderRadius: 20,
    borderColor: '#c2b7b7',
    borderStyle: 'dashed',
    color: '#0e0c0c',
    outline: 'none'
  }

  const style = {
    ...baseStyle
  }

  const { getRootProps, getInputProps } = useDropzone({
    onDrop: (acceptedFiles, fileRejections, _) => onDrop(acceptedFiles, fileRejections, sentFiles, files, includeOc, includeDiploma),
    multiple: true,
    accept: '.pdf',
    minSize: 0,
    maxSize: 5242880
  })

  const modalBody = () => (
    <ModalBody>
      <h5>Standardowe załączniki</h5>
      <Row>
        <Col>
          <Label for='input-oc'>Załącz ubezpieczenie OC: </Label>
          <Input
            id='input-oc'
            type='checkbox'
            style={{ marginLeft: '5px' }}
            checked={includeOc}
            onChange={_ => {
              const countBefore = sentFiles.length + files.filter(x => !sentFiles.some(y => y.item1 === x.file.name)).length
              const countAfter = !includeOc && !sentFiles.some(x => x.item1 === 'oc.pdf') ? countBefore + 1 : countBefore
              if (includeOc === false && countAfter > maxFileCount) {
                return
              }
              setIncludeOc(!includeOc)
            }}
          />
        </Col>
        <Col>
          <Label for='input-diploma'>Załącz dyplom:</Label>
          <Input
            id='input-diploma'
            type='checkbox'
            style={{ marginLeft: '5px' }}
            checked={includeDiploma}
            onChange={_ => {
              const countBefore = sentFiles.length + files.filter(x => !sentFiles.some(y => y.item1 === x.file.name)).length
              const countAfter = !includeDiploma && !sentFiles.some(x => x.item1 === 'diploma.pdf') ? countBefore + 1 : countBefore
              if (includeDiploma === false && countAfter > maxFileCount) {
                return
              }
              setIncludeDiploma(!includeDiploma)
            }}
          />
        </Col>
      </Row>
      <Col>
        <h5 style={{ marginTop: '15px' }}>Dodatkowe załączniki</h5>
        <div {...getRootProps({ style })}>
          <input {...getInputProps()} />
          <div>
            <p>Upuść lub kliknij aby wczytac pliki.</p>
            <p>Akceptowane formaty plików to: <b>pdf</b>.</p>
            <p>Maksymalna wielkość pliku to 5 MB.</p>
          </div>
        </div>
      </Col>
      {
        files && files.length > 0 &&
          <Col>
            <h5 style={{ marginTop: '15px' }}>Pliki do wysłania</h5>
            {showUploadedFiles()}
          </Col>
      }
    </ModalBody>
  )

  return (
    <>
      <ModalHeader>
        Prześlij załączniki
      </ModalHeader>
      {
        isLoading
          ? <LoadingIndicator />
          : modalBody()
      }

      <ModalFooter>
        <Button
          color='info'
          onClick={handleSendAttachments}
        >
          Wyślij
        </Button>
        <Button
          color='danger'
          onClick={() => {
            dispatch(hideLoadingScreen())
          }}
        >
          Anuluj
        </Button>
      </ModalFooter>
    </>
  )
}

function SentAttachmentsTable ({ id }) {
  const [files, setFiles] = useState([])
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    async function loadFiles () {
      setIsLoading(true)
      const url = `/OperatFiles/id/${id}`
      const requestOptions = {
        method: 'GET',
        headers: await callForHeader('json')
      }

      request(url, requestOptions).then(response => {
        const filteredFiles = response.filter(x => x.item1.includes('attachment')).map(x => {
          const item1 = x.item1.replace('attachment/', '')
          return ({ item1, item2: x.item2 })
        })
        setFiles(filteredFiles)
      }).catch(_ => {
      }).finally(() => {
        setIsLoading(false)
      })
    }

    loadFiles()
  }, [id])

  const showFiles = () => (
    <Table>
      <thead>
        <tr>
          <th>#</th>
          <th>Nazwa pliku</th>
          <th />
        </tr>
      </thead>
      {generateRows()}
    </Table>
  )

  const generateRows = () => {
    return files.map((item, idx) =>
      (
        <tbody key={idx}>
          <tr>
            <th scope='row'>{idx + 1}</th>
            <td>{item.item1}</td>
            <td><Button color='danger' onClick={() => handleDeleteItem(item.item1)}>Usuń</Button></td>
            <td><Button color='info' onClick={() => handleDownloadItem(item)}>Pobierz</Button></td>
          </tr>
        </tbody>
      ))
  }

  async function handleDeleteItem (filename) {
    setIsLoading(true)
    const url = `/OperatFiles/id/${id}/attachment/filename/${filename}`
    const requestOptions = {
      method: 'DELETE',
      headers: await callForHeader()
    }

    request(url, requestOptions).then(_ => {
      const filteredFiles = files.filter(x => x.item1 !== filename)
      setFiles(filteredFiles)
    }).catch(_ => {
    }).finally(() => {
      setIsLoading(false)
    })
  }

  function handleDownloadItem (item) {
    operatorFilesService.getFileBlob(item.item2, item.item1)
  }

  return (
    <>
      {
        isLoading
          ? <LoadingIndicator />
          : (files && showFiles())
      }
    </>
  )
}

function ConfirmSuccess () {
  const dispatch = useDispatch()
  const data = useSelector(state => state.modal.data)
  return (
    <>
      <ModalHeader> Sukces </ModalHeader>
      <ModalBody> {data.message}</ModalBody>
      <ModalFooter>
        <Button
          color='info'
          onClick={() => {
            dispatch(hideLoadingScreen())
          }}
        >
          Ok
        </Button>
      </ModalFooter>
    </>
  )
}

function ConfirmError () {
  const dispatch = useDispatch()
  const data = useSelector(state => state.modal.data)
  return (
    <>
      <ModalHeader> Błąd </ModalHeader>
      <ModalBody> {data.message} </ModalBody>
      <ModalFooter>
        <Button
          color='info'
          onClick={() => {
            dispatch(hideLoadingScreen())
          }}
        >
          Ok
        </Button>
      </ModalFooter>
    </>
  )
}
