import React, { useEffect, memo } from 'react'
import { compose } from 'redux'
import { connect } from 'react-redux'
import { ReactMarkdown } from 'react-markdown/lib/react-markdown'
import LoadingIndicator from 'components/LoadingIndicator'
import reducer from './reducer'
import saga from './saga'
import { useInjectReducer } from 'utils/injectReducer'
import { useInjectSaga } from 'utils/injectSaga'
import { createStructuredSelector } from 'reselect'
import { makeSelectHomePageContent } from './selectors'
import { loadHomePageContent } from './actions'
import { useTranslation } from 'react-i18next'
import remarkGfm from 'remark-gfm'

const key = 'homePage'

function Home (props) {
    useInjectReducer({ key, reducer })
    useInjectSaga({ key, saga })
    const { i18n } = useTranslation();

    useEffect(() => {
        props.handleLoadPageContent()
        // eslint-disable-next-line
    }, [i18n.language])

    return (
    <div className='content'>
        <div className='container'>
            {
                props.pageContent.loading
                    ? <LoadingIndicator />
                    : (<ReactMarkdown remarkPlugins={[remarkGfm]}>{props.pageContent.content}</ReactMarkdown>)
            }
        </div>
    </div>
    )
}

const mapStateToProps = createStructuredSelector({
    pageContent: makeSelectHomePageContent()
  })
  
export function mapDispatchToProps (dispatch) {
    return {
      handleLoadPageContent: () => dispatch(loadHomePageContent()),
    }
}
  
const withConnect = connect(mapStateToProps, mapDispatchToProps)
  
export default compose(withConnect, memo)(Home)
  

