import React, { useEffect } from 'react'
import { useMsal } from '@azure/msal-react'
import { clientService } from '../../services'
import { modalTypeConstants } from '../../constants'
import { useDispatch, useSelector } from 'react-redux'
import { showLoadingScreen, hideLoadingScreen } from 'views/Modal/actions'
import { loadUser } from 'views/App/actions'

function EmptyUser (props) {
  const { instance } = useMsal()
  const user = useSelector(state => state.global.user)
  const dispatch = useDispatch()
  useEffect(() => {
    if (user.userType === '') {
      
      const account = instance.getActiveAccount()
      clientService.getAdminDataById(account.idTokenClaims.oid).then(exist => {
        if (!exist) {
          const newClient = {
            id: account.idTokenClaims.oid,
            name: account.name,
            mail: account.idTokenClaims.emails[0],
            address: `${account.idTokenClaims.streetAddress}, ${account.idTokenClaims.postalCode}, ${account.idTokenClaims.city}`,
            firstname: account.idTokenClaims.given_name,
            lastname: account.idTokenClaims.family_name,
            phone: account.idTokenClaims.extension_Numertelefonu
          }
          dispatch(showLoadingScreen(modalTypeConstants.LOADING, { newClient, message: 'Trwa tworzenie użytkownika' }))
          clientService.createNewClient(newClient).then(() => {
            dispatch(loadUser(newClient.id))
            dispatch(hideLoadingScreen())
          }).catch(err => {console.log(err)})
        }
      }).catch(err => {console.log(err)})
    }
  // eslint-disable-next-line
  }, [user.userType, instance, dispatch, props.history])

  return (
    <div />
  )
}

export default EmptyUser
