import {
  LOAD_OPERATORS_INFO,
  OPERATORS_INFO_LOADED,
  LOAD_OPERATORS_INFO_ERROR,
  SET_SUBSCRIPTION_PAGE_NUMBER,
  SET_OPERATORS_INFO_SORT_PARAMETER,
  SET_OPERATORS_INFO_FILTERS,
  SET_CURRENT_OPERATOR,
  UPDATE_SUBSCRIPTION,
  CREATE_SUBSCRIPTION,
  RESET_SUBSCRIPTIONS
} from './constants'

export function loadOperatorsInfo () {
  return {
    type: LOAD_OPERATORS_INFO
  }
}

export function operatorsInfoLoaded (payload) {
  return {
    type: OPERATORS_INFO_LOADED,
    payload
  }
}

export function loadOperatorsInfoError (payload) {
  return {
    type: LOAD_OPERATORS_INFO_ERROR,
    payload
  }
}

export function setSubscriptionPageNumber (payload) {
  return {
    type: SET_SUBSCRIPTION_PAGE_NUMBER,
    payload
  }
}

export function setOperatorsInfoSortParameter (payload) {
  return {
    type: SET_OPERATORS_INFO_SORT_PARAMETER,
    payload
  }
}

export function setOperatorsInfoFilters (payload) {
  return {
    type: SET_OPERATORS_INFO_FILTERS,
    payload
  }
}

export function setCurrentOperator (payload) {
  return {
    type: SET_CURRENT_OPERATOR,
    payload
  }
}

export function updateSubscription (payload) {
  return {
    type: UPDATE_SUBSCRIPTION,
    payload
  }
}

export function createSubscription (payload) {
  return {
    type: CREATE_SUBSCRIPTION,
    payload
  }
}

export function resetSubscriptions () {
  return {
    type: RESET_SUBSCRIPTIONS
  }
}
