import React from 'react'
import {
  Button,
  Card,
  CardBody,
  UncontrolledCollapse,
  UncontrolledPopover,
  PopoverBody
} from 'reactstrap'

import { MapToPrewiew } from '../Maps'
import { useTranslation } from 'react-i18next'

function OrderInfoUncontrolledCollapse (props) {
  const { t } = useTranslation('common')
  const cordsList = props.row.latitudeLongitude.split(',')
  const cords = cordsList.length > 1 && { lat: parseFloat(cordsList[0]), lng: parseFloat(cordsList[1]) }

  return (
    <>
      <UncontrolledCollapse toggler='#orderData'>
        <Card>
          <CardBody>
            <div>
              <p>
                <b>{t('common.orderInfo.description')}:</b> {props.row.description}
              </p>
            </div>
            <div>
              <p>
                <b>{t('common.orderInfo.address')}:</b> {props.row.location}
              </p>
            </div>
            <div>
              <p>
                <b>{t('common.orderInfo.orderSubject')}:</b> {props.row.orderSubject}
              </p>
            </div>
            <div>
              <p>
                <b>{t('common.orderInfo.valuationScope')}:</b> {props.row.valuationScope}
              </p>
            </div>
            <div>
              <p>
                <b>{t('common.orderInfo.landRegisterNumber')}:</b> {props.row.ekw}
              </p>
            </div>
            <div>
              <p>
                <b>{t('common.orderInfo.propertyIdentification')}:</b> {props.row.propertyIdentification}
              </p>
            </div>
            <Button
              className='long-btn' id='UncontrolledPopoverMAP'
              type='button' color='success'
            >Pokaż mapę
            </Button>
            <UncontrolledPopover
              placement='right'
              target='UncontrolledPopoverMAP'
              trigger='legacy'
            >
              <PopoverBody>
                {cordsList[0] !== '0' ? <MapToPrewiew cords={cords} /> : <div>Nie podano współrzednych</div>}
              </PopoverBody>
            </UncontrolledPopover>
          </CardBody>
        </Card>
      </UncontrolledCollapse>
    </>
  )
}

export default OrderInfoUncontrolledCollapse
