import { useMsal } from '@azure/msal-react'
import { useEffect } from 'react'

function Logout () {
  const { instance } = useMsal()
  useEffect(() => {
    instance.logout()
  }, [instance])

  // TODO: lepiej przerobić te wszystkie puste komponenty na zwykle funkcje (ale trzeba sprawdzic czy zadziala dobrze)
  return (
    <>
    </>
  )
}

export default Logout
