import React from 'react'
import { Link } from 'react-router-dom'
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem
} from 'reactstrap'

import { adminRoutes, clientRoutes, emptyRoutes, operatorRoutes } from '../../routes/index'
import MessageBell from './MessageBell'
import SettingsItem from './SettingsItem'
import NameItem from './NameItem'
import Language from './Language'

class Header extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      isOpen: false,
      dropdownOpen: false,
      color: 'transparent'
    }
    this.toggle = this.toggle.bind(this)
    this.sidebarToggle = React.createRef()
  }

  toggle () {
    if (this.state.isOpen) {
      this.setState({
        color: 'transparent'
      })
    } else {
      this.setState({
        color: 'dark'
      })
    }
    this.setState({
      isOpen: !this.state.isOpen
    })
  }

  getRouting () {
    switch(this.props.userType) {
      case 'admin':
        return adminRoutes(this.props.t)
      case 'operator':
        return operatorRoutes(this.props.t)
      case 'client':
        return clientRoutes(this.props.t)
      default:
        return emptyRoutes(this.props.t)
    }
  }

  getBrand () {
    let brandName = ''
    const routing = this.getRouting().filter((prop, key) => prop.path !== '/')
    for(const x of routing){
      if (window.location.href.indexOf(x.path) !== -1) {
        brandName = x.name
        break
      }
    }

    return brandName
  }

  openSidebar () {
    document.documentElement.classList.toggle('nav-open')
    this.sidebarToggle.current.classList.toggle('toggled')
  }

  // function that adds color dark/transparent to the navbar on resize (this is for the collapse)
  updateColor () {
    if (window.innerWidth < 993 && this.state.isOpen) {
      this.setState({
        color: 'dark'
      })
    } else {
      this.setState({
        color: 'transparent'
      })
    }
  }

  componentDidMount () {
    window.addEventListener('resize', this.updateColor.bind(this))
  }

  componentDidUpdate (e) {
    if (
      window.innerWidth < 993 &&
      e.history.location.pathname !== e.location.pathname &&
      document.documentElement.className.indexOf('nav-open') !== -1
    ) {
      document.documentElement.classList.toggle('nav-open')
      this.sidebarToggle.current.classList.toggle('toggled')
    }
  }

  render () {
    return (
      // add or remove classes depending if we are on full-screen-maps page or not
      <Navbar
        color={
          this.props.location.pathname.indexOf('full-screen-maps') !== -1
            ? 'dark'
            : this.state.color
        }
        expand='lg'
        className={
          this.props.location.pathname.indexOf('full-screen-maps') !== -1
            ? 'navbar-absolute fixed-top'
            : 'navbar-absolute fixed-top ' +
              (this.state.color === 'transparent' ? 'navbar-transparent ' : '')
        }
      >
        <div>
          <div className='navbar-wrapper'>
            <div className='navbar-toggle'>
              <button
                type='button'
                ref={this.sidebarToggle}
                className='navbar-toggler'
                onClick={() => this.openSidebar()}
              >
                <span className='navbar-toggler-bar bar1' />
                <span className='navbar-toggler-bar bar2' />
                <span className='navbar-toggler-bar bar3' />
              </button>
            </div>
            <NavbarBrand>{this.getBrand()}</NavbarBrand>
          </div>
        </div>
        <NavbarToggler onClick={this.toggle}>
          <span className='navbar-toggler-bar navbar-kebab' />
          <span className='navbar-toggler-bar navbar-kebab' />
          <span className='navbar-toggler-bar navbar-kebab' />
        </NavbarToggler>
        <Collapse
          isOpen={this.state.isOpen}
          navbar
          className='justify-content-end'
        >
          <NavbarBrand><Language /></NavbarBrand>
          <NavbarBrand href='/users-profile'><NameItem /></NavbarBrand>
          <Nav navbar>
            <NavItem>
              <Link to='/users-profile' className='nav-link btn-magnify'>
                <i className='nc-icon nc-layout-11' />
                <p>
                  <span className='d-lg-none d-md-block'>Profil</span>
                </p>
              </Link>
            </NavItem>
            <MessageBell />
            <SettingsItem />
          </Nav>
        </Collapse>
      </Navbar>
    )
  }
}

export default Header
