import { createSelector } from 'reselect'
import { initialState } from './reducer'

const selectMarketPage = (state) => state.marketPage || initialState

const makeSelectOrders = () =>
  createSelector(selectMarketPage, (state) => state.orders)

const makeSelectSaveOrder = () =>
  createSelector(selectMarketPage, (state) => state.saveOrder)

const makeSelectActiveOrder = () =>
  createSelector(selectMarketPage, (state) => state.activeOrder)

const makeSelectPropositionId = () =>
  createSelector(selectMarketPage, (state) => state.propositionId)

const makeSelectPropositions = () =>
  createSelector(selectMarketPage, (state) => state.propositions)

export {
  selectMarketPage,
  makeSelectOrders,
  makeSelectSaveOrder,
  makeSelectActiveOrder,
  makeSelectPropositionId,
  makeSelectPropositions
}
