import { callForHeader } from '../utils/HeadersCall'
const apiUrl = process.env.REACT_APP_API_URL

export const orderService = {
  getOperatorOrders,
  reserveOrder,
  getSingleOrder
}

async function reserveOrder (guidOrder, guidOperator) {
  const requestOptions = {
    method: 'PUT',
    headers: await callForHeader('json')
  }

  return window.fetch(`${apiUrl}/Order/${guidOrder}/reserve/${guidOperator}`, requestOptions).then(handleResponse)
}

async function getSingleOrder (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return window.fetch(`${apiUrl}/Order/id/${id}`, requestOptions).then(handleResponse)
}

async function getOperatorOrders (guidOperator) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/Order/announced/${guidOperator}`, requestOptions).then(handleResponse)
}

function handleResponse (response) {
  return response.text().then(text => {
    const data = text && JSON.parse(text)
    if (!response.ok) {
      if (response.status === 404) {
        return []
      }

      const error = (data && data.message) || response.statusText
      return Promise.reject(error)
    }
    const pageCount = response.headers.get('X-Page-Count')
    pageCount && localStorage.setItem('pageCount', pageCount)
    return data
  }).then(data => { return data })
}
