import React from 'react'
import PropTypes from 'prop-types'
import { Col, FormGroup, Input, Label } from 'reactstrap'
const ShowIdCheckbox = ({ setIsIdHidden, isIdHidden, label }) => {
  return (
    <FormGroup>
      <Col sm={{ size: 10 }}>
        <FormGroup>
          <Label
            for='show-operat-id' sm={10} onClick={() => {
              setIsIdHidden(!isIdHidden)
            }}
          >
            <Input
              name='show-operat-id'
              id='show-operat-id'
              type='checkbox'
              checked={!isIdHidden}
              onChange={() => {
                setIsIdHidden(!isIdHidden)
              }}
            />
            {label}
          </Label>
        </FormGroup>
      </Col>
    </FormGroup>
  )
}

ShowIdCheckbox.propTypes = {
  setIsIdHidden: PropTypes.func.isRequired,
  isIdHidden: PropTypes.bool.isRequired,
  label: PropTypes.string.isRequired
}

export default ShowIdCheckbox
