import { DOWNLOAD_FROM_EKW, LOAD_OPERAT_INITIAL_WEIGHTS, FILES_PUT_PROTOCOL, SET_OPERAT, ADD_NEW_RATE, REMOVE_WEIGHTS, FILES_LOADED, REMOVE_RATINGS, FILES_PUT_WEIGHTS, PUT_RATE, ADD_NEW_WEIGHT, CSV_EDIT_CELL, FILES_ADD_LINES_TO_CSV_FROM_CSV, FILES_ADD_LINE_TO_CSV, FILES_CLEAR_STATE, FILES_EDIT_OPERAT, FILES_GET_CSV, FILES_GET_INPUT, FILES_GET_OPERAT, FILES_LOAD_EMPTY_INPUT, FILES_LOAD_EMPTY_OPERAT, FILES_LOAD_EMPTY_PHOTO, FILES_LOAD_OPERAT_TYPE, FILES_LOAD_PHOTO_FILE_TO_GALLERY, FILES_PUT_OPERAT, FILES_REMOVE_LINE_FROM_CSV, REMOVE_OPERAT_FROM_GENERATED, REMOVE_PHOTOS, REMOVE_PHOTO_FILES, SET_ALL_VALID, FILES_GET_WEIGHTS, OPERAT_UPDATE_LIST, FILES_LOAD_EMPTY_PROTOCOL, FILES_GET_PROTOCOL, OPERAT_UPDATE_PROTOCOL_LIST } from './constants'
import { v4 as uuidv4 } from 'uuid'

import produce from 'immer'
export const initialState = {
  operatTypeId: '',
  id: '',
  files: {},
  csv: [],
  disabled: true,
  input: [],
  weights: [],
  photo: {},
  operat: {},
  protocol: [],
  // used to refresh photos, appended to the end of the link
  randomString: Date.now()
}

const operatReducer = (state = initialState, action) =>
  produce(state, (draft) => {
    let ekw = ''
    try {
      ekw = action.payload && action.payload.ekw && JSON.parse(action.payload.ekw)
    } catch (ex) {
      ekw = ''
    }

    const updateList = (src) => {
      let list = src[action.payload.id].data[action.payload.idx].value
      if (action.payload.del) {
        list.splice(action.payload.listIdx, 1)
      } else {
        list = [...list, action.payload.value]
      }
      src[action.payload.id].data[action.payload.idx].value = list
    }
    const input = draft.input
    const kw = action.payload && action.payload.name && input.find(x => x.name === action.payload.name)
    // eslint-disable-next-line default-case
    switch (action.type) {
      case SET_OPERAT:
        draft.operat = action.payload
        break
      case FILES_CLEAR_STATE:
        draft.input = []
        draft.disabled = false
        draft.id = ''
        draft.photo = []
        draft.csv = []
        draft.files = []
        draft.weights = []
        draft.protocol = []
        draft.randomString = Date.now()
        break
      case FILES_PUT_OPERAT:
        draft.input[action.payload.id].data[action.payload.idx].value = action.payload.value
        break
      case FILES_PUT_PROTOCOL:
        draft.protocol[action.payload.id].data[action.payload.idx].value = action.payload.value
        break
      case OPERAT_UPDATE_LIST:
        updateList(draft.input)
        break
      case OPERAT_UPDATE_PROTOCOL_LIST:
        updateList(draft.protocol)
        break
      case FILES_PUT_WEIGHTS: {
        const cellIndex = draft.weights.findIndex(row => row.name === action.payload.name)
        const cellToEdit = draft.weights[cellIndex]
        cellToEdit[action.payload.dataField] = action.payload.newValue
        draft.weights[cellIndex] = cellToEdit
        break
      }
      case ADD_NEW_WEIGHT: {
        draft.weights = [...draft.weights, {
          name: uuidv4(),
          description: 'opis',
          participation: 20,
          rate: 0,
          justification: '',
          ratings: [
            {
              rate: 0,
              rateText: 'Wybierz ocenę',
              description: 'opis'
            }
          ]
        }]
        break
      }
      case ADD_NEW_RATE: {
        draft.weights[action.payload.rowIndex].ratings = [...draft.weights[action.payload.rowIndex].ratings, {
          id: uuidv4(),
          rate: draft.weights[action.payload.rowIndex].ratings.length === 0 ? 0 : Math.max.apply(Math, draft.weights[action.payload.rowIndex].ratings.map(x => parseInt(x.rate))) + 1,
          rateText: 'ocena',
          description: 'opis'
        }]
        break
      }
      case PUT_RATE: {
        const cellIndex = draft.weights[action.payload.rowIndex].ratings.findIndex(row => row.rate === action.payload.rate)
        const cellToEdit = draft.weights[action.payload.rowIndex].ratings[cellIndex]
        cellToEdit[action.payload.dataField] = action.payload.newValue
        draft.weights[action.payload.rowIndex].ratings[cellIndex] = cellToEdit
        break
      }
      case FILES_GET_OPERAT:
        draft.disabled = action.payload.disabled
        draft.id = action.payload.id
        draft.input = action.payload.files
        break
      case FILES_LOAD_OPERAT_TYPE:
        draft.input = action.payload.input
        draft.photo = action.payload.photo
        break
      case FILES_LOAD_EMPTY_PHOTO:
        draft.photo = action.payload
        break
      case FILES_LOAD_EMPTY_INPUT:
        draft.input = action.payload
        break
      case FILES_LOAD_EMPTY_PROTOCOL:
        draft.protocol = action.payload
        break
      case LOAD_OPERAT_INITIAL_WEIGHTS:
        draft.weights = action.payload
        break
      case FILES_GET_INPUT:
        draft.input = action.payload.input
        break
      case FILES_GET_PROTOCOL:
        draft.protocol = action.payload.protocol
        break
      case FILES_GET_WEIGHTS:
        draft.weights = action.payload.weights
        break
      case FILES_GET_CSV:
        draft.csv = action.payload.csv
        break
      case FILES_LOADED:
        draft.id = action.payload.id
        draft.files = action.payload.files
        draft.operatInfo = action.payload.operatInfo
        draft.disabled = true
        draft.operatTypeId = action.payload.operatTypeId
        draft.randomString = Date.now()
        break
      case FILES_LOAD_EMPTY_OPERAT:
        draft.disabled = action.payload.disabled
        draft.id = action.payload.id
        break
      case FILES_EDIT_OPERAT:
        draft.disabled = action.payload.disabled
        break
      case FILES_ADD_LINE_TO_CSV:
        draft.csv = draft.csv.concat(action.payload.newRow)
        break
      case FILES_ADD_LINES_TO_CSV_FROM_CSV:
        draft.csv = draft.csv.concat(action.payload.newRows)
        break
      case FILES_REMOVE_LINE_FROM_CSV:
        draft.csv = draft.csv.filter(item => !action.payload.rows.includes(item.id))
        break
      case FILES_LOAD_PHOTO_FILE_TO_GALLERY:
        if (!draft.photo.data[action.payload.id].data.some(x => x.id && x.id === action.payload.files.id)) {
          draft.photo.data[action.payload.id].data.shift()
          draft.photo.data[action.payload.id].data.push(action.payload.files)
        }
        break
      case REMOVE_PHOTOS:
        for (const i of draft.photo.data[action.payload.id].data) {
          delete i.file
          delete i.url
          draft.files.photo = draft.files.photo.filter(x => x.item1 !== i.title)
        }
        break
      case REMOVE_PHOTO_FILES:
        for (const i of draft.photo.data[action.payload.id].data) {
          delete i.file
        }
        break
      case REMOVE_OPERAT_FROM_GENERATED:
        draft.generated = draft.generated.filter(item => item.id !== action.payload)
        break
      case DOWNLOAD_FROM_EKW:
        Object.entries(ekw).forEach(key => {
          kw.data = kw.data.map(obj => obj.name === key ? { ...obj, value: ekw[key] } : obj)
        })
        draft.input = input.map(x => x.name === action.payload.name ? kw : x)
        break
      case SET_ALL_VALID:
        for (let i = 0; i < draft.input.length; i++) {
          if (draft.input[i].data) {
            for (let j = 0; j < draft.input[i].data.length; j++) {
              if (typeof draft.input[i].data[j].isValid !== 'undefined') {
                draft.input[i].data[j].isValid = true
              }
            }
          }
        }
        break
      case REMOVE_WEIGHTS:
      {
        let indexes = []
        for (let i = 0; i < action.payload.indexes.length; i++) {
          indexes = [...indexes, draft.weights.findIndex(x => x.name === action.payload.indexes[i])]
        }
        for (let i = 0; i < indexes.length; i++) {
          draft.weights.splice(indexes[i] - i, 1)
        }
        break
      }
      case REMOVE_RATINGS:
      {
        const ratings = [...draft.weights[action.payload.rowIndex].ratings]
        let indexes = []
        for (let i = 0; i < action.payload.indexes.length; i++) {
          indexes = [...indexes, ratings.findIndex(x => x.id === action.payload.indexes[i])]
        }
        for (let i = 0; i < indexes.length; i++) {
          ratings.splice(indexes[i] - i, 1)
        }
        for (let i = 0; i < ratings.length; i++) {
          ratings[i].rate = i
        }
        draft.weights[action.payload.rowIndex].ratings = ratings
        break
      }
      case CSV_EDIT_CELL:
      {
        const found = draft.csv.find(x => x.id === action.id)
        if (found) {
          found[action.columnToChange] = action.newValue
        }
        break
      }
    }
  })

export default operatReducer
