import {
  CREATE_OPERAT, LOAD_OPERATS, LOAD_OPERATS_ERROR,
  UPLOAD_OWN_GENERATED, SEND_PDF, LOAD_OPERAT_ORDER,
  LOAD_OPERAT_ORDER_ERROR, OPERATS_LOADED, OPERAT_ORDER_LOADED,
  REMOVE_OPERAT, SELECT_OPERAT_TYPE, SET_ACTIVE_OPERAT,
  SET_PAGE_NUMBER, SET_OPERATS_FILTERS,
  SET_OPERATS_SORT_PARAMETERS,
  UPLOAD_SIGNED_OPERAT
} from './constants'

export function loadOperats () {
  return {
    type: LOAD_OPERATS
  }
}

export function operatsLoaded (operats) {
  return {
    type: OPERATS_LOADED,
    payload: operats
  }
}

export function loadOperatsError (error) {
  return {
    type: LOAD_OPERATS_ERROR,
    payload: error
  }
}

export function removeOperat () {
  return {
    type: REMOVE_OPERAT
  }
}

export function setActiveOperat (operat) {
  return {
    type: SET_ACTIVE_OPERAT,
    payload: operat
  }
}

export function createOperat () {
  return {
    type: CREATE_OPERAT
  }
}

export function selectOperatType (operatType) {
  return {
    type: SELECT_OPERAT_TYPE,
    payload: operatType
  }
}

export function setPageNumber (pageNumber) {
  return {
    type: SET_PAGE_NUMBER,
    payload: pageNumber
  }
}

export function loadOperatOrder () {
  return {
    type: LOAD_OPERAT_ORDER
  }
}

export function operatOrderLoaded (operatOrder) {
  return {
    type: OPERAT_ORDER_LOADED,
    payload: operatOrder
  }
}

export function loadOperatOrderError (error) {
  return {
    type: LOAD_OPERAT_ORDER_ERROR,
    payload: error
  }
}

export function sendPdf (id, emails, sendToPrincipal) {
  return {
    type: SEND_PDF,
    id,
    emails,
    sendToPrincipal
  }
}

export function uploadOwnGeneratedOperat (id, file) {
  return {
    type: UPLOAD_OWN_GENERATED,
    id,
    file
  }
}

export function uploadSignedOperat (id, file) {
  return {
    type: UPLOAD_SIGNED_OPERAT,
    id,
    file
  }
}

export function setOperatFilters (payload) {
  return {
    type: SET_OPERATS_FILTERS,
    payload
  }
}

export function setOperatSortParameters (payload) {
  return {
    type: SET_OPERATS_SORT_PARAMETERS,
    payload
  }
}
