import { LOAD_OPERAT_TYPES, LOAD_OPERAT_TYPES_ERROR, LOAD_USER, LOAD_USER_ERROR, OPERAT_TYPES_LOADED, USER_LOADED, LOAD_EKW_DATA, EKW_DATA_LOADED, LOAD_PUBLIC_SETTINGS, PUBLIC_SETTINGS_LOADED, SET_NOTIFY_REGISTER } from './constants'

export function loadOperatTypes () {
  return {
    type: LOAD_OPERAT_TYPES
  }
}

export function operatTypesLoaded (operatTypes) {
  return {
    type: OPERAT_TYPES_LOADED,
    payload: operatTypes
  }
}

export function loadOperatTypesError (error) {
  return {
    type: LOAD_OPERAT_TYPES_ERROR,
    payload: error
  }
}

export function loadUser (id) {
  return {
    type: LOAD_USER,
    id
  }
}

export function userLoaded (payload) {
  return {
    type: USER_LOADED,
    payload
  }
}

export function loadUserError (payload) {
  return {
    type: LOAD_USER_ERROR,
    payload
  }
}

export function loadEkwData () {
  return {
    type: LOAD_EKW_DATA
  }
}

export function ekwDataLoaded (payload) {
  return {
    type: EKW_DATA_LOADED,
    payload
  }
}

export function loadPublicSettings () {
  return {
    type: LOAD_PUBLIC_SETTINGS
  }
}

export function publicSettingsLoaded (payload) {
  return {
    type: PUBLIC_SETTINGS_LOADED,
    payload
  }
}

export function setNotifyRegister (payload) {
  return {
    type: SET_NOTIFY_REGISTER,
    payload
  }
}
