import { createSelector } from 'reselect'
import { initialState } from './reducer'

const selectOperat = (state) => state.operat || initialState

const makeSelectInput = () =>
  createSelector(selectOperat, (state) => state.input)

const makeSelectId = () =>
  createSelector(selectOperat, state => state.id)

const makeSelectInputFileUrl = () =>
  createSelector(selectOperat, state => state.files.input.item2)

const makeSelectWeights = () =>
  createSelector(selectOperat, state => state.weights)

const makeSelectActiveOperat = () =>
  createSelector(selectOperat, state => state.operat)

const makeSelectProtocol = () =>
  createSelector(selectOperat, state => state.protocol)

const makeSelectCsv = () =>
  createSelector(selectOperat, state => state.csv)
export {
  selectOperat,
  makeSelectInput,
  makeSelectId,
  makeSelectInputFileUrl,
  makeSelectWeights,
  makeSelectActiveOperat,
  makeSelectCsv,
  makeSelectProtocol
}
