
import produce from 'immer'
import { LOAD_OPERATORS_INFO, LOAD_OPERATORS_INFO_ERROR, OPERATORS_INFO_LOADED, SET_OPERATORS_INFO_FILTERS, SET_OPERATORS_INFO_SORT_PARAMETER, SET_SUBSCRIPTION_PAGE_NUMBER, SET_CURRENT_OPERATOR } from './constants'
import { capitalizeFirstLetter } from 'utils/capitalizeFirstLetter'

export const initialState = {
  operatorsInfo: {
    loading: false,
    error: false,
    data: [],
    pageNumber: 1,
    sortFieldName: null,
    sortDirection: 'desc',
    filters: {
      mail: '',
      name: ''
    }
  },
  currentOperator: {
    id: '',
    name: ''
  }
}

const subscriptionsReducer = (state = initialState, action) => produce(state, (draft) => {
  // eslint-disable-next-line default-case
  switch (action.type) {
    case LOAD_OPERATORS_INFO:
      draft.operatorsInfo.data = []
      draft.operatorsInfo.error = false
      draft.operatorsInfo.loading = true
      break
    case OPERATORS_INFO_LOADED:
      draft.operatorsInfo.data = action.payload
      draft.operatorsInfo.error = false
      draft.operatorsInfo.loading = false
      break
    case LOAD_OPERATORS_INFO_ERROR:
      draft.operatorsInfo.data = []
      draft.operatorsInfo.loading = false
      draft.operatorsInfo.error = action.payload
      break
    case SET_SUBSCRIPTION_PAGE_NUMBER:
      draft.operatorsInfo.pageNumber = action.payload
      break
    case SET_OPERATORS_INFO_SORT_PARAMETER:
      draft.operatorsInfo.sortDirection =
        draft.operatorsInfo.sortDirection === 'asc' ? 'desc' : 'asc'
      draft.operatorsInfo.sortFieldName = capitalizeFirstLetter(action.payload)
      break
    case SET_OPERATORS_INFO_FILTERS:
      draft.operatorsInfo.filters = action.payload
      break
    case SET_CURRENT_OPERATOR:
      draft.currentOperator = action.payload
      break
  }
})

export default subscriptionsReducer
