import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { Button, Form, FormGroup, Input, Spinner, Row, Col } from 'reactstrap'
import ReactStars from 'react-rating-stars-component'
import { operatService } from 'services'
const ReviewForm = ({ onSubmit, operat }) => {
  const [rating, setRating] = useState(5)
  const [comment, setComment] = useState('')
  const [finished, setFinished] = useState(false)
  useEffect(() => {
    operatService.getOperatReview(operat.id).then(reviews => {
      if (reviews.length > 0) {
        setRating(reviews[0].rating)
        setComment(reviews[0].comment)
      }
    }).catch(err => {
      console.log(err)
    }).finally(() => setTimeout(setFinished(true), 500))
    return function cleanup () {
      setRating(5)
      setComment('')
      setFinished(false)
    }
  }
  , [operat.id])

  const form = () =>
    <Form
      onSubmit={(e) => onSubmit(e, rating)}
    >
      <h4>Twoja opinia o operacie:</h4>
      <FormGroup>
        <Input
          type='textarea'
          name='review-comment'
          id='review-comment'
          placeholder='Komentarz'
          value={comment}
          onChange={(e) => setComment(e.target.value)}
          required
        />
      </FormGroup>
      <ReactStars
        count={5}
        value={rating}
        edit
        onChange={(e) => setRating(e)}
        size={24}
        id='review-rating-stars'
        name='review-rating-stars'
        activeColor='#ffd700'
      />
      <Button
        color='success'
      >
        Zapisz recenzję
      </Button>
    </Form>

  const spinner = () =>
    <Row>
      <Col sm='12' md={{ size: 3, offset: 5 }}>
        <Spinner
          color='primary'
          style={{ width: '8rem', height: '8rem' }}
          animation='border'
          variant='info'
        >
          {' '}
        </Spinner>
      </Col>
    </Row>
  return (
    <div>
      {
      finished
        ? form()
        : spinner()
          }
    </div>
  )
}

ReviewForm.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  orderId: PropTypes.object.isRequired
}

export default ReviewForm
