import { modalTypeConstants } from '../../constants'

import { call, put, select, takeLatest } from 'redux-saga/effects'
import { callForHeader } from 'utils/HeadersCall'
import { showLoadingScreen, hideLoadingScreen } from 'views/Modal/actions'
import { filesLoaded, getFormatedFiles, removePhotoFiles, setAllValid } from './actions'
import { FILES_GET_BY_OPERAT, FILES_SEND_PHOTOS, GENERATE_OPERAT, GENERATE_PROTOCOL, SAVE_CSV_AREA, SAVE_INPUT_AREA, SAVE_WEIGHTS_AREA } from './constants'
import { makeSelectCsv, makeSelectId, makeSelectInput, makeSelectProtocol, makeSelectWeights } from './selectors'
import { addNotification } from 'utils/addNotification'
import { makeSelectActiveOperat } from 'views/History/selectors'
import { operatFilesService } from 'services'
import { handleRequestWithTimeout } from 'views/Modal/timeout'

export function * saveWeightsArea (action) {
  yield put(showLoadingScreen(
    modalTypeConstants.LOADING,
    { message: 'saveWeights', translate: true }
  ))
  const data = yield select(makeSelectWeights())
  const sum = data.map(x => x.participation).reduce((partialSum, a) => partialSum + a, 0)
  if (sum !== 100) {
    yield put(hideLoadingScreen())
    yield call(addNotification, '', 'Suma cech nieruchomości musi być równa 100%', 'danger')
    return
  }
  const formData = new FormData()
  const blb = new Blob([JSON.stringify({ weights: data })], { type: 'application/json' })
  formData.append('file', blb, 'weights.json')
  yield call(handleUploadConvertRequest, formData, action.id)
}

export function * saveCsvArea (action) {
  try {
    yield put(showLoadingScreen(
      modalTypeConstants.LOADING,
      { message: 'saveCsv', translate: true }
    ))
    const operat = yield select(makeSelectActiveOperat())
    const weights = yield select(makeSelectWeights())
    const csv = yield select(makeSelectCsv())
    let isValid = true
    action.columnKeys.forEach(key => {
      if (csv.some(obj => Object.prototype.hasOwnProperty.call(obj, key) && (obj[key] === '' || obj[key] === undefined || obj[key] === null))) {
        isValid = false
      }
    })
    if (!isValid) {
      yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'saveCsvValidationError', translate: true }))
      return
    }
    yield call(operatFilesService.sendCSVFile, csv.map(e => ({ ...e })), operat.id, weights)
    yield put(hideLoadingScreen())
    yield put(getFormatedFiles())
  } catch (err) {
    console.log(err)
    yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'saveCsvFailed', translate: true }))
  }
}

function * handleUploadConvertRequest (formData, operatId) {
  const requestOptions = {
    method: 'POST',
    headers: yield call(callForHeader),
    body: formData
  }
  const requestUrl = `/OperatFiles/upload/${operatId}/convert`
  try {
    yield call(handleRequestWithTimeout, requestUrl, requestOptions)
    yield put(hideLoadingScreen())
    yield put(getFormatedFiles())
  } catch (err) {
    yield put(hideLoadingScreen())
    console.log(err)
  }
}

export function * saveInputArea (action) {
  yield put(setAllValid())
  yield put(getFormatedFiles())

  yield put(showLoadingScreen(
    modalTypeConstants.LOADING,
    { message: 'saveInputArea', translate: true }
  ))
  const data = yield select(makeSelectInput())
  const formData = new FormData()
  const blb = new Blob([JSON.stringify({ input: data })], { type: 'application/json' })
  formData.append('file', blb, 'params.json')
  yield call(handleUploadConvertRequest, formData, action.id)
}

export function * generateOperat () {
  const requestOptions = {
    method: 'POST',
    headers: yield call(callForHeader)
  }
  const operatId = yield select(makeSelectId())
  const requestUrl = `/Operat/id/${operatId}/generate`
  try {
    yield put(showLoadingScreen(
      modalTypeConstants.LOADING,
      { message: 'generateOperat', translate: true }
    ))
    yield call(handleRequestWithTimeout, requestUrl, requestOptions)
    yield put(hideLoadingScreen())
    yield call(addNotification, '', 'Operat zostal wygenerowany', 'success')
  } catch (err) {
    if (Object.prototype.hasOwnProperty.call(err, 'response') && err.response.status === 402) {
      yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'unactiveSubscription', translate: true }))
    } else {
      yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'generateOperatFailed', translate: true }))
    }
  }
}

export function * generateProtocol () {
  try {
    const operatId = yield select(makeSelectId())
    yield put(showLoadingScreen(
      modalTypeConstants.LOADING,
      { message: 'generateProtocol', translate: true }
    ))
    const protocolData = yield select(makeSelectProtocol())
    const formData = new FormData()
    const blb = new Blob([JSON.stringify({ protocol: protocolData })], { type: 'application/json' })
    formData.append('file', blb, 'protocol.json')
    const saveRequestOptions = {
      method: 'POST',
      headers: yield call(callForHeader),
      body: formData
    }
    const saveRequestUrl = `/OperatFiles/upload/${operatId}/convert`
    yield call(handleRequestWithTimeout, saveRequestUrl, saveRequestOptions)

    const generateRequestOptions = {
      method: 'POST',
      headers: yield call(callForHeader)
    }
    const generateRequestUrl = `/Operat/id/${operatId}/generate/protocol`
    yield call(handleRequestWithTimeout, generateRequestUrl, generateRequestOptions)
    yield put(hideLoadingScreen())
    yield put(getFormatedFiles())
    yield call(addNotification, '', 'Protokół został wygenerowany', 'success')
  } catch (err) {
    yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'generateProtocolFailed', translate: true }))
  }
}

export function * sendPhotos ({ payload }) {
  yield put(showLoadingScreen(
    modalTypeConstants.LOADING,
    { message: 'sendPhotos', translate: true }
  ))

  const data = payload.photos.filter(x => Object.prototype.hasOwnProperty.call(x, 'file'))
  if (data.length === 0) {
    return
  }

  const formData = new FormData()
  data.forEach((element, idx) => {
    formData.append(`file${idx}`, element.file, element.file.name)
  })

  const requestUrl = `/OperatFiles/upload2/${payload.operatId}`
  const requestOptions = {
    method: 'POST',
    headers: yield call(callForHeader),
    body: formData
  }

  try {
    yield call(handleRequestWithTimeout, requestUrl, requestOptions)
    yield put(removePhotoFiles(payload.photoGroupId))
    yield put(hideLoadingScreen())
    yield put(getFormatedFiles())
  } catch (err) {
    yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'sendPhotosFailed', translate: true }))
  }
}

export function * fetchFormatedFiles () {
  yield put(showLoadingScreen(modalTypeConstants.LOADING, { message: 'Trwa przeładowanie operatu' }))
  const operat = yield select(makeSelectActiveOperat())
  const requestOptions = {
    method: 'GET',
    headers: yield call(callForHeader)
  }
  const requestUrl = `/OperatFiles/id/${operat.id}/formated`

  try {
    const files = yield call(handleRequestWithTimeout, requestUrl, requestOptions)
    yield put(filesLoaded(files, operat))
    yield put(hideLoadingScreen())
  } catch (err) {
    console.log(err)
    yield put(hideLoadingScreen())
  }
}

export default function * globalData () {
  yield takeLatest(SAVE_INPUT_AREA, saveInputArea)
  yield takeLatest(GENERATE_PROTOCOL, generateProtocol)
  yield takeLatest(GENERATE_OPERAT, generateOperat)
  yield takeLatest(FILES_SEND_PHOTOS, sendPhotos)
  yield takeLatest(SAVE_WEIGHTS_AREA, saveWeightsArea)
  yield takeLatest(FILES_GET_BY_OPERAT, fetchFormatedFiles)
  yield takeLatest(SAVE_CSV_AREA, saveCsvArea)
}
