import React, { useState } from 'react'
import { useMsal } from '@azure/msal-react'
import { history } from '../../helpers'

import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap'
import { useTranslation } from 'react-i18next'

function SettingsItem (props) {
  const [open, setOpen] = useState(false)
  const { instance } = useMsal()
  const { t } = useTranslation('common')

  const LogoutHandler = (instance) => {
    try {
      history.push('/')
      console.log('Trying to logout')
      const logoutResponse = instance.logout()
      console.log(logoutResponse)
    } catch (err) {
      console.log(err)
    }
  }

  const dropdownToggle = (e) => {
    setOpen(!open)
  }

  return (
    <div>
      <Dropdown nav isOpen={open} toggle={(e) => dropdownToggle(e)}>
        <DropdownToggle caret nav>
          <i className='nc-icon nc-settings-gear-65' />
          <p>
            <span className='d-lg-none d-md-block'>Konto</span>
          </p>
        </DropdownToggle>
        <DropdownMenu right>
          <DropdownItem
            tag='a'
            onClick={() => {
              LogoutHandler(instance)
            }}
          >
            {t('settingsHamburgerMenu.logout')}
          </DropdownItem>
        </DropdownMenu>
      </Dropdown>
    </div>
  )
}

export default SettingsItem
