import { callForHeader } from 'utils/HeadersCall'
const apiUrl = process.env.REACT_APP_API_URL

export const operatorService = {
  getOperatorData,
  getOperators,
  promote,
  updateOper,
  getOperatorReviews,
  getOperatorTrainings,
  addTraining,
  deleteTraining
}

async function getOperatorData (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/Operator/id/${id}`, requestOptions).then(handleResponse)
}

async function getOperators () {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }
  return await window.fetch(`${apiUrl}/Operator`, requestOptions).then(handleResponse)
}

async function promote (clientId) {
  const requestOptions = {
    method: 'PUT',
    headers: await callForHeader()
  }

  return await window.fetch(`${apiUrl}/Operator/promote/clientid/${clientId}`, requestOptions).then((response) => handleResponse(response, true))
}

async function updateOper (body) {
  const requestOptions = {
    method: 'PUT',
    headers: await callForHeader('json'),
    body: JSON.stringify(body)
  }

  return await fetch(`${apiUrl}/Operator/by-repo`, requestOptions).then((response) => handleResponse(response))
}

async function getOperatorReviews (operatorId) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader()
  }
  return await fetch(`${apiUrl}/Operator/${operatorId}/review`, requestOptions).then(handleResponse)
}

async function getOperatorTrainings (operatorId) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader()
  }
  return await fetch(`${apiUrl}/Operator/${operatorId}/training`, requestOptions).then(handleResponse)
}

async function addTraining (training) {
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader('json'),
    body: JSON.stringify(training)
  }

  return await fetch(`${apiUrl}/Operator/training`, requestOptions).then(handleResponse)
}

async function deleteTraining (training) {
  const requestOptions = {
    method: 'DELETE',
    headers: await callForHeader('json')
  }
  return await fetch(`${apiUrl}/Operator/training/${training}`, requestOptions).then(handleResponse)
}

function handleResponse (response, promotion = false) {
  return response.text().then(text => {
    const data = text && JSON.parse(text)
    if (!response.ok) {
      if (response.status === 400 && promotion) {
        return response.status
      }

      const error = (data && data.message) || response.statusText
      return Promise.reject(error)
    }

    return data
  })
}
