import React, { useEffect, useState, memo } from 'react'
import { connect, useDispatch } from 'react-redux'
import { compose } from 'redux'
import BootstrapTable from 'react-bootstrap-table-next'
import Pagination from '@mui/material/Pagination'
import { Button, Col, Collapse } from 'reactstrap'
import reducer from './reducer'
import saga from './saga'
import { useInjectReducer } from 'utils/injectReducer'
import { useInjectSaga } from 'utils/injectSaga'
import { useHistory } from 'react-router'
import { createStructuredSelector } from 'reselect'
import { makeSelectCurrentOperator, makeSelectOperatorsInfo } from './selectors'
import { createSubscription, loadOperatorsInfo, setCurrentOperator, setOperatorsInfoFilters, setOperatorsInfoSortParameter, setSubscriptionPageNumber, updateSubscription } from './actions'
import LoadingIndicator from 'components/LoadingIndicator'
import SubscriptionRow from './SubscriptionRow'
import UsersFilters from 'components/Filters/Users'
import { showLoadingScreen } from 'views/Modal/actions'
import { modalTypeConstants } from '../../constants'

const key = 'subscriptions'

function Subscriptions (props) {
  useInjectReducer({ key, reducer })
  useInjectSaga({ key, saga })
  const dispatch = useDispatch()
  const history = useHistory()
  const [areFiltersVisible, setAreFiltersVisible] = useState(false)
  const sortable = {
    onSort: (field, order) => {
      props.handleSetOperatorsInfoSortParameter(field)
    },
    sortFunc: (a, b, order, dataField, rowA, rowB) => {

    },
    sort: true
  }

  const columns = [
    {
      dataField: 'id',
      text: 'ID'
    },
    {
      dataField: 'name',
      text: 'Nazwa',
      ...sortable
    },
    {
      dataField: 'mail',
      text: 'Adres e-mail',
      ...sortable
    },
    {
      dataField: 'nrOfOperats',
      text: 'Stworzone operaty',
      ...sortable,
      formatter: (cellContent, row) => (row.nrOfOperats !== undefined && row.maxNrOfOperats !== undefined)
        ? <p>{`${row.nrOfOperats}/${row.maxNrOfOperats}`}</p>
        : <div />
    },
    {
      dataField: 'nrOfGenerations',
      text: 'Wygenerowane pdfy',
      ...sortable,
      formatter: (cellContent, row) => (row.nrOfGenerations !== undefined && row.maxNrOfGenerations !== undefined)
        ? <p>{`${row.nrOfGenerations}/${row.maxNrOfGenerations}`}</p>
        : <div />
    },
    {
      text: 'Utwórz subskrypcję',
      formatter: (cellContent, row) => <Button
        color='primary' disabled={row.hasSubscription} onClick={() => {
          props.handleSetCurrentOperator(row.id, row.name)
          dispatch(showLoadingScreen(modalTypeConstants.CREATE_SUBSCRIPTION_FORM))
        }}
                                       > Utwórz
                                       </Button>
    }
  ]

  const handlePageChange = (event, newPage) => {
    const search = new URLSearchParams(history.location.search)
    search.set('pageNumber', newPage.toString())
    history.push({
      pathname: '/subscriptions',
      search: search.toString()
    })
    props.handleSetSubscriptionPageNumber(newPage)
  }

  const expandRow = () => {
    return {
      nonExpandable: props.operatorsInfo.data.filter(x => x.hasSubscription === false).map(x => x.id),
      expandColumnPosition: 'right',
      showExpandColumn: true,
      expandByColumnOnly: true,
      expandColumnRenderer: ({ expanded, rowKey, expandable }) => (
        <Button disabled={!expandable}>Edytuj</Button>
      ),
      expandHeaderColumnRenderer: ({ isAnyExpands }) => ('Edytuj'),
      renderer: (row) => {
        const subscription = {
          id: row.id,
          isActive: row.isActive,
          canRenew: row.canRenew,
          nrOfOperats: row.nrOfOperats,
          maxNrOfOperats: row.maxNrOfOperats,
          nrOfGenerations: row.nrOfGenerations,
          maxNrOfGenerations: row.maxNrOfGenerations,
          createdDateTime: row.createdDateTime
        }
        return (<SubscriptionRow subscription={subscription} handleUpdateSubscription={props.handleUpdateSubscription} />)
      }
    }
  }

  useEffect(() => {
    props.handleSetSubscriptionPageNumber('1')
    // eslint-disable-next-line
    }, [])

  useEffect(() => {
    props.handleLoadOperatorsInfo()
    // eslint-disable-next-line
    }, [props.operatorsInfo.pageNumber, props.operatorsInfo.filters, props.operatorsInfo.sortFieldName, props.operatorsInfo.sortDirection])

  return (
    <div className='content'>
      <div className='container'>
        <Col md='12'>
          <Button color='success' onClick={() => setAreFiltersVisible(v => !v)}>Filtry</Button>
          <Collapse isOpen={areFiltersVisible}>
            <UsersFilters handleSetUserFilters={props.handleSetOperatorsInfoFilters} />
          </Collapse>
          <Button color='danger' onClick={() => props.handleResetSubscriptions()}>Resetuj subscrypcje</Button>
          <Pagination count={localStorage.getItem('pageCount')} color='primary' page={(new URLSearchParams(history.location.search)).get('pageNumber') ? parseInt((new URLSearchParams(history.location.search)).get('pageNumber')) : props.operatorsInfo.pageNumber} onChange={handlePageChange} />
          {
                    props.operatorsInfo.loading
                      ? <LoadingIndicator />
                      : <BootstrapTable
                          bootstrap4
                          keyField='id'
                          columns={columns}
                          data={props.operatorsInfo.data}
                          striped
                          hover
                          condensed
                          bordered={false}
                          expandRow={expandRow()}
                        />
                }
        </Col>
      </div>
    </div>
  )
}
const mapStateToProps = createStructuredSelector({
  operatorsInfo: makeSelectOperatorsInfo(),
  currentOperator: makeSelectCurrentOperator()
})

export function mapDispatchToProps (dispatch) {
  return {
    handleLoadOperatorsInfo: () => dispatch(loadOperatorsInfo()),
    handleSetSubscriptionPageNumber: (pageNumber) => dispatch(setSubscriptionPageNumber(pageNumber)),
    handleSetOperatorsInfoSortParameter: fieldName => dispatch(setOperatorsInfoSortParameter(fieldName)),
    handleSetOperatorsInfoFilters: filters => dispatch(setOperatorsInfoFilters(filters)),
    handleSetCurrentOperator: (id, name) => dispatch(setCurrentOperator({ id, name })),
    handleUpdateSubscription: subscription => dispatch(updateSubscription(subscription)),
    handleCreateSubscription: subscription => dispatch(createSubscription(subscription)),
    handleResetSubscriptions: () => dispatch(showLoadingScreen(modalTypeConstants.CONFIRM_RESET_SUBSCRIPTIONS))
  }
}

const withConnect = connect(mapStateToProps, mapDispatchToProps)

export default compose(withConnect, memo)(Subscriptions)
