async function obstructOperat (operatId) {
  const requestOptions = {
    method: 'PUT',
    headers: await callForHeader('json')
  }
  return await fetch(`${apiUrl}/Operat/id/${operatId}/obstructed/trigger`, requestOptions).then(handleResponse)
}

function handleResponse (response) {
  return response.text().then(text => {
    let data = ''
    if (text.startsWith('Data')) {
      data = text
    } else {
      data = text && JSON.parse(text)
    }
    if (!response.ok) {
      if (response.status === 404 || response.status === 500) {
        return []
      }
      return Promise.reject(data || response.statusText)
    }
    const pageCount = response.headers.get('X-Page-Count')
    pageCount && localStorage.setItem('pageCount', pageCount)
    return data
  })
}
