import { Offcanvas, OffcanvasHeader, OffcanvasBody, ListGroup, ListGroupItem, Badge } from 'reactstrap'
import { Launcher } from '@bnbsystems/react-chat-window'
import { memo, useEffect, useState } from 'react'
import { callForHeader } from 'utils/HeadersCall'
import { HubConnectionBuilder, LogLevel } from '@microsoft/signalr'
import { useMsal } from '@azure/msal-react'
import { addNotification } from 'utils/addNotification'
import { compose } from 'redux'
import { v4 as uuidv4 } from 'uuid'
import { connect } from 'react-redux'
import { createStructuredSelector } from 'reselect'
import { makeSelectChatIsOpen, makeSelectConnection, makeSelectConversations, makeSelectCurrentlyChatWith, makeSelectMessages } from './selectors'
import {
  loadConversations, loadMessagesWithUser, markConversationAsRead, markConversationAsUnread,
  markMessagesAsSeen, openChat, setChatIsOpen, setConnection,
  setCurrentlyChatWith, setMessagesWithUser, toggleMessageVisible
} from './actions'
import { toDateString } from 'utils/DateFormat'
import { useInjectReducer } from 'utils/injectReducer'
import { useInjectSaga } from 'utils/injectSaga'
import reducer from './reducer'
import saga from './saga'
import { useTranslation } from 'react-i18next'
const key = 'chat'
export const Chat = (props) => {
  useInjectReducer({ key, reducer })
  useInjectSaga({ key, saga })
  const { accounts } = useMsal()
  const { t } = useTranslation('common')
  const [usersOffcanvasOpen, setUsersOffcanvasOpen] = useState(false)
  const [, setUsers] = useState([])
  const [connection, setConnection] = useState({})
  // eslint-disable-next-line
  useEffect(() => {
    async function fetchData () {
      await joinRoom()
      const requestOptions = {
        method: 'GET',
        headers: await callForHeader('json')
      }
      fetch(process.env.REACT_APP_API_URL + '/Client?pageNumber=1&pageSize=999999', requestOptions).then(res => res.json()).then(res => setUsers(res)).catch(err => { console.log(err) })
    }
    fetchData()
    // eslint-disable-next-line
  }, [])
  async function joinRoom () {
    try {
      const connection = new HubConnectionBuilder()
        .withUrl(process.env.REACT_APP_API_URL + '/chat')
        .configureLogging(LogLevel.Information)
        .build()

      connection.on('ReceiveMessage', (userId, message) => {
        const newMess = {
          date: toDateString(new Date(), t('months', { returnObjects: true })),
          id: uuidv4(),
          author: 'them',
          data: {
            text: message
          },
          type: 'text'
        }
        addNotification('', 'Otrzymałeś nową wiadomość, sprawdź czat.', 'success')
        props.handleSetMessagesWithUser(userId, newMess)
        props.handleMarkConversationAsUnread(userId)
      })

      connection.on('LoadMessages', (userId, messages) => {
        props.handleLoadMessagesWithUser(userId, messages, t('months', { returnObjects: true }))
      })

      connection.on('SawMessages', (userId) => props.handleMarkMessagesAsSeen(userId))
      connection.on('LoadConversations', (conversations) => {
        props.handleLoadConversations(conversations)
      })

      connection.onclose(e => {
        props.handleSetConnection({})
      })

      await connection.start()
      await connection.invoke('JoinRoom', accounts[0].idTokenClaims.oid)
      setConnection(connection)
    } catch (e) {
      console.log(e)
    }
  }

  const sendMessage = async (e) => {
    try {
      await connection.invoke('SendPrivateMessage', props.currentlyChatWith.id, e.data.text ? e.data.text : e.data.emoji)
      e.date = toDateString(new Date(), t('months', { returnObjects: true }))
      e.id = uuidv4()
      e.status = 'sent'
      props.handleSetMessagesWithUser(props.currentlyChatWith.id, e)
    } catch (e) {
      console.log(e)
    }
  }

  // eslint-disable-next-line
    useEffect(async () => {
    if (!props.currentlyChatWith.id) return
    if (props.chatIsOpen === false) return
    try {
      await connection.invoke('GetMessagesBetween2Users', props.currentlyChatWith.id)
      await connection.invoke('SeeMessages', props.currentlyChatWith.id)
    } catch (err) {
      console.log(err)
    }
    // eslint-disable-next-line
    }, [props.chatIsOpen])
  const openChat = async (user) => {
    props.handleOpenChat(user, connection)
    setUsersOffcanvasOpen(false)
  }

  return (
    <>
      <Offcanvas toggle={() => setUsersOffcanvasOpen(prev => !prev)} isOpen={usersOffcanvasOpen}>
        <OffcanvasHeader toggle={() => setUsersOffcanvasOpen(prev => !prev)}>
          {t('chat.conversations')}
        </OffcanvasHeader>
        <OffcanvasBody>
          <ListGroup className='list-group-hover' style={{ cursor: 'pointer' }}>
            {props.conversations && props.conversations.map((e) =>
              <ListGroupItem
                className='justify-content-between'
                style={{ fontWeight: e.unread ? 'bold' : 'normal' }} key={e.client.id} onClick={async () => await openChat(e.client)}
              >{e.client.name + ' '}
                {e.unread && <Badge pill color='primary'>{t('chat.new')}</Badge>}
              </ListGroupItem>)}
          </ListGroup>
        </OffcanvasBody>
      </Offcanvas>
      <Launcher
        isOpen={props.chatIsOpen}
        handleClick={() => { if (props.chatIsOpen) props.handleSetChatIsOpen(false); else setUsersOffcanvasOpen(true) }}
        onMessageWasSent={async e => { await sendMessage(e) }}
        agentProfile={{
          teamName: props.currentlyChatWith.name && props.currentlyChatWith.name
        }}
        newMessagesCount={props.conversations ? props.conversations.filter(x => x.unread).length : 0}
        onMessageClick={(data) => { props.handleToggleMessageVisible(props.currentlyChatWith.id, data.id) }}
        messageList={Object.prototype.hasOwnProperty.call(props.messages, props.currentlyChatWith.id) && props.messages[props.currentlyChatWith.id]}
        showEmoji
      />
    </>
  )
}

const mapStateToProps = createStructuredSelector({
  connection: makeSelectConnection(),
  messages: makeSelectMessages(),
  currentlyChatWith: makeSelectCurrentlyChatWith(),
  conversations: makeSelectConversations(),
  chatIsOpen: makeSelectChatIsOpen()
})

export function mapDispatchToProps (dispatch) {
  return {
    handleLoadMessagesWithUser: (userId, messages, monthNames) => dispatch(loadMessagesWithUser(userId, messages, monthNames)),
    handleSetMessagesWithUser: (userId, message) => dispatch(setMessagesWithUser(userId, message)),
    handleSetConnection: (connection) => dispatch(setConnection(connection)),
    handleSetCurrentlyChatWith: (user) => dispatch(setCurrentlyChatWith(user)),
    handleLoadConversations: (conversations) => dispatch(loadConversations(conversations)),
    handleMarkConversationAsRead: (userId) => dispatch(markConversationAsRead(userId)),
    handleMarkConversationAsUnread: (userId) => dispatch(markConversationAsUnread(userId)),
    handleSetChatIsOpen: (payload) => dispatch(setChatIsOpen(payload)),
    handleToggleMessageVisible: (userId, messageId) => dispatch(toggleMessageVisible(userId, messageId)),
    handleMarkMessagesAsSeen: (userId) => dispatch(markMessagesAsSeen(userId)),
    handleOpenChat: (user, connection) => dispatch(openChat(user, connection))
  }
}

const withConnect = connect(mapStateToProps, mapDispatchToProps)

export default compose(withConnect, memo)(Chat)
