import React, { useCallback } from 'react'
import { useDropzone } from 'react-dropzone'
import { v4 as uuidv4 } from 'uuid'
import { loadFileToGallery } from 'views/Operat/actions'
import { useDispatch, useSelector } from 'react-redux'

function ImageDropzone (props) {
  const baseStyle = {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: '20px',
    borderWidth: 2,
    borderRadius: 20,
    borderColor: '#c2b7b7',
    borderStyle: 'dashed',
    color: '#0e0c0c',
    outline: 'none'
  }

  const style = {
    ...baseStyle
  }
  const dispatch = useDispatch()
  const operatState = useSelector(state => state.operat)
  const onDrop = useCallback(
    (acceptedFiles) => {
      let fileNames = operatState.photo.data[props.id].data.filter(x => !x.file)
      if (fileNames.length === 0) {
        fileNames = operatState.photo.data[props.id].data.slice(0, acceptedFiles.length)
      }
      for (let i = 0; i < fileNames.length; i++) {
        if (i + 1 > acceptedFiles.length) {
          return
        }
        const fileName = fileNames[i].title
        const blob = acceptedFiles[i].slice(0, acceptedFiles[i].size, 'image/png')
        const newFile = new File([blob], fileName, { type: 'image/png' })
        const fileData = {
          id: uuidv4(),
          title: fileName,
          url: URL.createObjectURL(acceptedFiles[i]),
          file: newFile
        }
        props.enableBtn()
        dispatch(loadFileToGallery(props.id, fileData))
      }
    },
    [dispatch, operatState.photo.data, props]
  )

  const { getRootProps, getInputProps } = useDropzone({
    onDrop,
    multiple: props.multiple ? props.multiple : false
  })

  return (
    <section className='container'>
      <div {...getRootProps({ className: 'dropzone', style })}>
        <input {...getInputProps()} />
        <p>Upuść lub kliknij aby wczytac plik zdjęcia.</p>
      </div>
      <aside />
    </section>
  )
}

export default ImageDropzone
