import { createSelector } from 'reselect'
import { initialState } from './reducer'

const selectUsersPage = (state) => state.usersPage || initialState

const makeSelectUserType = () =>
  createSelector(selectUsersPage, (state) => state.userType)

const makeSelectUsers = () =>
  createSelector(selectUsersPage, (state) => state.users)

export {
  selectUsersPage,
  makeSelectUserType,
  makeSelectUsers
}
