import { callForHeader } from '../utils/HeadersCall'
const apiUrl = process.env.REACT_APP_API_URL

export const operatTypeService = {
  getTypeParams
}

async function getTypeParams (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return window.fetch(`${apiUrl}/OperatType/id/${id}/params`, requestOptions).then(handleResponse)
}

function handleResponse (response) {
  return response.text().then(text => {
    let data = ''
    if (text.startsWith('Data')) {
      data = text
    } else {
      if (text.startsWith('"{')) {
        text = text
          .replace(/\\\\n/g, '\\n')
          .replace('"{', '{')
          .replace('}"', '}')
          .replace(/\s+/g, ' ')
          .replace(/\\"/g, '"')
        data = JSON.parse(text)
      } else {
        data = text && JSON.parse(text)
      }
    }
    if (!response.ok) {
      if (response.status === 404) {
        // TODO auto logout if 404 response returned from api
        return []
      }

      const error = (data && data.message) || response.statusText
      return Promise.reject(error)
    }

    return data
  })
}
