import { LOAD_TRAININGS, TRAININGS_LOADED, LOAD_TRAININGS_ERROR, LOAD_OPERATOR_PROFILE_DATA, LOAD_OPERATOR_PROFILE_DATA_ERROR, OPERATOR_PROFILE_DATA_LOADED } from './constants'
import produce from 'immer'

export const initialState = {
  operatorId: '',
  operatorProfileData: {
    loading: false,
    error: false,
    trainings: [],
    operatCount: 0,
    reviews: [],
    diploma: '',
    operator: {}
  },
  trainings: {
    loading: false,
    error: false,
    trainings: []
  }
}

const userReducer = (state = initialState, action) =>
  produce(state, (draft) => {
    // eslint-disable-next-line default-case
    switch (action.type) {
      case LOAD_TRAININGS:
        draft.trainings.trainings = []
        draft.trainings.error = false
        draft.trainings.loading = true
        break
      case TRAININGS_LOADED:
        draft.trainings.trainings = action.payload
        draft.trainings.error = false
        draft.trainings.loading = false
        break
      case LOAD_TRAININGS_ERROR:
        draft.trainings.error = action.payload
        draft.trainings.loading = false
        break
      case LOAD_OPERATOR_PROFILE_DATA:
        draft.operatorProfileData.loading = true
        draft.operatorProfileData.error = false
        draft.operatorProfileData.trainings = []
        draft.operatorProfileData.operatCount = 0
        draft.operatorProfileData.reviews = []
        draft.operatorProfileData.diploma = ''
        draft.operatorProfileData.operator = {}
        break
      case OPERATOR_PROFILE_DATA_LOADED:
        draft.operatorProfileData.loading = false
        draft.operatorProfileData.error = false
        draft.operatorProfileData.trainings = action.payload.trainings
        draft.operatorProfileData.operatCount = action.payload.operatCount
        draft.operatorProfileData.reviews = action.payload.reviews
        draft.operatorProfileData.diploma = action.payload.diploma
        draft.operatorProfileData.operator = action.payload.operator
        break
      case LOAD_OPERATOR_PROFILE_DATA_ERROR:
        draft.operatorProfileData.error = action.payload
        draft.operatorProfileData.loading = false
        break
    }
  })
export default userReducer
