import React, { useState } from 'react'
import { useMsal } from '@azure/msal-react'
import {
  Card,
  CardBody,
  Row,
  Col,
  Accordion,
  AccordionItem,
  AccordionBody,
  AccordionHeader
} from 'reactstrap'
import { useLocation } from 'react-router'
import defaultAvatar from '../../assets/img/default-avatar.png'
import fakeImg from '../../assets/img/damir-bosnjak.jpg'
import ReadonlyTable from 'components/Tables/ReadonlyTable'

function UserProfileTab (props) {
  const { accounts } = useMsal()
  const [openAccordion, setOpenAccordion] = useState('')
  const createInZpbInformation = () => {
    if (!props.operatorProfileData.operator) return null
    const text = 'Operator ' + props.operatorProfileData.operator.name + ' ' + (!props.operatorProfileData.operator.inZbpList ? 'nie ' : '') + 'jest zapisany do listy rzeczoznawców ZBP.'
    return <p>{text}</p>
  }
  const location = useLocation()

  return (
    <>
      <Row>
        <Col md='12'>
          <Card className='card-user'>
            <div className='image'>
              <img alt='...' src={fakeImg} />
            </div>
            <CardBody>
              <div className='author'>
                <a href='/users-profile' onClick={(e) => e.preventDefault()}>
                  <img
                    alt='...'
                    className='avatar border-gray'
                    src={defaultAvatar}
                  />
                  {!location.search
                    ? <div>
                      <h5 className='title'>{/* state.operator.name */}</h5>
                      <h5 className='title'>
                        {accounts[0].idTokenClaims.given_name}{' '}
                        {accounts[0].idTokenClaims.family_name}
                      </h5>
                      <h5 className='title'>{/* state.operator.mail */}</h5>
                    </div>
                    : <h5 className='title'>{props.operatorProfileData.operator && props.operatorProfileData.operator.name}</h5>}
                </a>
                {
                  location.search &&
                    <div>
                      {props.operatorProfileData.operator.description && props.operatorProfileData.operator.description !== '' &&
                        <cite>
                          {props.operatorProfileData.operator.description}
                        </cite>}
                      <Accordion
                        open={openAccordion}
                        toggle={(e) => e === openAccordion ? setOpenAccordion('') : setOpenAccordion(e)}
                      >
                        <AccordionItem>
                          <AccordionHeader targetId='1'>
                            Dyplom
                          </AccordionHeader>
                          <AccordionBody accordionId='1'>
                            {props.operatorProfileData.diploma === '' || typeof props.operatorProfileData.diploma !== 'string' ? 'operator nie wgrał dyplomu do systemu' : <img src={props.operatorProfileData.diploma} alt='dyplom' />}
                          </AccordionBody>
                        </AccordionItem>
                        <AccordionItem>
                          <AccordionHeader targetId='2'>
                            Przebyte szkolenia
                          </AccordionHeader>
                          <AccordionBody accordionId='2'>
                            {props.operatorProfileData.trainings.length > 0
                              ? <ReadonlyTable
                                  headers={['Tytuł', 'Adres url']}
                                  data={props.operatorProfileData.trainings}
                                />
                              : 'Brak danych'}
                          </AccordionBody>
                        </AccordionItem>
                        <AccordionItem>
                          <AccordionHeader targetId='3'>
                            Kontakt
                          </AccordionHeader>
                          <AccordionBody accordionId='3'>
                            <ul style={{ textAlign: 'left', fontSize: '1.5rem' }}>
                              {props.operatorProfileData.operator &&
                                <>
                                  <li>Adres e-mail: <a href={'mailto:' + props.operatorProfileData.operator.mail}>{props.operatorProfileData.operator.mail}</a></li>
                                  <li>Nr telefonu: {props.operatorProfileData.operator.phone}</li>
                                </>}
                            </ul>
                          </AccordionBody>
                        </AccordionItem>
                        <AccordionItem>
                          <AccordionHeader targetId='4'>
                            Opinie zleceniodawców
                          </AccordionHeader>
                          <AccordionBody accordionId='4'>
                            {props.operatorProfileData.reviews.length > 0
                              ? <ReadonlyTable
                                  headers={['Data', 'Zleceniodawca', 'Komentarz', 'Ocena']}
                                  data={props.operatorProfileData.reviews}
                                />
                              : 'Brak danych'}
                          </AccordionBody>
                        </AccordionItem>
                        <AccordionItem>
                          <AccordionHeader targetId='5'>
                            Pozostałe dane
                          </AccordionHeader>
                          <AccordionBody accordionId='5' style={{ textAlign: 'left', fontSize: '1rem' }}>
                            {createInZpbInformation()}
                            Liczba wykonanych operatów: {props.operatorProfileData.operatCount}
                          </AccordionBody>
                        </AccordionItem>
                      </Accordion>
                    </div>
                }
              </div>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </>
  )
}

export default UserProfileTab
