import React, { useState } from 'react'
import {
  Button,
  Form,
  FormGroup, Input, Label
} from 'reactstrap'

export const NewOperatForm = (props) => {
  const [operatType, setOperatType] = useState('')

  return (
    <Form
      id='new-operat-form' onSubmit={(e) => {
        e.preventDefault()
        props.handleCreateOperat()
      }}
    >
      <Input
        type='select' required className='dark-select' id='operat-type-select' value={operatType.id ? operatType.id : ''} onChange={(e) => {
          if (e.target.value === 'CLEAR') {
            setOperatType('')
          } else {
            props.handleSelectOperatType(props.operatTypes.operatTypes.find(x => x.id === e.target.value))
            setOperatType(props.operatTypes.operatTypes.find(x => x.id === e.target.value))
          }
        }} style={{ width: '100%' }} placeholder='wybierz typ operatu'
      >
        <option value='' disabled>Wybierz rodzaj operatu</option>
        {props.operatTypes.operatTypes.map((element) => {
          return (
            <option key={element.id} value={element.id}>{element.name}</option>
          )
        })}
        <option value='CLEAR'>---Kasuj wybór---</option>
      </Input>
      <FormGroup>
        <Label for='operat-name'>
          Nazwa operatu
        </Label>
        <Input type='text' id='operat-name' name='operat-name' />
      </FormGroup>
      <Button type='submit' color='success'>Stwórz operat</Button>
    </Form>
  )
}

export default NewOperatForm
