import { put, takeLatest } from 'redux-saga/effects'
import { markConversationAsRead, setChatIsOpen, setCurrentlyChatWith } from './actions'
import { OPEN_CHAT } from './constants'

export function * openChat (action) {
  yield put(setCurrentlyChatWith(action.user))
  yield put(markConversationAsRead(action.user.id))
  yield put(setChatIsOpen(true))
}

export default function * watch () {
  yield takeLatest(OPEN_CHAT, openChat)
}
