import { callForHeader } from '../utils/HeadersCall'

const apiUrl = process.env.REACT_APP_API_URL

export const clientService = {
  getAdminDataById,
  createNewClient,
  getClientData,
  updateClient
}

async function updateClient (body) {
  const requestOptions = {
    method: 'PUT',
    headers: await callForHeader('json'),
    body: JSON.stringify(body)
  }

  return await fetch(`${apiUrl}/Client/by-repo`, requestOptions).then((response) => handleResponse(response))
}

async function getClientData (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }
  return await fetch(`${apiUrl}/Client/id/${id}`, requestOptions).then(handleResponse)
}

async function createNewClient (newClient) {
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader('json'),
    body: JSON.stringify(newClient)
  }

  return await window.fetch(`${apiUrl}/Client`, requestOptions).then(handleResponse)
}

async function getAdminDataById (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/Client/id/${id}`, requestOptions).then(handleResponse)
}

function handleResponse (response) {
  return response.text().then(text => {
    const data = text && JSON.parse(text)
    if (!response.ok) {
      if (response.status === 400) {
        // TODO auto logout if 400 response returned from api
        return []
      }
      if (response.status === 404) {
        // TODO auto logout if 404 response returned from api
        return false
      }

      const error = (data && data.message) || response.statusText
      return Promise.reject(error)
    }
    return data
  })
}
