export const validators = {
  validatePositiveNumbers
}

function validatePositiveNumbers (evt, regex) {
  if (evt == null || regex == null) {
    return
  }
  const theEvent = evt || window.event
  let key = theEvent.keyCode || theEvent.which
  key = String.fromCharCode(key)
  let val = theEvent.target.value + key
  if (theEvent.type === 'paste') {
    val = window.event.clipboardData.getData('text/plain')
  }

  if (regex.test(val)) {
    theEvent.target.classList.remove('validate')
  } else {
    const tcl = theEvent.target.classList
    tcl.add('validate')
    setTimeout(function () {
      tcl.remove('validate')
    }, 400)
    theEvent.returnValue = false
    if (theEvent.preventDefault) {
      theEvent.preventDefault()
    }
  }
};
