import React, { useState } from 'react'
import { useMsal } from '@azure/msal-react'
import { certificateService } from '../../services/certificate.service'
import { Button, Form, FormGroup, Label, Input } from 'reactstrap'
import { modalTypeConstants } from '../../constants'
import { useDispatch } from 'react-redux'
import { hideLoadingScreen, showLoadingScreen } from './actions'

function NewOrderForm (props) {
  const { accounts } = useMsal()
  const dispatch = useDispatch()
  const [certificatePassword, setCertificatePassword] = useState('')
  const [repeatPassword, setRepeatPassword] = useState('')

  const addCertificate = (e) => {
    e.preventDefault()
    const firstName = accounts[0].idTokenClaims.given_name ?? ''
    const lastName = accounts[0].idTokenClaims.family_name ?? ''
    const nameLastName = firstName + ' ' + lastName

    const dto = {
      operatorId: accounts[0].idTokenClaims.oid,
      nameLastName: nameLastName,
      certificatePassword: certificatePassword,
      certificateSignDate: new Date()
    }
    dispatch(showLoadingScreen(modalTypeConstants.LOADING, { message: 'generateCertificate', translate: true }))
    certificateService.generateCertificate(dto).then(() => {
      dispatch(hideLoadingScreen())
    }).catch((error) => dispatch(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: error })))
  }

  return (
    <div>
      <Form
        onSubmit={(e) => addCertificate(e)}
      >
        <FormGroup>
          <Label for='certificatePassword'>Hasło do certyfikatu</Label>
          <Input
            type='password'
            name='certificatePassword'
            id='certificatePassword'
            value={certificatePassword}
            onChange={(e) => {
              setCertificatePassword(e.target.value)
            }}
            required
          />
        </FormGroup>
        <FormGroup>
          <Label for='certificatePassword'>Powtórz hasło</Label>
          <Input
            type='password'
            name='repeatPassword'
            id='repeatPassword'
            value={repeatPassword}
            onChange={(e) => {
              setRepeatPassword(e.target.value)
            }}
            required
          />
        </FormGroup>
        <Button
          disabled={certificatePassword.length === 0 || certificatePassword !== repeatPassword}
          style={{ width: '100%' }}
          color='success'
        >
          Wygeneruj certyfikat
        </Button>
      </Form>
    </div>
  )
}

export default NewOrderForm
