
import History from '../views/History/History'
import Market from '../views/Market/Market'
import Create from '../views/Operat/Create'
import Users from '../views/User/Users'
import Logout from 'views/Logout'
import Home from 'views/Home/Home'

export function operatorRoutes (t) {
  return [
    {
      path: '/market',
      name: t('market.route.marketplace'),
      icon: 'nc-icon nc-cart-simple',
      component: Market
    },
    {
      path: '/history',
      name: t('history.history'),
      icon: 'fa fa-file-signature',
      component: History
    },
    {
      path: '/create',
      component: Create
    },
    {
      path: '/users-profile',
      name: t('userProfile.userProfile'),
      icon: 'nc-icon nc-single-02',
      component: Users
    },
    {
      path: '/logout',
      component: Logout
    },
    {
      path: 'http://feedback.aoperat.pl/',
      name: t('externalUrls.feedback'),
      icon: 'fa fa-brain',
      external: true
    },
    {
      path: '/',
      component: Home
    }
  ]
}
