import React, { useState, memo, useEffect } from 'react'

import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink
} from 'reactstrap'
import UserFilesTab from './UserFilesTab'
import EditUser from './EditUser'
import UserProfileTab from './UserProfileTab'
import { useLocation } from 'react-router'
import classnames from 'classnames'
import OperatorTrainingsTab from './OperatorTrainingsTab'
import { userRolesConstants } from '../../constants'
import { addTraining, loadOperatorProfileData, loadTrainings, removeTraining } from './actions'
import { createStructuredSelector } from 'reselect'
import { makeSelectOperatorProfileData, makeSelectTrainings } from './selectors'
import { connect, useSelector } from 'react-redux'
import { compose } from 'redux'
import { useInjectReducer } from 'utils/injectReducer'
import reducer from './reducer'
import saga from './saga'
import { useInjectSaga } from 'utils/injectSaga'
import { useTranslation } from 'react-i18next'
const key = 'user'
function Users (props) {
  useInjectReducer({ key, reducer })
  useInjectSaga({ key, saga })
  const { t } = useTranslation('common')
  const user = useSelector(state => state.global.user)
  const [activeTab, setActiveTab] = useState('1')
  const toggle = (tab) => {
    if (activeTab !== tab) setActiveTab(tab)
  }
  const location = useLocation()

  useEffect(() => {
    if (location.search) {
      const params = new URLSearchParams(location.search)
      const operatorId = params.get('operatorId')
      props.handleLoadOperatorProfileData(operatorId)
    }
  // eslint-disable-next-line
  }, [location.search])

  const isClient = user.userType === userRolesConstants.CLIENT
  const createNavItem = (tabNr, label) =>
    <NavItem>
      <NavLink
        className={classnames({ active: activeTab === tabNr })}
        onClick={() => {
          toggle(tabNr)
        }}
      >
        {label}
      </NavLink>
    </NavItem>
  return (
    <>
      <div className='content'>
        <Nav tabs>
          {createNavItem('1', t('userProfile.userProfile'))}
          {!location.search &&
            <>
              {createNavItem('2', t('userProfile.profileEdition'))}
              {!isClient && createNavItem('3', t('userProfile.files'))}
              {!isClient && createNavItem('4', t('userProfile.trainings'))}
            </>}
        </Nav>
        <TabContent activeTab={activeTab}>
          <TabPane tabId='1'>
            <UserProfileTab
              handleLoadOperatorProfileData={props.handleLoadOperatorProfileData}
              operatorProfileData={props.operatorProfileData}
            />
          </TabPane>
          <TabPane tabId='2'>
            <EditUser />
          </TabPane>
          {!isClient &&
            <>
              <TabPane tabId='3'>
                <UserFilesTab />
              </TabPane>
              <TabPane tabId='4'>
                <OperatorTrainingsTab
                  handleLoadTrainings={props.handleLoadTrainings}
                  handleLoadOperatorProfileData={props.handleLoadOperatorProfileData}
                  operatorProfileData={props.operatorProfileData}
                  trainings={props.trainings}
                  handleAddTraining={props.handleAddTraining}
                  handleRemoveTraining={props.handleRemoveTraining}
                />
              </TabPane>
            </>}
        </TabContent>
      </div>
    </>
  )
}

const mapStateToProps = createStructuredSelector({
  trainings: makeSelectTrainings(),
  operatorProfileData: makeSelectOperatorProfileData()
})

export function mapDispatchToProps (dispatch) {
  return {
    handleLoadTrainings: () => dispatch(loadTrainings()),
    handleLoadOperatorProfileData: (operatorId) => dispatch(loadOperatorProfileData(operatorId)),
    handleAddTraining: () => dispatch(addTraining()),
    handleRemoveTraining: (id) => dispatch(removeTraining(id))
  }
}
const withConnect = connect(mapStateToProps, mapDispatchToProps)

export default compose(withConnect, memo)(Users)
