import React, { useState } from 'react'
import {
  Card,
  CardBody,
  Row,
  Col, Button, Collapse
} from 'reactstrap'

import UserImageDropzone from './UserImageDropzone'
import UserUploadedFiles from './UserUploadedFiles'
import { useTranslation } from 'react-i18next'

function UserProfileTab (props) {
  const { t } = useTranslation('common')
  const [sendNew, setSendNew] = useState(false)
  const [showSendStamp, setShowSendStamp] = useState(false)
  const [showSendDiploma, setShowSendDiploma] = useState(false)
  const [showSendOc, setShowSendOc] = useState(false)
  const [showFiles, setShowFiles] = useState(false)

  return (
    <>
      <Row>
        <Col md='8'>
          <Card>
            <CardBody>
              <Button color='primary' onClick={() => setSendNew(!sendNew)}>
                {t('userProfile.buttons.sendFiles')}
              </Button>
              <Collapse isOpen={sendNew}>
                <Card>
                  <CardBody>
                    <UserImageDropzone acceptedExt='.pdf, .pem' fileType='pdf' maxLength={100} />
                  </CardBody>
                </Card>
              </Collapse>
              <Button color='primary' onClick={() => setShowSendStamp(!showSendStamp)}>
                {t('userProfile.buttons.sendStamp')}
              </Button>
              <Collapse isOpen={showSendStamp}>
                <Card>
                  <CardBody>
                    <UserImageDropzone acceptedExt='.png, .jpg' fileType='stamp' maxLength={1} />
                  </CardBody>
                </Card>
              </Collapse>
              <Button color='primary' onClick={() => setShowSendDiploma(!showSendDiploma)}>
                {t('userProfile.buttons.sendDiploma')}
              </Button>
              <Collapse isOpen={showSendDiploma}>
                <Card>
                  <CardBody>
                    <UserImageDropzone acceptedExt='.pdf' fileType='diploma' maxLength={1} />
                  </CardBody>
                </Card>
              </Collapse>
              <Button color='primary' onClick={() => setShowSendOc(!showSendOc)}>
                {t('userProfile.buttons.sendLiabilityInsurance')}
              </Button>
              <Collapse isOpen={showSendOc}>
                <Card>
                  <CardBody>
                    <UserImageDropzone acceptedExt='.pdf' fileType='oc' maxLength={1} />
                  </CardBody>
                </Card>
              </Collapse>
              <Button color='primary' id='showFiles' onClick={() => setShowFiles(!showFiles)}>
                {t('userProfile.buttons.showUploadedFiles')}
              </Button>
              <Collapse toggler='#showFiles' isOpen={showFiles}>
                <Card>
                  <CardBody>
                    {showFiles && <UserUploadedFiles />}
                  </CardBody>
                </Card>
              </Collapse>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </>
  )
}

export default UserProfileTab
