import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import {
  Card,
  CardBody,
  Button,
  Collapse
} from 'reactstrap'

export function InfoForAdmin () {
  const [isInfoVisible, setIsInfoVisible] = useState(false)
  const operatState = useSelector(state => state.operat)
  return (
    <>
      <Button style={{ width: '16rem' }} color='info' onClick={() => setIsInfoVisible(!isInfoVisible)}>Informacje</Button>
      <Collapse isOpen={isInfoVisible}>
        <Card>
          <CardBody>
            {operatState.operatInfo && Object.keys(operatState.operatInfo).map(x => operatState.operatInfo[x] && (<p>{x + ': ' + operatState.operatInfo[x]}</p>))}
          </CardBody>
        </Card>
      </Collapse>
    </>
  )
}
