import React, { useState } from 'react'
import {
  Card,
  CardBody,
  Input,
  Button,
  Form
} from 'reactstrap'

export const UsersFilters = ({ handleSetUserFilters }) => {
  const [username, setUsername] = useState('')
  const [email, setEmail] = useState('')
  const handleClear = () => {
    setUsername('')
    setEmail('')
    handleSetUserFilters({ name: '', mail: '' })
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    handleSetUserFilters({ name: username, mail: email })
  }
  return (
    <Card>
      <CardBody>
        <Form onSubmit={handleSubmit}>
          <Input
            placeholder='Nazwa użytkownika' id='filter-username' name='filter-username' onChange={(e) => {
              setUsername(e.target.value)
            }} value={username}
          />
          <Input
            placeholder='Email użytkownika' id='filter-user-email' name='filter-user-email' onChange={(e) => {
              setEmail(e.target.value)
            }} value={email}
          />
          <Button onClick={handleClear}>Wyczyść</Button>
          <Button type='submit' color='success'>Filtruj</Button>
        </Form>
      </CardBody>
    </Card>
  )
}

export default UsersFilters
