import React, { useEffect, useState } from 'react'
import BootstrapTable from 'react-bootstrap-table-next'
import cellEditFactory from 'react-bootstrap-table2-editor'
import ToolkitProvider from 'react-bootstrap-table2-toolkit'
import { useDispatch, useSelector } from 'react-redux'
import { Button, Card, CardBody, Col, FormGroup, Input, Row, UncontrolledTooltip } from 'reactstrap'
import { showLoadingScreen } from 'views/Modal/actions'
import { modalTypeConstants } from '../../constants'
import { loadOperatWeights, loadOperatInitialWeights, saveWeightsArea, updateWeights, addNewWeight, updateRatings, addNewRate, removeWeights, removeRatings } from './actions'
import { fixPercentValue } from 'utils/fixPercentValue'
import { useTranslation } from 'react-i18next'

function EditComponent (props) {
  const [value, setValue] = useState(props.value)
  

  return ([
    <Input
      value={value}
      type={props.type}
      min={props.min}
      max={props.max}
      onChange={(e) => {
        props.type === 'number' ? setValue(fixPercentValue(e.target.value)) : setValue(e.target.value)
      }}
      onBlur={() => { props.type === 'number' ? props.onUpdate(parseInt(value)) : props.onUpdate(value) }}
    />
  ])
}
function CsvComponent ({ operatTypeParams, operat }) {
  const [selectedWeight, selectWeight] = useState(null)
  const [selectedWeightIndex, setRowIndex] = useState(0)
  const dispatch = useDispatch()
  const operatState = useSelector(state => state.operat)
  const { t } = useTranslation('common')
  const innerCols = [
    {
      dataField: 'id',
      hidden: true
    },
    {
      dataField: 'rate',
      text: t('create.weights.ratingTableHeaders.value'),
      align: 'center',
      headerAlign: 'center',
      sort: true,
      formatter: (cell, row, rowIndex, formatExtraData) => (
        <>
        <p id={`weight-value-${rowIndex}`}>{cell}</p>
        <UncontrolledTooltip
          placement='top'
          target={`weight-value-${rowIndex}`}
        >
          Najmniejsza wartość to najmniej korzystna ocena
        </UncontrolledTooltip>
        </>
      )
    },
    {
      dataField: 'rateText',
      text: t('create.weights.ratingTableHeaders.rating'),
      align: 'center',
      headerAlign: 'center',
      editorRenderer: (editorProps, value, row, column, rowIndex, columnIndex) => (
        <EditComponent {...editorProps} value={value} type='text' onUpdate={e => dispatch(updateRatings(selectedWeightIndex, row.rate, 'rateText', e))} />
      ),
      sort: true
    },
    {
      dataField: 'description',
      text: t('create.weights.ratingTableHeaders.description'),
      align: 'center',
      headerAlign: 'center',
      sort: true,
      editable: true,
      editorRenderer: (editorProps, value, row, column, rowIndex, columnIndex) => (
        <EditComponent {...editorProps} value={value} type='text' onUpdate={e => dispatch(updateRatings(selectedWeightIndex, row.rate, 'description', e))} />
      )
    },
    {
      editable: false,
      text: t('create.weights.ratingTableHeaders.selectRating'),
      align: 'center',
      headerAlign: 'center',
      sort: true,
      formatter: (cellContent, row) => (<Button
        color='info' onClick={e => {
          dispatch(updateWeights(selectedWeight.name, 'rate', row.rate))
        }}
                                        >Wybierz
      </Button>)
    }
  ]
  const [selectedRows, setSelectedRows] = useState([])
  const [selectedInnerRows, setSelectedInnerRows] = useState([])
  const selectRow = (onSelect, onSelectAll) => {
    const select = {
      mode: 'checkbox',
      clickToEdit: false,
      clickToSelect: false,
      clickToExpand: true,
      onSelect,
      onSelectAll
    }
    return select
  }
  const cols = [
    {
      dataField: 'name',
      text: t('create.weights.weightTableHeaders.id'),
      hidden: false
    },
    {
      dataField: 'description',
      text: t('create.weights.weightTableHeaders.description'),
      editable: true,
      align: 'center',
      headerAlign: 'center',
      sort: true,
      editorRenderer: (editorProps, value, row, column, rowIndex, columnIndex) => (
        <EditComponent {...editorProps} value={value} type='text' onUpdate={e => dispatch(updateWeights(row.name, 'description', e))} />
      )
    },
    {
      dataField: 'justification',
      hidden: true
    },
    {
      dataField: 'participation',
      text: t('create.weights.weightTableHeaders.participation'),
      editable: true,
      align: 'center',
      headerAlign: 'center',
      sort: true,
      editorRenderer: (editorProps, value, row, column, rowIndex, columnIndex) => (
        <EditComponent {...editorProps} value={value} type='number' min={1} max={100} onUpdate={e => dispatch(updateWeights(row.name, 'participation', e))} />
      )
    },
    {
      dateField: 'rate',
      text: t('create.weights.weightTableHeaders.rating'),
      editable: false,
      align: 'center',
      headerAlign: 'center',
      formatter: (cellContent, row) => {
        if (row.ratings.length === 0) {
          return ''
        }
        const rate = row.ratings.find(x => x.rate === row.rate)
        if (rate) {
          return rate.rateText
        } else {
          return ''
        }
      }
    }
  ]

  useEffect(() => {
    const load = () => {
      dispatch(showLoadingScreen(
        modalTypeConstants.LOADING,
        {}
      ))
      loadOperatWeights(dispatch, dataJson.item2)
    }

    const dataJson = operatState.files.weights
    !!dataJson && (Object.keys(dataJson).length > 0 ? load() : dispatch(loadOperatInitialWeights(operatTypeParams.weights)))
    // eslint-disable-next-line
  }, [operatState.files.weights])

  const cellEdit = cellEditFactory({
    mode: 'click',
    blurToSave: true
  })

  const handleTableChange = (type, { data, cellEdit: { rowId, dataField, newValue } }) => {
    dispatch(updateWeights(rowId, dataField, newValue))
  }
  const expandRow = {
    renderer: row => (
      <>
        <Button color='success' onClick={() => dispatch(addNewRate(selectedWeightIndex))}>Dodaj nową ocenę</Button>
        <Button
          color='info'
          onClick={() => {
            dispatch(removeRatings(selectedInnerRows, selectedWeightIndex))
            setSelectedInnerRows([])
          }}
        >
          Usuń ocenę
        </Button>
        <FormGroup>
          <BootstrapTable
            keyField='id'
            remote={{ cellEdit: true }}
            data={row.ratings}
            columns={innerCols}
            selectRow={selectRow((row, isSelect, rowIndex, e) => {
              isSelect && setSelectedInnerRows((allRows) => [...allRows, row.id])
              !isSelect &&
                setSelectedInnerRows(selectedInnerRows.filter((item) => item !== row.id))
            }, (isSelect, rows) => {
              !isSelect && setSelectedInnerRows([])
              isSelect &&
                setSelectedInnerRows(
                  rows.map((row) => {
                    return row.name
                  })
                )
            }
            )}
            tabIndexCell
            bootstrap4
            striped
            hover
            condensed
            bordered={false}
            cellEdit={cellEdit}
            onTableChange={handleTableChange}
          />
        </FormGroup>
        Uzasadnienie wyboru oceny
        <Input
          type='textarea'
          value={row.justification}
          onChange={(e) => dispatch(updateWeights(row.name, 'justification', e.target.value))}
        />
      </>
    ),
    onlyOneExpanding: true,
    onExpand: (row, isExpand, rowIndex, e) => { selectWeight(row); setRowIndex(rowIndex) }
  }
  return (
    <>
      <Card>
        <CardBody>
          <br />
          <br />
          <br />
          <br />
          <div className='container'>
            <Row>
              <Col md='12'>
                <Card>
                  {operatState.weights && <ToolkitProvider
                    keyField='name'
                    data={operatState.weights}
                    columns={cols}
                    exportCSV={{ onlyExportSelection: true, exportAll: true }}
                                          >
                    {(props) => (
                      <div>
                        <Button
                          color='success'
                          onClick={() => {
                            dispatch(saveWeightsArea(operatState.id))
                          }}
                        >
                          Wyślij dane
                        </Button>
                        <Button
                          color='info'
                          onClick={() => {
                            dispatch(removeWeights(selectedRows))
                            setSelectedRows([])
                          }}
                        >
                          Usuń cechę nieruchomości
                        </Button>
                        <Button
                          color='info'
                          id='copyWeights'
                          onClick={() => {
                            dispatch(showLoadingScreen(
                              modalTypeConstants.OPERATS_LIST_MODAL,
                              { operat, dataType: 'weights' }
                            ))
                          }}
                        >
                          kopiuj cechy nieruchomości z innego operatu
                        </Button>
                        <Button
                          color='info'
                          onClick={() => {
                            dispatch(addNewWeight())
                          }}
                        >
                          Dodaj nową cechę nieruchomości
                        </Button>
                        <hr />
                        <BootstrapTable
                          {...props.baseProps}
                          remote={{ cellEdit: true }}
                          tabIndexCell
                          bootstrap4
                          striped
                          hover
                          selectRow={selectRow((row, isSelect, rowIndex, e) => {
                            isSelect && setSelectedRows((allRows) => [...allRows, row.name])
                            !isSelect &&
                              setSelectedRows(selectedRows.filter((item) => item !== row.name))
                          }, (isSelect, rows) => {
                            !isSelect && setSelectedRows([])
                            isSelect &&
                              setSelectedRows(
                                rows.map((row) => {
                                  return row.name
                                })
                              )
                          }
                          )}
                          condensed
                          bordered={false}
                          cellEdit={cellEdit}
                          expandRow={expandRow}
                          onTableChange={handleTableChange}
                        />
                      </div>
                    )}
                  </ToolkitProvider>}
                </Card>
              </Col>
            </Row>
          </div>
        </CardBody>
      </Card>
    </>
  )
}

export default CsvComponent
