import React, { useState } from "react";
import { useSelector } from "react-redux";
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
} from "reactstrap";

function ImageCarousel (props) {
  const [activeIndex, setActiveIndex] = useState(props.currentIndex)
  const [animating, setAnimating] = useState(false)
  const operatState = useSelector(state => state.operat)
  console.log(operatState)
  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === props.images.length - 1 ? 0 : activeIndex + 1
    setActiveIndex(nextIndex)
  }

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? props.images.length - 1 : activeIndex - 1
    setActiveIndex(nextIndex)
  }

  const goToIndex = (newIndex) => {
    if (animating) return;
    setActiveIndex(newIndex)
  }

  const slides = props.images.map(image => {
    return (
      <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={image.id}
      >
        <div className="d-flex justify-content-center">
          <img src={image.url?.includes('blob.core.windows.net') ?`${image.url}&date=${operatState.randomString}` : image.url} alt={image.title} className="img-fluid" />
        </div>
      </CarouselItem>
    )
  })

  return (
    <Carousel
      activeIndex={activeIndex}
      next={next}
      previous={previous}
    >
      <CarouselIndicators
        items={props.images}
        activeIndex={activeIndex}
        onClickHandler={goToIndex}
      />
      {slides}
      <CarouselControl
        direction="prev"
        directionText="Previous"
        onClickHandler={previous}
      />
      <CarouselControl
        direction="next"
        directionText="Next"
        onClickHandler={next}
      />
    </Carousel>
  );
}

export default ImageCarousel;
