import React, { useCallback, useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { v4 as uuidv4 } from 'uuid'
import {
  Button, Card,
  CardBody, Col, Row, UncontrolledCollapse
} from 'reactstrap'
import { modalTypeConstants } from '../../constants'
import { mapService, operatFilesService } from '../../services'
import ImageDropzone from '../Common/ImageDropzone'
import ReactstrapImageGallery from './ReactstrapImageGallery'
import { saveAs } from 'file-saver'
import { useDispatch, useSelector } from 'react-redux'
import { showLoadingScreen, hideLoadingScreen } from 'views/Modal/actions'
import { loadFileToGallery, loadOperatEmptyPhoto, removePhotos, sendPhotos } from './actions'
function PhotoArea (props) {
  const dispatch = useDispatch()
  const operatState = useSelector(state => state.operat)
  const operat = useSelector(state => state.operat)
  const { t } = useTranslation('common')
  const [disabledBtns, setDisableBtns] = useState([])

  const mapPhotos = useCallback((photo) => {
    const result = props.operatTypeParams.photo.map(x => ({ ...x }))
    for (let i = 0; i < result.length; i++) {
      result[i].data = result[i].data.map(x => {
        const finded = photo.find(p => p.item1 === x.title)
        if (!finded) {
          return x
        } else {
          return { ...x, url: finded.item2 }
        }
      })
    }
    return { data: result }
  }, [props.operatTypeParams])

  useEffect(() => {
    const photo = operatState.files.photo.map(x => ({ ...x }))
    !!photo && photo.length > 0
      ? dispatch(loadOperatEmptyPhoto(mapPhotos(photo)))
      : dispatch(loadOperatEmptyPhoto(
        { data: props.operatTypeParams.photo }
      ))
  }, [dispatch, operatState.files, props.operatTypeParams, mapPhotos])

  const handleSendPhotos = (id) => {
    setDisableBtns(prev => [...prev, id])
    dispatch(sendPhotos(operatState.id, operatState.photo.data[id].data, id))
  }

  const isSendPhotosButtonDisabled = (id) => {
    return !operatState.photo.data[id].data.every((item) =>
      item.hasOwnProperty('url') || item.hasOwnProperty('file')
    ) ||
      !operatState.photo.data[id].data.some(x => x.hasOwnProperty('file')) ||
      disabledBtns.includes(id)
  }

  const handlePhotosDelete = async (id) => {
    setDisableBtns(prev => [...prev, id])
    dispatch(showLoadingScreen(
      modalTypeConstants.LOADING,
      { message: 'deleteFile', translate: true }
    ))

    operatState.photo.data[id].data.map(async x => await operatFilesService.removeSingleFile(operatState.id, x.title).catch(err => console.log(err)))
    dispatch(removePhotos(id))
    dispatch(hideLoadingScreen())
  }
  return (
    <Card>
      <CardBody>
        <Button
          color='secondary'
          id='copyInputArea'
          onClick={() => {
            dispatch(showLoadingScreen(
              modalTypeConstants.OPERATS_LIST_MODAL,
              { operat: props.operat, history: props.history, dataType: 'photos' }
            ))
          }}
          style={{ marginBottom: '1rem' }}
        >
          kopiuj zdjęcia z innego operatu
        </Button>
        {!!operatState.photo.data &&
          operatState.photo.data.map((item, id) => {
            return (
              <div key={item.name + 'Area' + id}>
                <Card style={{ boxShadow: '6px 6px 10px 2px rgb(0 0 0 / 15%)' }}>
                  <CardBody>
                    <Button
                      color='info'
                      id={item.name + 'Area' + id}
                      style={{ marginBottom: '1rem', width: '14rem' }}
                    >
                      {t(item.text)}
                    </Button>
                    <Button
                      color='success'
                      id={item.name + 'send' + id}
                      style={{ marginBottom: '1rem', width: '17rem' }}
                      onClick={() => { handleSendPhotos(id) }}
                      disabled={isSendPhotosButtonDisabled(id)}
                    >
                      {`wyślij ${item.text}`}
                    </Button>
                    <Button
                      color='danger'
                      id={item.name + 'send' + id}
                      style={{ marginBottom: '1rem', width: '16rem' }}
                      onClick={() => {
                        handlePhotosDelete(id)
                      }}
                      disabled={
                        !operatState.photo.data[id].data.every((item) =>
                          item.hasOwnProperty('url')
                        )
                      }
                    >
                      {`usuń ${item.text}`}
                    </Button>
                    {item.name === 'zdjecia_okolica' && <Button
                      style={{ marginBottom: '1rem', width: '14rem' }} onClick={() => {
                        let address = props.order.location
                        if (!address && address !== '') {
                          try {
                            address = operat.input.find(x => x.name === 'adres').data.find(x => x.name === 'full').value
                          } catch (ex) {
                            console.log(ex)
                          }
                        }
                        mapService.get(address, 'GoogleRoadmap').then(
                          (img) => {
                            let fileNames = operatState.photo.data[id].data.filter(x => !x.file)
                            if (fileNames.length === 0) {
                              fileNames = operatState.photo.data[id].data.slice(0, 1)
                            }
                            const fileName = fileNames[0].title
                            const blob = img.slice(0, img.size, 'image/png')
                            const newFile = new File([blob], fileName, { type: 'image/png' })
                            const fileData = {
                              id: uuidv4(),
                              title: fileName,
                              url: URL.createObjectURL(img),
                              file: newFile
                            }
                            dispatch(loadFileToGallery(id, fileData))
                            saveAs(img, 'file.png')
                          }
                        )
                      }}
                                                        >{t('create.buttons.googleMaps')}
                    </Button>}
                    <UncontrolledCollapse toggler={item.name + 'Area' + id}>
                      <br />
                      <Row>
                        <Col sm='12' md={{ size: 6, offset: 3 }}>
                          <ImageDropzone id={id} multiple enableBtn={() => setDisableBtns(prev => prev.filter(x => x !== id))} fileNames={item.data.map(x => x.title)} />
                        </Col>
                      </Row>
                      <br />
                      <ReactstrapImageGallery images={item.data} limit={4} />
                    </UncontrolledCollapse>
                    <p>{item.description}</p>
                  </CardBody>
                </Card>
              </div>
            )
          })}
      </CardBody>
    </Card>
  )
}

export default PhotoArea
