import LoadingIndicator from 'components/LoadingIndicator'
import React, { useEffect, useState, useRef } from 'react'
import { useTranslation } from 'react-i18next'
import { useDispatch } from 'react-redux'
import { useHistory } from 'react-router'
import { Button, Col, Row, UncontrolledTooltip } from 'reactstrap'
import { showConfirmBox } from 'utils/showConfirmBox'
import { hideLoadingScreen, showLoadingScreen } from 'views/Modal/actions'
import { modalTypeConstants } from '../../constants'
import { operatFilesService, operatService } from '../../services'
function HistoryRow (props) {
  const { t } = useTranslation('common')
  const dispatch = useDispatch()
  const [operat, setOperat] = useState(null)
  const history = useHistory()
  const inputFile = useRef(null)
  const inputFileSigned = useRef(null)
  useEffect(() => {
    setOperat(props.row)
    props.handleLoadOperatOrder()
    // eslint-disable-next-line
  }, [])

  const downloadOperat = () => {
    dispatch(showLoadingScreen(modalTypeConstants.LOADING, { message: 'downloadOperat', translate: true }))
    operatFilesService.getFilesByOperat(operat.id).then(response => {
      for (const file of response) {
        if (file.item2.includes('signed.pdf') || file.item2.includes('unsigned.pdf')) {
          dispatch(hideLoadingScreen())
          let childWindow = window.open(file.item2, '_blank');
          childWindow.location.reload(); 
          return
        }
      }
      dispatch(hideLoadingScreen())
      dispatch(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'downloadOperatFailed', translate: true }))
    }).catch(err => {dispatch(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'downloadOperatFailed', translate: true }))})
  }

  const signPdf = () => {
    dispatch(showLoadingScreen(modalTypeConstants.SIGN_OPERAT))
  }

  const redirectToOperatPage = (id) => {
    const search = new URLSearchParams()
    search.set('id', id || operat.id)
    history.push({ pathname: '/create', search: search.toString() })
  }

  return (
    <div>
      {props.operatOrder.loading
        ? <LoadingIndicator />
        : <div>
          <Row>
            <Col>
              <Button
                color='info'
                type='submit'
                onClick={() => {
                  redirectToOperatPage()
                }}
                disabled={operat && operat.isOwnGenerated}
                style={{ width: '100%' }}
              >
                {t('history.table.buttons.showOperat')}
              </Button>
            </Col>
            <Col>
              <Button
                color='info' type='submit' onClick={() => {
                  operatService.copyOperat(operat.id).then(result => {
                    props.handleLoadOperats()
                    props.handleSetActiveOperat(result)
                    redirectToOperatPage(result.id)
                  }).catch(err => {
                    if (typeof (err) === 'object' && err.hasOwnProperty('status') && err.status === 402) {
                      dispatch(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'unactiveSubscription', translate: true }))
                    } else {
                      dispatch(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'operatCopyFailed', translate: true }))
                    }
                  })
                }}
                disabled={operat && operat.isOwnGenerated}
                style={{ width: '100%' }}
              >
                {t('history.table.buttons.copy')}
              </Button>
            </Col>
            <Col>
              <Button
                color='info' type='file' onClick={() => inputFile.current.click()} disabled={(props.operatOrder.operatOrder.shouldBeValidated && props.operatOrder.operatOrder.shouldBeValidated) || (operat && operat.status.toLowerCase() === 'signed')}
                id='upload-own-generated-btn'
                style={{ width: '100%' }}
              >
                {t('history.table.buttons.upload')}
              </Button>
            </Col>
            <UncontrolledTooltip
              placement='top'
              target='upload-own-generated-btn'
            >
              Przycisk służy do wgrywania własnego operatu, wygenerowanego poza platformą Aoperat.
            </UncontrolledTooltip>
            <input
              type='file'
              id='file-upload-own-operat'
              ref={inputFile}
              style={{ display: 'none' }}
              accept='.pdf'
              onChange={(event) => props.handleUploadOwnGeneratedOperat(operat.id, event.currentTarget.files[0])}
            />
            <Col>
              <Button
                disabled={operat && !['generated', 'signed'].includes(operat.status.toLowerCase())}
                color='info' onClick={() => {
                  downloadOperat()
                }}
                style={{ width: '100%' }}
              >
                {t('history.table.buttons.download')}
              </Button>
            </Col>
          </Row>
          <br />
          <Row>
            <Col>
              <Button
                disabled={operat && operat.status.toLowerCase() !== 'generated'} color='info'
                onClick={() => { dispatch(showLoadingScreen(modalTypeConstants.ATTACHMENTS_OPTIONS, { id: operat.id })) }}
                style={{ width: '100%' }}
              >
                Zarządzaj załącznikami
              </Button>
            </Col>
            <Col>
              <Button disabled={operat && operat.status.toLowerCase() !== 'generated'} color='info' type='submit' onClick={() => { signPdf() }} style={{ width: '100%' }}>
                {t('history.table.buttons.sign')}
              </Button>
            </Col>
            <Col>
              <Button
                color='info' type='file' onClick={() => inputFileSigned.current.click()} disabled={operat && operat.status.toLowerCase() === 'signed'}
                id='upload-signed-btn'
                style={{ width: '100%' }}
              >
                {t('history.table.buttons.uploadSigned')}
              </Button>
            </Col>
            <UncontrolledTooltip
              placement='top'
              target='upload-signed-btn'
            >
              Przycisk służy do wgrywania podpisanego operatu, po uprzednim wygenerowaniu go w platformie Aoperat i podpisaniu go np. poprzez profil zaufany.
            </UncontrolledTooltip>
            <input
              type='file'
              id='file-upload-signed-operat'
              ref={inputFileSigned}
              style={{ display: 'none' }}
              accept='.pdf'
              onChange={(event) => props.handleUploadSignedOperat(operat.id, event.currentTarget.files[0])}
            />
            <Col>
              <Button disabled={operat && operat.status.toLowerCase() !== 'signed'} color='info' type='submit' onClick={() => { dispatch(showLoadingScreen(modalTypeConstants.SEND_OPERAT_MODAL, { operat: props.row, sendFunction: props.handleSendPdf })) }} style={{ width: '100%' }}>
                {t('history.table.buttons.send')}
              </Button>
            </Col>

          </Row>
          <br />
          <Row>
            <Col>
              <Button color='danger' onClick={() => showConfirmBox(t('history.table.confirm.message'), t('history.table.confirm.warning'), () => props.handleRemoveOperat())} style={{ width: '100%' }}>
                {t('history.table.buttons.delete')}
              </Button>
            </Col>
            <Col />
            <Col />
            <Col />
          </Row>
        </div>}
    </div>

  )
}

export default HistoryRow
