import { createSelector } from 'reselect'
import { initialState } from './reducer'

const selectSubscription = (state) => state.subscriptions || initialState

const makeSelectOperatorsInfo = () => createSelector(selectSubscription, state => state.operatorsInfo)

const makeSelectCurrentOperator = () => createSelector(selectSubscription, state => state.currentOperator)

export {
  selectSubscription,
  makeSelectOperatorsInfo,
  makeSelectCurrentOperator
}
