import React, { useState, useEffect } from 'react'

const ScrollButton = () => {
  const scrollToTop = () => {
    getWrapper().scrollTop = 0
  }
  const [visible, setVisible] = useState(false)
  const getWrapper = () => document.querySelector('#wrapper')
  useEffect(() => {
    getWrapper().addEventListener('scroll', toggleVisible)

    return () => { getWrapper()?.removeEventListener('scroll', toggleVisible)}
  // eslint-disable-next-line
  }, [])
  const toggleVisible = () => {
    const scrolled = getWrapper().scrollTop
    if (scrolled > 300) {
      setVisible(true)
    } else if (scrolled <= 300) {
      setVisible(false)
    }
  }

  return (
    <div className='fixed-plugin' style={{ marginBottom: '12vh', color: '#FFFFFF', display: visible ? 'inline' : 'none' }}>
      <div>
        <div onClick={() => scrollToTop()}>
          <i className='fa fa-arrow-up fa-2x' /> <br />
          Do góry
        </div>
      </div>
    </div>
  )
}

export default ScrollButton
