export const validateLandRegisterCode = (ekw, ekwData) => {
  ekw = ekw.toUpperCase()
  let result = ''
  if (ekw === '' || !ekw) {
    result += 'Numer księgi wieczystej nie może być pusty. '
  }

  const splitted = ekw.split('/')
  if (splitted.length !== 3) {
    result += 'Nieprawidłowa liczba sekcji numeru księgi wieczystej, powinny być 3 oddzielone znakiem /. '
    return result
  }
  if (splitted[0].length !== 4 || splitted[1].length !== 8 || splitted[2].length !== 1) {
    result += 'Nieprawidłowa długość którejś z sekcji numeru księgi wieczystej. '
  }

  if (!ekwData.regions.includes(splitted[0])) {
    result += 'Nie ma takiego kodu Wydziału Ksiąg Wieczystych Sądu Rejonowego. '
  }
  let controlSum = 0
  let idx = 0
  for (let i = 0; i < splitted[0].length; i++) {
    controlSum += ekwData.weights[splitted[0][i]] * ekwData.multipliers[idx++]
  }
  for (let i = 0; i < splitted[1].length; i++) {
    controlSum += ekwData.weights[splitted[1][i]] * ekwData.multipliers[idx++]
  }
  if (isNaN(parseInt(splitted[1]))) {
    result += 'Druga sekcja numeru księgi wieczystej powinna być daną liczbową. '
  }

  if (isNaN(parseInt(splitted[2]))) {
    result += 'Cyfra kontrolna numeru księgi wieczystej powinna być daną liczbową. '
    return result
  }

  if (controlSum % 10 !== parseInt(splitted[2])) {
    result += 'Niepoprawna suma kontrolna przy uzupełnianiu księgi wieczystej. '
  }
  return result
}
