import React, { useEffect, useState } from 'react'
import { useMsal } from '@azure/msal-react'
import { Table, Button } from 'reactstrap'
import { operatorFilesService } from '../../services'
import { modalTypeConstants } from '../../constants'
import confirm from 'reactstrap-confirm'
import { useDispatch } from 'react-redux'
import { showLoadingScreen, hideLoadingScreen } from 'views/Modal/actions'

function UserUploadedFiles (props) {
  const { accounts } = useMsal()
  const [operatorFiles, setOperatorFile] = useState([])
  const dispatch = useDispatch()

  const generateRows = () => {
    return operatorFiles.map((item, idx) => (
      <tbody key={idx}>
        <tr>
          <th scope='row'>{idx + 1}</th>
          <td>{item.item1}</td>
          <td>
            <Button
              color='primary'
              onClick={() => {
                operatorFilesService
                  .getFileBlob(item.item2, item.item1)
              }}
            >
              pobierz
            </Button>
            {'    '}
            <Button
              color='warning'
              onClick={() => {
                deleteFile(item)
              }}
            >
              Usuń
            </Button>
          </td>
        </tr>
      </tbody>
    ))
  }

  const deleteFile = async (item) => {
    const result = await confirm({
      title: <>Ostrzeżenie</>,
      message: `Czy na pewno chcesz usunąć plik ${item.item1}`,
      confirmText: 'Usuń',
      confirmColor: 'danger',
      cancelColor: 'link text-primary'
    })
    if (result) {
      dispatch(showLoadingScreen(modalTypeConstants.LOADING, { message: 'deleteFile', translate: true }))
      operatorFilesService
        .removeOperatorFileByName(
          accounts[0].idTokenClaims.oid,
          item.item1
        )
        .then(() => {
          setOperatorFile(
            operatorFiles.filter((obj) => obj.item1 !== item.item1)
          )
          dispatch(hideLoadingScreen())
        })
    }
  }

  const files = () => (
    <Table>
      <thead>
        <tr>
          <th>#</th>
          <th>nazwa pliku</th>
          <th>Akcja</th>
        </tr>
      </thead>
      {generateRows()}
    </Table>
  )

  useEffect(() => {
    dispatch(showLoadingScreen(modalTypeConstants.LOADING, { message: 'loadingData', translate: true }))
    operatorFilesService
      .getOperatorFiles(accounts[0].idTokenClaims.oid)
      .then((result) => {
        setOperatorFile(result)
        dispatch(hideLoadingScreen())
      })
  }, [accounts, dispatch])

  return <>{operatorFiles.length > 0 && files()}</>
}

export default UserUploadedFiles
