import { memo, useEffect, useState } from 'react'
import BootstrapTable from 'react-bootstrap-table-next'
import {
  Row,
  Col,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Button,
  Collapse
} from 'reactstrap'
import { userRolesConstants } from '../../constants'
import { useHistory } from 'react-router'
import Pagination from '@mui/material/Pagination'
import confirm from 'reactstrap-confirm'
import { connect } from 'react-redux'
import { createStructuredSelector } from 'reselect'
import { makeSelectUsers, makeSelectUserType } from './selectors'
import { deleteUser, loadUsers, promoteUser, setUserFilters, setUsersPageNumber, setUsersSortParameters, setUserType } from './actions'
import { compose } from 'redux'
import { useInjectReducer } from 'utils/injectReducer'
import reducer from './reducer'
import saga from './saga'
import { useInjectSaga } from 'utils/injectSaga'
import LoadingIndicator from 'components/LoadingIndicator'
import UsersFilters from 'components/Filters/Users'
const key = 'usersPage'
function Users (props) {
  useInjectReducer({ key, reducer })
  useInjectSaga({ key, saga })
  const history = useHistory()
  const [isDropdownOpen, setIsDropdownOpen] = useState(false)
  const [isFiltersVisible, setIsFiltersVisible] = useState(false)
  const userTypes = [
    { label: 'Klient', value: 'client' },
    { label: 'Operator', value: 'operator' },
    { label: 'Administrator', value: 'admin' }
  ]

  function getPromotedUserRole () {
    switch (props.userType) {
      case "client": return "Operator";
      case "operator": return "Administrator";
      default: return "";
    }
  }

  const promoteUser = async (id, name) => {
    const promoteTo = getPromotedUserRole()

    const result = await confirm({
      title: <>Ostrzeżenie</>,
      message: <>Czy na pewno chcesz wypromować użytkownika {name} do roli <b>{promoteTo}</b></>,
      confirmText: 'Tak',
      confirmColor: 'info',
      cancelColor: 'link text-primary',
      cancelText: 'nie'
    })
    if (!result) {
      return
    }

    props.handlePromoteUser(id)
  }

  const deleteUser = async (id, name) => {
    const role = userTypes.find(x => x.value === props.userType)?.label
    const result = await confirm({
      title: <>Ostrzeżenie</>,
      message: <>Czy na pewno chcesz usunąć użytkownika {name} z grupy <b>{role}</b></>,
      confirmText: 'Tak',
      confirmColor: 'info',
      cancelColor: 'link text-primary',
      cancelText: 'nie'
    })
    if (!result) {
      return
    }

    props.handleDeleteUser(id)
  }

  useEffect(() => {
    props.handleSetUsersPageNumber('1')
    // eslint-disable-next-line
  }, [])

  const handleChange = (event, newPage) => {
    const search = new URLSearchParams(history.location.search)
    search.set('pageNumber', newPage.toString())
    history.push({
      pathname: '/users-promotion',
      search: search.toString()
    })
    props.handleSetUsersPageNumber(newPage)
  }

  const sortable = {
    onSort: (field, order) => {
      props.handleSetUsersSortParameters(field)
    },
    sortFunc: (a, b, order, dataField, rowA, rowB) => {

    },
    sort: true
  }
  const columns = () => {
    return [
      {
        dataField: 'id',
        text: 'ID',
        hidden: false
      },
      {
        dataField: 'name',
        text: 'Nazwa',
        ...sortable,
        editable: false,
        align: 'center',
        headerAlign: 'center'
      },
      {
        dataField: 'mail',
        text: 'Adres e-mail',
        ...sortable,
        editable: false,
        align: 'center',
        headerAlign: 'center'
      },
      props.userType !== userRolesConstants.CLIENT &&
      {
        text: 'Profil użytkownika',
        align: 'center',
        headerAlign: 'center',
        formatter: (cellContent, row) => <Button color='primary' onClick={() => history.push({ pathname: '/users-profile', search: '?operatorId=' + row.id })}>
          Profil użytkownika
        </Button>
      },
      {
        text: 'Promocja',
        align: 'center',
        headerAlign: 'center',
        formatter: (cellContent, row) => <Button disabled={!row.canPromote} color='success' onClick={() => promoteUser(row.id, row.name)}>
          Promocja
        </Button>
      },
      {
        text: 'Usun',
        align: 'center',
        headerAlign: 'center',
        formatter: (cellContent, row) => <Button disabled={props.userType === 'admin'} color='danger' onClick={() => deleteUser(row.id, row.name)}>
          Usuń
        </Button>
      }
    ]
  }

  useEffect(() => {
    props.handleLoadUsers()
  // eslint-disable-next-line
  }, [props.userType, props.users.pageNumber, props.users.filters, props.users.sortFieldName, props.users.sortDirection])

  return (
    <div className='content'>
      <div className='container'>
        <Button color='success' onClick={() => setIsFiltersVisible(v => !v)}>Filtry</Button>
        <Collapse isOpen={isFiltersVisible}>
          <UsersFilters handleSetUserFilters={props.handleSetUserFilters} />
        </Collapse>
        <Dropdown isOpen={isDropdownOpen} toggle={() => setIsDropdownOpen(!isDropdownOpen)}>
          <Pagination count={localStorage.getItem('pageCount')} color='primary' page={(new URLSearchParams(history.location.search)).get('pageNumber') ? parseInt((new URLSearchParams(history.location.search)).get('pageNumber')) : 1} onChange={handleChange} />
          <DropdownToggle caret>Wybierz rodzaj użytkownika [{userTypes.map(x => x.value === props.userType ? x.label : '')}]</DropdownToggle>
          <DropdownMenu>
            {userTypes.map((element) => {
              return (
                <DropdownItem
                  key={element.value}
                  onClick={() => {
                    props.handleSetUserType(element.value)
                  }}
                >
                  {element.label}
                </DropdownItem>
              )
            })}
          </DropdownMenu>
        </Dropdown>
        <Row>
          <Col md='12'>
            {props.users.loading
              ? <LoadingIndicator />
              : <BootstrapTable
                  bootstrap4
                  keyField='id'
                  data={props.users.users}
                  columns={columns()}
                  striped
                  hover
                  condensed
                  bordered={false}
                />}
          </Col>
        </Row>
      </div>
    </div>
  )
}
const mapStateToProps = createStructuredSelector({
  userType: makeSelectUserType(),
  users: makeSelectUsers()
})

export function mapDispatchToProps (dispatch) {
  return {
    handleSetUserType: (userType) => dispatch(setUserType(userType)),
    handleLoadUsers: () => dispatch(loadUsers()),
    handlePromoteUser: (userId) => dispatch(promoteUser(userId)),
    handleDeleteUser: (userId) => dispatch(deleteUser(userId)),
    handleSetUsersPageNumber: (pageNumber) => dispatch(setUsersPageNumber(pageNumber)),
    handleSetUserFilters: (filters) => dispatch(setUserFilters(filters)),
    handleSetUsersSortParameters: fieldName => dispatch(setUsersSortParameters(fieldName))
  }
}

const withConnect = connect(mapStateToProps, mapDispatchToProps)

export default compose(withConnect, memo)(Users)
