
import produce from 'immer'
import { LOAD_HOME_PAGE_CONTENT, HOME_PAGE_CONTENT_LOADED, LOAD_HOME_PAGE_CONTENT_ERROR } from './constants'

export const initialState = {
  pageContent: {
    content: '',
    loading: true,
    error: false
  }
}

const subscriptionsReducer = (state = initialState, action) => produce(state, (draft) => {
  // eslint-disable-next-line default-case
  switch (action.type) {
    case LOAD_HOME_PAGE_CONTENT:
      draft.pageContent.content = ''
      draft.pageContent.error = false
      draft.pageContent.loading = true
      break
    case HOME_PAGE_CONTENT_LOADED:
      draft.pageContent.content = action.payload
      draft.pageContent.error = false
      draft.pageContent.loading = false
      break
    case LOAD_HOME_PAGE_CONTENT_ERROR:
      draft.pageContent.data = ''
      draft.pageContent.loading = false
      draft.pageContent.error = action.payload
      break
  }
})

export default subscriptionsReducer
