import React, { useState } from 'react'
import {
  Card,
  CardBody,
  Input,
  Button,
  Form
} from 'reactstrap'

export const OrderPropositionFilters = ({ handleSetPropositionFilters }) => {
  const [upTillDateFrom, setUpTillDateFrom] = useState('')
  const [upTillDateTo, setUpTillDateTo] = useState('')
  const [priceFrom, setPriceFrom] = useState('')
  const [priceTo, setPriceTo] = useState('')
  const handleClear = () => {
    setUpTillDateFrom('')
    setUpTillDateTo('')
    setPriceFrom('')
    setPriceTo('')
    handleSetPropositionFilters({ upTillDateFrom: '', upTillDateTo: '', priceFrom: '', priceTo: '' })
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    handleSetPropositionFilters({ upTillDateFrom, upTillDateTo, priceFrom, priceTo })
  }
  return (
    <Card>
      <CardBody>
        <Form onSubmit={handleSubmit}>
          <label for='order-proposition-price-from'>
            Cena od
          </label>
          <Input
            type='number' placeholder='Cena od' id='order-proposition-price-from' name='order-proposition-price-from' onChange={(e) => {
              setPriceFrom(e.target.value)
            }} value={priceFrom}
          />
          <label for='order-proposition-price-to'>
            Cena do
          </label>
          <Input
            type='number' placeholder='Cena do' id='order-proposition-price-to' name='order-proposition-price-to' onChange={(e) => {
              setPriceTo(e.target.value)
            }} value={priceTo}
          />
          <label for='order-proposition-up-till-date-from'>
            Termin ukończenia od
          </label>
          <Input
            type='date' placeholder='Termin ukończenia od' id='order-proposition-up-till-date-from' name='order-proposition-up-till-date-from' onChange={(e) => {
              setUpTillDateFrom(e.target.value)
            }} value={upTillDateFrom}
          />
          <label for='order-proposition-up-till-date-to'>
            Termin ukończenia do
          </label>
          <Input
            type='date' placeholder='Termin ukończenia do' id='order-proposition-up-till-date-to' name='order-proposition-up-till-date-to' onChange={(e) => {
              setUpTillDateTo(e.target.value)
            }} value={upTillDateTo}
          />
          <Button onClick={handleClear}>Wyczyść</Button>
          <Button type='submit' color='success'>Filtruj</Button>
        </Form>
      </CardBody>
    </Card>
  )
}

export default OrderPropositionFilters
