import { HOME_PAGE_CONTENT_LOADED, LOAD_HOME_PAGE_CONTENT, LOAD_HOME_PAGE_CONTENT_ERROR } from './constants'

export function loadHomePageContent () {
  return {
    type: LOAD_HOME_PAGE_CONTENT
  }
}

export function homePageContentLoaded (payload) {
  return {
    type: HOME_PAGE_CONTENT_LOADED,
    payload
  }
}

export function loadHomePageContentError (payload) {
  return {
    type: LOAD_HOME_PAGE_CONTENT_ERROR,
    payload
  }
}
