import React, { useEffect, useRef, useState } from 'react'
import BootstrapTable from 'react-bootstrap-table-next'
import cellEditFactory from 'react-bootstrap-table2-editor'
import ToolkitProvider, { CSVExport } from 'react-bootstrap-table2-toolkit'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { Button, Card, CardBody, Col, Input, Row, UncontrolledTooltip } from 'reactstrap'
import confirm from 'reactstrap-confirm'
import { showLoadingScreen } from 'views/Modal/actions'
import { modalTypeConstants } from '../../constants'
import { addLinesToCSVFromCSV, addLineToCSV, loadOperatCSV, removeLineFromCSV, editCsvCell, loadOperatWeights, loadOperatInitialWeights, saveCsvArea } from './actions'
import { ReactComponent as Exclamation } from 'assets/img/exclamation.svg'

function CsvComponent ({ operat, operatTypeParams }) {
  const { t } = useTranslation('common')
  const dispatch = useDispatch()
  const operatState = useSelector(state => state.operat)
  const [selectedRows, setSelectedRows] = useState([])
  const [csvToRead, setCsvToRead] = useState()

  const columnKeys = [
    ...operatTypeParams && operatTypeParams.csvColumns.map(x => x.dataField),
    ...operatState.weights && operatState.weights.map(x => x.name)
  ]

  const inputFile = useRef(null)
  const baseColumnProps = {
    headerAlign: 'center',
    align: 'center',
    editable: true,
    sort: true
  }
  const createCsvDataRenderer = (editorProps, value, row, column, rowIndex, columnIndex, type, colName) => (<EditComponent {...editorProps} value={value} type={type} onUpdate={e => dispatch(editCsvCell(row.id, colName, e))} />)
  const createCsvWeightsRenderer = (editorProps, value, row, column, rowIndex, columnIndex, rangeLow, rangeHigh, colName) => (<EditWeightComponent {...editorProps} value={value} rangeLow={rangeLow} rangeHigh={rangeHigh} onUpdate={e => dispatch(editCsvCell(row.id, colName, e))} />)
  const cols = () => [
    {
      dataField: 'id',
      text: 'ID',
      hidden: true
    },
    {
      text: "Błędy",
      editable: false,
      formatter: (cellContent, row) => {
        return ( row && Object.entries(row).some(([key, value]) => (value === undefined || value === null || value === '') && columnKeys.includes(key)) 
          ? <>
            <Exclamation style={{width: "20px", height: "20px"}} id={`row-errors-${row.id}`} fill='red' />
            <UncontrolledTooltip
              placement='top'
              target={`row-errors-${row.id}`}
            >
              <p>Uzupełnij wszystkie pola w rzędzie</p>
            </UncontrolledTooltip></> 
          : <div />
          )}
    },
    ...operatTypeParams && operatTypeParams.csvColumns.map( x => ({
      dataField: x.dataField,
      text: x.text,
      ...baseColumnProps,
      editorRenderer: (editorProps, value, row, column, rowIndex, columnIndex) => createCsvDataRenderer(editorProps, value, row, column, rowIndex, columnIndex, x.type, x.dataField)
    })),
    ...operatState.weights && operatState.weights.map(x => ({
      dataField: x.name,
      text: "Wartość oceny "+x.description,
      ...baseColumnProps,
      editorRenderer: (editorProps, value, row, column, rowIndex, columnIndex) => createCsvWeightsRenderer(editorProps, value, row, column, rowIndex, columnIndex, x.range[0], x.range[1], x.name)
    }))
  ]

  useEffect(() => {
    const csvItems = operatState.files.csv
    const load = () => {
      dispatch(showLoadingScreen(
        modalTypeConstants.LOADING,
        { message: 'loadingData', translate: true }
      ))
      loadOperatCSV(dispatch, csvItems.item2)
    }
    !!csvItems && Object.keys(csvItems).length > 0 && load()
    const loadWeights = () => {
      dispatch(showLoadingScreen(
        modalTypeConstants.LOADING,
        {}
      ))
      loadOperatWeights(dispatch, dataJson.item2)
    }

    const dataJson = operatState.files.weights
    !!dataJson && (Object.keys(dataJson).length > 0 ? loadWeights() : dispatch(loadOperatInitialWeights(operatTypeParams.weights)))
    // eslint-disable-next-line
  }, [operatState.files.csv])

  const selectRow = () => {
    const select = {
      mode: 'checkbox',
      clickToEdit: false,
      clickToSelect: false,
      onSelect: (row, isSelect, rowIndex, e) => {
        isSelect && setSelectedRows((allRows) => [...allRows, row.id])
        !isSelect &&
          setSelectedRows(selectedRows.filter((item) => item !== row.id))
      },
      onSelectAll: (isSelect, rows) => {
        !isSelect && setSelectedRows([])
        isSelect &&
          setSelectedRows(
            rows.map((row) => {
              return row.id
            })
          )
      }
    }
    return select
  }
  const cellEdit = cellEditFactory({
    mode: 'click'
  })
  const { ExportCSVButton } = CSVExport

  const processCSV = (str) => {
    const rows = str.slice(str.indexOf('\n') + 1).split('\n').filter(x => x !== '')
    const EXPECTED_COUNT_OF_COLUMNS = 9
    if (
      rows.some(
        (row) =>
          (row.match(/;/g) || []).length !== EXPECTED_COUNT_OF_COLUMNS - 1
      )
    ) {
      const columns = str.split('\n')[0].split(';').map(item => item.trim()).filter(x => x !== '')
      const requiredColumns = cols.filter(x => x.text !== 'ID').map(x => { return x.dataField }).filter(x => !columns.includes(x.trim()))
      showWarning(EXPECTED_COUNT_OF_COLUMNS, requiredColumns)
      return
    }
    dispatch(addLinesToCSVFromCSV(rows))
  }

  function EditWeightComponent (props) {
    const [value, setValue] = useState(props.value)
    return (
      <Input
        value={value}
        type='number'
        onChange={(e) => setValue((prevState) => {
          if(e.target.value >= props.rangeLow && e.target.value <= props.rangeHigh ) {
            return e.target.value
          }
          return prevState
        })}
        min={props.rangeLow}
        max={props.rangeHigh}
        onBlur={() => { props.onUpdate(value) }}
      />
    )
  }
  
  function EditComponent (props) {
    const [value, setValue] = useState(props.value)
    return (
      props.type === 'number' ?
      <Input
        value={value}
        type={props.type}
        onChange={(e) => setValue((prevState) => {
          if(e.target.value >= 0){
            return e.target.value
          }
          return prevState
        })}
        min={0}
        onBlur={() => { props.onUpdate(value) }}
      />
      : <Input
        value={value}
        type={props.type}
        onChange={(e) => setValue(e.target.value)}
        
        onBlur={() => { props.onUpdate(value) }}
      />
    )
  }
  const submit = () => {
    const file = csvToRead
    const reader = new FileReader()

    reader.onload = function (e) {
      const text = e.target.result
      processCSV(text)
    }

    reader.readAsText(file)
  }

  const showWarning = async (EXPECTED_COUNT_OF_COLUMNS, requiredColumns) => {
    const mess = `
      Liczba kolumn w pliku się nie zgadza.
      Plik  powinien zawierać ${EXPECTED_COUNT_OF_COLUMNS} kolumn.
      Brakujące kolumny to : ${requiredColumns.map(x => x)}
      `
    await confirm({
      title: <>{t('history.table.confirm.warning')}</>,
      message: mess,
      confirmText: 'Wróć',
      cancelText: '',
      confirmColor: 'danger',
      cancelColor: 'link text-primary'
    })
  }

  const handleTableChange = (type, { data, cellEdit: { rowId, dataField, newValue } }) => {
    dispatch(editCsvCell(rowId, dataField, newValue))
  }

  return (
    <>
      <Card>
        <CardBody>
          <br />
          <br />
          <br />
          <br />
          <div className='container'>
            <Row>
              <Col md='12'>
                <Card>
                  <ToolkitProvider
                    keyField='id'
                    data={operatState.csv}
                    columns={cols()}
                    exportCSV={{ onlyExportSelection: true, exportAll: true }}
                  >
                    {(props) => (
                      <div>
                        <Button
                          color='success'
                          onClick={() => {
                            dispatch(saveCsvArea(columnKeys))
                          }}
                        >
                          Wyślij dane
                        </Button>
                        <Button
                          color='info'
                          onClick={() => {
                            dispatch(addLineToCSV(columnKeys))
                          }}
                        >
                          Dodaj transakcję
                        </Button>
                        <Button
                          color='info'
                          onClick={() => {
                            dispatch(removeLineFromCSV(
                              selectedRows
                            ))
                            setSelectedRows([])
                          }}
                        >
                          Usuń zaznaczone linie
                        </Button>
                        <ExportCSVButton {...props.csvProps}>
                          Export do pliku
                        </ExportCSVButton>
                        <Button onClick={() => inputFile.current.click()}>
                          {csvToRead
                            ? 'wybierz plik [' + csvToRead.name + ']'
                            : 'wybierz plik'}
                        </Button>
                        <input
                          type='file'
                          id='file'
                          ref={inputFile}
                          style={{ display: 'none' }}
                          accept='.csv'
                          onChange={(e) => {
                            setCsvToRead(e.target.files[0])
                          }}
                        />
                        <Button
                          disabled={!csvToRead}
                          onClick={(e) => {
                            e.preventDefault()
                            submit()
                          }}
                        >
                          Importuj
                        </Button>
                        <Button
                          color='secondary'
                          id='copyInputArea'
                          onClick={() => {
                            dispatch(showLoadingScreen(
                              modalTypeConstants.OPERATS_LIST_MODAL,
                              { operat: operat, dataType: 'csv' }
                            ))
                          }}
                        >
                          kopiuj dane transakcyjne z innego operatu
                        </Button>
                        <hr />
                        <BootstrapTable
                          wrapperClasses='table-scroll'
                          {...props.baseProps}
                          remote={{ cellEdit: true }}
                          selectRow={selectRow()}
                          tabIndexCell
                          bootstrap4
                          striped
                          hover
                          condensed
                          bordered={false}
                          cellEdit={cellEdit}
                          onTableChange={handleTableChange}
                        />
                      </div>
                    )}
                  </ToolkitProvider>
                </Card>
              </Col>
            </Row>
          </div>
        </CardBody>
      </Card>
    </>
  )
}

export default CsvComponent
