import React, { useRef, useEffect, useState } from 'react'
import NotificationAlert from 'react-notification-alert'
import {
  UncontrolledTooltip,
  Col,
  FormGroup,
  Input,
  Button,
  UncontrolledCollapse,
  Label,
  Form
} from 'reactstrap'
import { modalTypeConstants } from '../../constants'
import { formatDate } from 'utils/DateFormat'
import { useDispatch, useSelector } from 'react-redux'
import { showLoadingScreen } from 'views/Modal/actions'
import { downloadFromEkw, loadOperatEmptyInput, loadOperatInput, saveInputArea, updateList, updateValue } from './actions'
import { useMsal } from '@azure/msal-react'
import InputMask from 'react-input-mask'
import { validateLandRegisterCode } from 'utils/validateLandRegisterCode'
import { ReactComponent as Exclamation } from 'assets/img/exclamation.svg'
function InputArea (props) {
  const { accounts } = useMsal()
  const operatState = useSelector(state => state.operat)
  const ekwData = useSelector(state => state.global.ekw)
  const [listInputData, setListInputData] = useState({})

  const dispatch = useDispatch()
  useEffect(() => {
    const load = () => {
      dispatch(showLoadingScreen(
        modalTypeConstants.LOADING,
        {}
      ))
      loadOperatInput(dispatch, dataJson.item2, false)
    }

    const dataJson = operatState.files.input
    !!dataJson && (Object.keys(dataJson).length > 0 ? load() : dispatch(loadOperatEmptyInput(props.operatTypeParams.input)))
    if (Object.keys(dataJson).length === 0) {
      applyMacros()
      loadInitialDataFromOrder()
    }
    // eslint-disable-next-line
  }, [operatState.operatTypeId, operatState.files, props.operatTypeParams, dispatch])

  const loadInitialDataFromOrder = () => {
    const temp = JSON.parse(JSON.stringify(props.operatTypeParams.input))
    if (props.order && props.order.ekw && props.order.ekw !== '') {
      try {
        const kwIndex = temp.findIndex(x => x.name === 'kw')
        const kwNrIndex = temp[kwIndex].findIndex(x => x.name === 'nr')
        temp[kwIndex].data[kwNrIndex].value = props.order.ekw
        dispatch(loadOperatEmptyInput(temp))
      } catch (err) {
        console.log(err)
      }
    }
  }

  const applyMacros = () => {
    const temp = JSON.parse(JSON.stringify(props.operatTypeParams.input))
    for (let i = 0; i < temp.length; i++) {
      for (let j = 0; j < temp[i].data.length; j++) {
        if (temp[i].data[j].default === 'now()' && temp[i].data[j].type === 'date') { temp[i].data[j].value = formatDate(new Date()) }
        if (temp[i].data[j].value === 'author()') { temp[i].data[j].value = [accounts[0].idTokenClaims.given_name, accounts[0].idTokenClaims.family_name].filter(x => x).join(' ') }
        if (temp[i].data[j].value === 'operatCreationDate()'){ temp[i].data[j].value = formatDate(new Date(operatState.operat.createdDateTime))}
        if (temp[i].data[j].value === 'client()' && operatState.input && operatState.input.length > 0){
          const kw_data = operatState.input.find(x => x.name === "kw").data
          const kw_owner = kw_data.find(x => x.name === "wlasciciel")
          if(kw_owner.value !== ""){
            temp[i].data[j].value = kw_owner.value
          }
        }
      }
    }
    dispatch(loadOperatEmptyInput(temp))
  }
  const handleChangeValue = (value, id, idx) => {
    dispatch(updateValue(value, id, idx))
  }

  const notifyRegister = useRef(null)

  const createEkwUrl = (code) => {
    const url = new URL('https://przegladarka-ekw.ms.gov.pl/eukw_prz/KsiegiWieczyste/wyszukiwanieKW')
    return url.toString()
  }

  function validateAndShowErrors () {
    const kwIndex = operatState.input.findIndex(x => x.name === 'kw')
    const kwNrIndex = operatState.input[kwIndex].data.findIndex(x => x.name === 'nr')
    const ekw = operatState.input[kwIndex].data[kwNrIndex]
    const landRegisterValidationResult = validateLandRegisterCode(ekw.value, ekwData)
    if (landRegisterValidationResult !== '') {
      const temp = JSON.parse(JSON.stringify(operatState.input))
      temp[kwIndex].data[kwNrIndex].isValid = false
      dispatch(loadOperatEmptyInput(temp))
      dispatch(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: landRegisterValidationResult }))
      return false
    }

    return true
  }
  const renderSwitch = (dataItem, idx, id, genItem) => {
    switch (dataItem.type) {
      case 'ekw': return (
        <>
          <Button onClick={() => dispatch(downloadFromEkw(dataItem.value, genItem.name))}>Pobierz z ekw</Button>
          <Button href={createEkwUrl(dataItem.value)} target='_blank'>Przeglądaj EKW</Button>
          <br />
          <InputMask
            mask='aa9a/99999999/9'
            value={dataItem.value}
            onChange={(e) => {
              handleChangeValue(e.target.value, id, idx)
            }}
            invalid={typeof dataItem.isValid === 'undefined' ? false : !dataItem.isValid}
            placeholder={dataItem.description}
          >
            {(inputProps) => <Input
              {...inputProps}
              type={dataItem.type}
              style={{ textTransform: 'uppercase' }}
              name={genItem.name + dataItem.type + idx}
              id={dataItem.name + idx}
                             />}
          </InputMask>
        </>
      )
      case 'list': return (
        <>
          <Form
            onSubmit={(e) => {
              e.preventDefault()
              if (listInputData.hasOwnProperty(idx.toString()) && listInputData[idx.toString()] !== '') {
                dispatch(updateList(listInputData[idx.toString()], id, idx, null, false))
                let newValue = listInputData
                newValue[idx.toString()] = ''
                setListInputData(newValue)
                e.target.reset()
              }
            }}
          >
            <FormGroup>
              <Label>{dataItem.label}</Label>
              <Input
                onChange={(e) => {
                  let newValue = listInputData
                  newValue[idx.toString()] = e.target.value
                  console.log(newValue)
                  setListInputData(newValue)
                }}
                type='text'
                required
              />
            </FormGroup>
            <Button type='submit'>{dataItem.button}</Button>
          </Form>
          <ul class='list-group'>
            {dataItem.value.map((e, listIdx) => (
              <li
                key={listIdx}
                class='list-group-item d-flex justify-content-between align-items-center'
                style={{ overflowWrap: 'anywhere' }}
              >
                {e}
                <span
                  onClick={() => { dispatch(updateList(listInputData[idx.toString()], id, idx, listIdx, true)) }}
                >
                  <i className='nc-icon nc-simple-remove' />
                </span>
              </li>
            ))}
          </ul>
        </>
      )
      default: return (
        <Input
          type={dataItem.type}
          name={genItem.name + dataItem.type + idx}
          id={dataItem.name + idx}
          placeholder={dataItem.description}
          value={dataItem.value}
          onChange={(e) => {
            handleChangeValue(e.target.value, id, idx)
          }}
        />
      )
    }
  }
  return (
    <>
      <NotificationAlert ref={notifyRegister} />
      {operatState.input && operatState.input.length > 0 &&
        operatState.input.map((genItem, id) => {
          return (
            <div key={genItem.name + 'Area' + id}>
              <Button
                color='info'
                id={genItem.name + 'Area' + id}
                style={{ width: '16rem' }}
              >
                {genItem.text}
              </Button> {genItem.data.some(x => x.hasOwnProperty('isValid') && !x.isValid) && <Exclamation fill='red' />}

              <UncontrolledCollapse toggler={genItem.name + 'Area' + id}>
                {genItem.data.map((dataItem, idx) => {
                  return (
                    <FormGroup row key={genItem.name + idx}>
                      <Col md='3'>
                        <p>{dataItem.description}</p>
                      </Col>
                      <Col sm={8}>
                        {renderSwitch(dataItem, idx, id, genItem)}
                        {dataItem.tooltip && dataItem.tooltip !== '' &&
                          <UncontrolledTooltip
                            placement='top'
                            target={dataItem.name + idx}
                          >
                            {dataItem.tooltip}
                          </UncontrolledTooltip>}
                      </Col>
                    </FormGroup>
                  )
                })}
              </UncontrolledCollapse>
            </div>
          )
        })}
      <Button
        color='success'
        id='saveInputArea'
        onClick={() => {
          if (!validateAndShowErrors()) { return }
          dispatch(saveInputArea(operatState.id))
        }}
        style={{ marginBottom: '1rem', width: '16rem' }}
      >
        Wyślij dane opisowe
      </Button>
      <Button
        color='secondary'
        id='copyInputArea'
        onClick={() => {
          dispatch(showLoadingScreen(
            modalTypeConstants.OPERATS_LIST_MODAL,
            { operat: props.operat, dataType: 'params' }
          ))
        }}
        style={{ marginBottom: '1rem' }}
      >
        kopiuj dane opisowe z innego operatu
      </Button>
    </>
  )
}

export default InputArea
