import React from 'react'
import PropTypes from 'prop-types'
import { Button, Form, FormGroup, Input, Label } from 'reactstrap'
const OperatorTrainingForm = ({ onSubmit }) => {
  return (
    <div>
      <Form
        onSubmit={(e) => {
          e.preventDefault()
          onSubmit()
        }}
        id='operator-training-form'
      >
        <FormGroup>
          <Label for='training-title'>Tytuł szkolenia</Label>
          <Input
            type='text'
            name='training-title'
            id='training-title'
            placeholder='Tytuł szkolenia'
            required
          />
        </FormGroup>
        <FormGroup>
          <Label for='training-url'>Adres url</Label>
          <Input
            type='text'
            name='training-url'
            id='training-url'
            placeholder='Adres url'
            required
          />
        </FormGroup>
        <Button
          color='success'
        >
          Dodaj szkolenie
        </Button>
      </Form>
    </div>
  )
}

OperatorTrainingForm.propTypes = {
  onSubmit: PropTypes.func.isRequired
}

export default OperatorTrainingForm
