import { Table, Button } from 'reactstrap'
import PropTypes from 'prop-types'
function ReadonlyTable ({ headers, data, style, className, deleteAction }) {
  return (
    <Table style={style} className={className}>
      <thead>
        <tr>
          <th>
            #
          </th>
          {headers.map((x, idx) => (
            <th key={idx}>{x}</th>
          ))}
        </tr>
      </thead>
      <tbody>
        {data.map((x, idx) => (
          <tr key={idx + x.title}>
            <th scope='row'>
              {idx + 1}
            </th>
            {Object.entries(x).map((t, idx) => (
              t[0] !== 'id' &&
                <td key={idx}>
                  {t[1]}
                </td>
            ))}
            {deleteAction && <td><Button color='danger' onClick={() => deleteAction(x.id)}>Usuń</Button></td>}
          </tr>
        ))}
      </tbody>
    </Table>
  )
}
ReadonlyTable.propTypes = {
  headers: PropTypes.array.isRequired,
  data: PropTypes.array.isRequired,
  styles: PropTypes.object,
  className: PropTypes.string,
  deleteAction: PropTypes.func
}
export default ReadonlyTable
