import { call, put, select, takeLatest } from 'redux-saga/effects'
import { makeSelectOperatorsInfo } from './selectors'
import { loadOperatorsInfoError, operatorsInfoLoaded } from './actions'
import { CREATE_SUBSCRIPTION, LOAD_OPERATORS_INFO, RESET_SUBSCRIPTIONS, UPDATE_SUBSCRIPTION } from './constants'
import { showLoadingScreen, hideLoadingScreen } from 'views/Modal/actions'
import { modalTypeConstants } from '../../constants'
import { handleRequestWithTimeout } from 'views/Modal/timeout'

export function * getOperatorsInfo () {
  const operatorsInfo = yield select(makeSelectOperatorsInfo())
  const headers = {
    Authorization: `Bearer ${localStorage.getItem('idToken')}`
  }
  const options = { method: 'GET', headers }

  let requestUrl = '/Subscription/operators-info'
  requestUrl += `?pageNumber=${operatorsInfo.pageNumber}&pageSize=5`
  if (operatorsInfo.filters) {
    requestUrl += `&name=${operatorsInfo.filters.name}&mail=${operatorsInfo.filters.mail}`
  }
  if (operatorsInfo.sortFieldName) {
    requestUrl += `&fieldName=${operatorsInfo.sortFieldName}&direction=${operatorsInfo.sortDirection}`
  }

  try {
    const response = yield call(handleRequestWithTimeout, requestUrl, options)
    yield put(operatorsInfoLoaded(response))
  } catch (err) {
    yield put(loadOperatorsInfoError(err))
  }
}

export function * updateSubscription (action) {
  const requestUrl = '/Subscription/by-repo'
  const headers = {
    Authorization: `Bearer ${localStorage.getItem('idToken')}`,
    'Content-type': 'application/json'
  }
  const options = { method: 'PUT', headers, body: JSON.stringify(action.payload) }
  yield call(sendAndHandleRequest, requestUrl, options)
}

export function * createSubscription (action) {
  const requestUrl = '/Subscription'
  const headers = {
    Authorization: `Bearer ${localStorage.getItem('idToken')}`,
    'Content-type': 'application/json'
  }
  const options = { method: 'POST', headers, body: JSON.stringify(action.payload) }
  yield call(sendAndHandleRequest, requestUrl, options)
}

function * sendAndHandleRequest (requestUrl, options) {
  yield put(showLoadingScreen(modalTypeConstants.LOADING, { message: 'saveSubscription', translate: true }))

  try {
    yield call(handleRequestWithTimeout, requestUrl, options)
    yield put(showLoadingScreen(modalTypeConstants.CREATE_SUBSCRIPTION_SUCCESSFUL))
  } catch (err) {
    yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'saveSubscriptionFailed', translate: true }))
  }
}

function * resetSubscriptions () {
  const requestUrl = '/Subscription/monthly-reset'
  const headers = {
    Authorization: `Bearer ${localStorage.getItem('idToken')}`
  }
  const options = { method: 'PATCH', headers }
  yield put(showLoadingScreen(modalTypeConstants.LOADING, { message: 'resetingSubscriptions', translate: true }))
  try {
    yield call(handleRequestWithTimeout, requestUrl, options)
    yield put(hideLoadingScreen())
    yield put(showLoadingScreen(modalTypeConstants.RESET_SUBSCRIPTIONS_SUCCESSFUL))
  } catch (err) {
    yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'resetSubscriptionsFailed', translate: true }))
  }
}

export default function * watch () {
  yield takeLatest(LOAD_OPERATORS_INFO, getOperatorsInfo)
  yield takeLatest(UPDATE_SUBSCRIPTION, updateSubscription)
  yield takeLatest(CREATE_SUBSCRIPTION, createSubscription)
  yield takeLatest(RESET_SUBSCRIPTIONS, resetSubscriptions)
}
