import {
  LOAD_CONVERSATIONS, LOAD_MESSAGES_WITH_USER, MARK_CONVERSATION_AS_READ,
  MARK_CONVERSATION_AS_UNREAD, SET_CHAT_IS_OPEN, SET_CONNECTION, SET_CURRENTLY_CHAT_WITH,
  SET_MESSAGES_WITH_USER, TOGGLE_MESSAGE_VISIBLE, OPEN_CHAT
} from './constants'

export function loadMessagesWithUser (userId, messages, monthNames) {
  return {
    type: LOAD_MESSAGES_WITH_USER,
    userId,
    messages,
    monthNames
  }
}

export function setCurrentlyChatWith (payload) {
  return {
    type: SET_CURRENTLY_CHAT_WITH,
    payload
  }
}

export function setConnection (payload) {
  return {
    type: SET_CONNECTION,
    payload
  }
}

export function setMessagesWithUser (userId, message) {
  return {
    type: SET_MESSAGES_WITH_USER,
    userId,
    message
  }
}

export function loadConversations (payload) {
  return {
    type: LOAD_CONVERSATIONS,
    payload
  }
}

export function markConversationAsRead (userId) {
  return {
    type: MARK_CONVERSATION_AS_READ,
    payload: userId
  }
}

export function markConversationAsUnread (userId) {
  return {
    type: MARK_CONVERSATION_AS_UNREAD,
    payload: userId
  }
}

export function setChatIsOpen (payload) {
  return {
    type: SET_CHAT_IS_OPEN,
    payload
  }
}

export function toggleMessageVisible (userId, messageId) {
  return {
    type: TOGGLE_MESSAGE_VISIBLE,
    userId,
    messageId
  }
}

export function markMessagesAsSeen (userId) {
  return {
    type: 'MARK_MESSAGES_AS_SEEN',
    userId
  }
}

export function openChat (user, connection) {
  return {
    type: OPEN_CHAT,
    user,
    connection
  }
}
