import { addNotification } from 'utils/addNotification'
import { callForHeader } from '../utils/HeadersCall'
const apiUrl = process.env.REACT_APP_API_URL

export const operatFilesService = {
  getFormatedFilesByOperat,
  getFilesByOperat,
  getFile,
  sendJSONFile,
  sendCSVFile,
  sendPICTUREFile,
  copyOperatParams,
  copyOperatCsv,
  copyOperatPhotos,
  uploadOwnGenerated,
  removeSingleFile,
  copyOperatWeights
}

async function getFormatedFilesByOperat (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return window.fetch(`${apiUrl}/OperatFiles/id/${id}/formated`, requestOptions).then(handleResponse)
}

async function getFilesByOperat (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/OperatFiles/id/${id}`, requestOptions).then(handleResponse)
}

async function getFile (url, useCache = false) {
  const headers = new Headers()
  headers.append('Cache-Control', 'no-cache')
  const requestOptions = useCache === true
    ? {
        method: 'GET'
      }
    : {
        method: 'GET',
        headers: headers
      }
  return await window.fetch(url, requestOptions).then(handleResponse)
}

async function copyOperatParams (fromId, toId) {
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader('json')
  }
  return await fetch(`${apiUrl}/OperatFiles/id/${fromId}/copy/params/id/${toId}`, requestOptions).then(handleResponse)
}

async function copyOperatCsv (fromId, toId) {
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader('json')
  }
  return await fetch(`${apiUrl}/OperatFiles/id/${fromId}/copy/csv/id/${toId}`, requestOptions).then(handleResponse)
}

async function copyOperatWeights (fromId, toId) {
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader('json')
  }
  return await fetch(`${apiUrl}/OperatFiles/id/${fromId}/copy/weights/id/${toId}`, requestOptions).then(handleResponse)
}

async function uploadOwnGenerated (id, file) {
  const data = new FormData()
  data.append('file', file)
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader(),
    body: data
  }

  return await fetch(`${apiUrl}/OperatFiles/id/${id}/own`, requestOptions).then(handleResponse)
}

async function removeSingleFile (id, fileName) {
  const requestOptions = {
    method: 'DELETE',
    headers: await callForHeader()
  }

  return await fetch(`${apiUrl}/OperatFiles/id/${id}/filename/${fileName.replaceAll('/', '%252F')}`, requestOptions).then(handleResponse)
}

async function copyOperatPhotos (fromId, toId) {
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader('json')
  }
  return await fetch(`${apiUrl}/OperatFiles/id/${fromId}/copy/pic/id/${toId}`, requestOptions).then(handleResponse)
}

async function sendPICTUREFile (data, id) {
  data = data.filter(x => Object.prototype.hasOwnProperty.call(x, 'file'))
  if (data.length === 0) {
    return
  }
  const formData = new FormData()
  data.forEach((element, idx) => {
    formData.append(`file${idx}`, element.file, element.file.name)
  })

  const requestOptions = {
    method: 'POST',
    headers: await callForHeader(),
    body: formData
  }

  return await window.fetch(`${apiUrl}/OperatFiles/upload2/${id}`, requestOptions).then(handleResponse)
}

async function sendJSONFile (data, id) {
  const formData = new FormData()
  const blb = new Blob([JSON.stringify({ input: data })], { type: 'application/json' })
  formData.append('file', blb, 'params.json')
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader(),
    body: formData
  }

  return await window.fetch(`${apiUrl}/OperatFiles/upload/${id}/convert`, requestOptions).then(handleResponse)
}

async function sendCSVFile (data, id, weights) {
  const formData = new FormData()
  if (!jsonToCSV(data, weights)) {
    return Promise.resolve()
  }
  const blb = new Blob([jsonToCSV(data, weights)], { type: 'text/csv' })
  formData.append('file', blb, 'data.csv')
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader(),
    body: formData
  }

  return await window.fetch(`${apiUrl}/OperatFiles/upload/${id}`, requestOptions).then(handleResponse)
}

function jsonToCSV (json, weights) {
  const items = json.map(item => { delete item.id; return item })
  const header = items[0] ? Object.keys(items[0]) : ['Data', 'CenaTransakcyjna', 'RodzajRynku', 'UdzialWPrawie', 'PowierzchniaLokalu', 'Adres', 'KondygnacjaLokalu', 'KondygnacjaBudynku', 'LiczbIzb']
  for (let i = 0; i < items.length; i++) {
    for (const property in items[i]) {
      const weight = weights.find(x => x.name === property)
      if (weight) {
        if (!isNaN(items[i][property])) {
          const max = Math.max.apply(Math, weight.ratings.map(x => parseInt(x.rate)))
          if (parseInt(items[i][property]) > max) {
            addNotification('', 'Błąd w linii: ' + (i + 1), 'danger')
            return false
          }
        }
      }
    }
  }
  const csv = [
    header.join(';'), // header row first
    ...items.map(row => header.map(fieldName => row[fieldName]).join(';'))
  ].join('\r\n')

  return csv
}

function handleResponse (response) {
  return response.text().then(text => {
    let data = ''
    if (text.startsWith('Data')) {
      data = text
    } else {
      data = text && JSON.parse(text)
    }
    if (!response.ok) {
      if (response.status === 404) {
        // TODO auto logout if 404 response returned from api
        return []
      }

      if (response.status === 500) {
        // TODO auto logout if 500 response returned from api
        return []
      }

      const error = (data && data.message) || response.statusText
      return Promise.reject(error)
    }

    return data
  })
}
