import { createSelector } from 'reselect'
import { initialState } from './reducer'

const selectChat = (state) => state.chat || initialState
const makeSelectConnection = () =>
  createSelector(selectChat, (state) => state.connection)

const makeSelectMessages = () =>
  createSelector(selectChat, (state) => state.messages)

const makeSelectCurrentlyChatWith = () =>
  createSelector(selectChat, (state) => state.currentlyChatWith)

const makeSelectConversations = () =>
  createSelector(selectChat, (state) => state.conversations)
const makeSelectChatIsOpen = () =>
  createSelector(selectChat, state => state.chatIsOpen)
export {
  selectChat,
  makeSelectConnection,
  makeSelectMessages,
  makeSelectCurrentlyChatWith,
  makeSelectConversations,
  makeSelectChatIsOpen
}
