import { call, put, select, takeLatest } from 'redux-saga/effects'
import i18n from 'i18n'
import { LOAD_HOME_PAGE_CONTENT } from './constants'

// PL
import userContentPathPl from '../../translations/pl/home-user.md'
import operatorContentPathPl from '../../translations/pl/home-operator.md'
import adminContentPathPl from '../../translations/pl/home-admin.md'

// EN
import userContentPathEn from '../../translations/en/home-user.md'
import operatorContentPathEn from '../../translations/en/home-operator.md'
import adminContentPathEn from '../../translations/en/home-admin.md'

import { makeSelectUser } from 'views/App/selectors'
import { homePageContentLoaded, loadHomePageContentError } from './actions'

function getContentPath (userType) {
  if (i18n.language === 'pl') {
    switch (userType) {
      case 'admin':
        return adminContentPathPl
      case 'operator':
        return operatorContentPathPl
      default:
        return userContentPathPl
    }
  }
  if (i18n.language === 'en') {
    switch (userType) {
      case 'admin':
        return adminContentPathEn
      case 'operator':
        return operatorContentPathEn
      default:
        return userContentPathEn
    }
  }
}

async function getContent (path) {
  const response = await fetch(path)
  return await response.text()
}

export function * getHomePageContent () {
  try {
    const user = yield select(makeSelectUser())
    const contentPath = getContentPath(user.userType)
    const content = yield call(getContent, contentPath)

    yield put(homePageContentLoaded(content))
  } catch (err) {
    yield put(loadHomePageContentError(err))
  }
}

export default function * watch () {
  yield takeLatest(LOAD_HOME_PAGE_CONTENT, getHomePageContent)
}
