import { useMsal } from '@azure/msal-react'
import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import {
  Button,
  Card, CardBody, CardHeader, CardTitle, Col, FormGroup,
  Input, Row
} from 'reactstrap'
import { adminService, operatorService, clientService } from '../../services'
import { modalTypeConstants, userRolesConstants } from '../../constants'
import { useDispatch, useSelector } from 'react-redux'
import { showLoadingScreen, hideLoadingScreen } from 'views/Modal/actions'

const initlUserState = {
  licenceNumber: 0,
  inZbpList: false,
  mail: '',
  address: '',
  id: '',
  name: '',
  nameWithTitle: '',
  degree: '',
  firstName: '',
  lastName: '',
  description: '',
  phone: ''
}

function EditUser (props) {
  const { accounts } = useMsal()
  const { t } = useTranslation('common')
  const user = useSelector(state => state.global.user)
  const dispatch = useDispatch()
  
  const [userInfo, setUserInfo] = useState(initlUserState)
  const [address, setAddress] = useState({ city: userInfo.address.split(',')[2], postalCode: userInfo.address.split(',')[1], street: userInfo.address.split(',')[0] })
  const isOper = user.userType === userRolesConstants.OPERATOR
  const isAdmin = user.userType === userRolesConstants.ADMIN
  const isClient = user.userType === userRolesConstants.CLIENT

  useEffect(() => {
    if (isAdmin) {
      adminService.getAdminDataById(accounts[0].idTokenClaims.oid).then(res => {
        updateStates(res)
      })
    }
    if (isOper) {
      operatorService.getOperatorData(accounts[0].idTokenClaims.oid).then(res => {
        updateStates(res)
      })
    }
    if (isClient) {
      clientService.getClientData(accounts[0].idTokenClaims.oid).then(res => {
        updateStates(res)
      })
    }
  }, [accounts, isAdmin, isOper, isClient])

  return (
    <>
      {' '}
      <Row>
        <Col md='8'>
          <Card className='card-user'>
            <CardHeader>
              <CardTitle tag='h5'>{t('userProfile.profileEdition')}</CardTitle>
            </CardHeader>
            <CardBody>
              <div>
                <Row>
                <Col className='pl-1' md='6'>
                  <FormGroup>
                    <label htmlFor='exampleInputEmail1'>{t('userProfile.userEmail')}</label>
                    <Input
                      placeholder='Email'
                      type='email'
                      value={userInfo.mail}
                      readOnly
                    />
                    </FormGroup>
                </Col>
                </Row>
                <Row>
                  <Col className='pr-1' md='6'>
                    <FormGroup>
                      <label>{t('userProfile.userName')}</label>
                      <Input
                        placeholder=''
                        type='text'
                        value={userInfo.name}
                        onChange={(e) => setUserInfo(prev => ({ ...prev, name: e.target.value }))}
                      />
                    </FormGroup>
                  </Col>
                  <Col className='pl-1' md='6'>
                    <FormGroup>
                      <label htmlFor='phone'>{t('userProfile.phoneNumber')}</label>
                      <Input
                        placeholder=''
                        type='phone'
                        value={userInfo.phone}
                        onChange={(e) => setUserInfo(prev => ({ ...prev, phone: e.target.value }))}
                      />
                    </FormGroup>
                  </Col>
                </Row>
                <Row>
                  <Col className='pr-1' md='6'>
                    <FormGroup>
                      <label>{t('userProfile.userFirstName')}</label>
                      <Input
                        value={userInfo.firstName}
                        placeholder='Imię'
                        type='text'
                        onChange={(e) => setUserInfo(prev => ({ ...prev, firstName: e.target.value }))}
                      />
                    </FormGroup>
                  </Col>
                  <Col className='pl-1' md='6'>
                    <FormGroup>
                      <label>{t('userProfile.userLastName')}</label>
                      <Input
                        value={userInfo.lastName}
                        placeholder='Nazwisko'
                        type='text'
                        onChange={(e) => setUserInfo(prev => ({ ...prev, lastName: e.target.value }))}
                      />
                    </FormGroup>
                  </Col>
                </Row>
                {!isClient && <Row>
                  <Col className='pr-1' md='6'>
                    <FormGroup>
                      <label>{t('userProfile.userDegree')}</label>
                      <Input
                        onChange={(e) => setUserInfo(prev => ({ ...prev, degree: e.target.value }))}
                        value={userInfo.degree}
                        placeholder='tytuł naukowy'
                        type='text'
                      />
                    </FormGroup>
                  </Col>
                  <Col className='pr-1' md='6'>
                    <FormGroup>
                      <label>{t('userProfile.userLicenceNumber')}</label>
                      <Input
                        value={userInfo.licenceNumber}
                        type='number'
                        defaultValue='Nr Licencji'
                        onChange={e => {
                          if (e.target.value.startsWith('-') || e.target.value < 0 || e.target.value === '') {
                            e.target.value = ''
                          }
                          e.target.value >= 0 && setUserInfo(prev => ({ ...prev, licenceNumber: parseInt(e.target.value) }))
                        }}
                      />
                    </FormGroup>
                  </Col>
                </Row>}
                {!isClient &&
                  <Row>
                    <Col md='12'>
                      <FormGroup>
                        <label>{t('userProfile.aboutMe')}</label>
                        <Input
                          defaultValue={userInfo.description}
                          type='textarea'
                          onChange={e => setUserInfo(prev => ({ ...prev, description: e.target.value }))}
                        />
                      </FormGroup>
                    </Col>
                  </Row>}
                {!isClient && <Row>
                  
                  {!isClient && <Col md='12'>
                    <FormGroup>
                        <FormGroup>
                          <Input
                            style={{marginRight: "5px"}}
                            type='checkbox'
                            name='zbpList'
                            id='zbpList'
                            checked={userInfo.inZbpList}
                            onClick={switchCheckbox}
                           />
                          <label for='zbpList'>
                            {t('userProfile.inZbpList')}
                          </label>
                        </FormGroup>
                    </FormGroup>
                  </Col>}
                </Row>}
                <Row>
                  <div className='update ml-auto mr-auto'>
                    <Button
                      className='btn-round'
                      color='primary'
                      type='submit'
                      onClick={() => { sendUpdate() }}
                    >
                      {t('userProfile.buttons.update')}
                    </Button>
                  </div>
                </Row>
              </div>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </>
  )

  function sendUpdate () {
    dispatch(showLoadingScreen(modalTypeConstants.LOADING, { message: 'saveUserData', translate: true }))
    const update = JSON.parse(JSON.stringify(userInfo))
    update.address = `${address.street},${address.city},${address.postalCode}`
    if (isAdmin) {
      adminService.updateUserInfo(update).then(() => dispatch(hideLoadingScreen()))
    }

    if (isOper) {
      operatorService.updateOper(update).then(() => dispatch(hideLoadingScreen()))
    }
    if (isClient) {
      clientService.updateClient(update).then(() => dispatch(hideLoadingScreen()))
    }
  }

  function switchCheckbox () {
    setUserInfo(prev => ({ ...prev, inZbpList: !userInfo.inZbpList }))
  }

  function updateStates (res) {
    setUserInfo(res)
    setAddress({ city: res.address.split(',')[1], postalCode: res.address.split(',')[2], street: res.address.split(',')[0] })
  }
}

export default EditUser
