import { modalTypeConstants } from '../../constants'
import { call, put, select, takeEvery } from 'redux-saga/effects'
import { callForHeader } from 'utils/HeadersCall'
import request from 'utils/request'
import { makeSelectUser } from 'views/App/selectors'
import { showLoadingScreen, hideLoadingScreen } from 'views/Modal/actions'

import { loadOperatorProfileDataError, loadTrainings, loadTrainingsError, operatorProfileDataLoaded, trainingsLoaded } from './actions'
import { ADD_TRAINING, LOAD_OPERATOR_PROFILE_DATA, LOAD_TRAININGS, REMOVE_TRAINING } from './constants'

export function * getTrainings () {
  const user = yield select(makeSelectUser())
  const operatorId = user.user.id
  const requestUrl = `/Operator/${operatorId}/training`
  const headers = yield call(callForHeader)
  const options = { method: 'GET', headers }
  try {
    const trainings = yield call(request, requestUrl, options)
    yield put(trainingsLoaded(trainings))
  } catch (err) {
    yield put(loadTrainingsError(err))
  }
}

export function * getOperatorProfileData (action) {
  const operatorId = action.payload
  const headers = yield call(callForHeader)
  const options = { method: 'GET', headers }
  try {
    yield put(showLoadingScreen(modalTypeConstants.LOADING, { message: 'loadUserData', translate: true }))
    const userType = yield call(request, `/User/id/${operatorId}`, options)
    const operatCount = yield call(request, `/Operat/${operatorId}/count`, options)
    const reviews = yield call(request, `/Operator/${operatorId}/review`, options)
    const trainings = yield call(request, `/Operator/${operatorId}/training`, options)
    const diploma = yield call(request, `/OperatorFiles/id/${operatorId}/filename/diploma.pdf`, options)
    let userUrl
    switch (userType) {
      case 'client': userUrl = `/Client/id/${operatorId}`; break
      case 'operator': userUrl = `/Operator/id/${operatorId}`; break
      case 'admin': userUrl = `/Admin/id/${operatorId}`; break
      default: throw Error('Invalid user type')
    }
    const operator = yield call(request, userUrl, options)

    yield put(operatorProfileDataLoaded({
      operatCount, reviews, trainings, diploma, operator
    }))
    yield put(hideLoadingScreen())
  } catch (err) {
    yield put(loadOperatorProfileDataError(err))
    yield put(showLoadingScreen(modalTypeConstants.ERROR_MESSAGE, { message: 'loadUserDataFailed', translate: true }))
  }
}

export function * addTraining () {
  const formElement = document.querySelector('#operator-training-form')
  const user = yield select(makeSelectUser())
  const data = new FormData(formElement)
  const body = {
    operatorId: user.user.id,
    title: data.get('training-title'),
    url: data.get('training-url')
  }
  const requestUrl = '/Operator/training'
  const headers = yield call(callForHeader, 'json')
  const options = { method: 'POST', headers, body: JSON.stringify(body) }
  try {
    yield call(request, requestUrl, options)
    yield put(loadTrainings())
  } catch (err) {
    console.log(err)
  }
}

export function * removeTraining (action) {
  const requestUrl = `/Operator/training/${action.id}`
  const headers = yield call(callForHeader)
  const options = { method: 'DELETE', headers }
  try {
    yield call(request, requestUrl, options)
    yield put(loadTrainings())
  } catch (err) {
    console.log(err)
  }
}

export default function * userData () {
  yield takeEvery(LOAD_TRAININGS, getTrainings)
  yield takeEvery(LOAD_OPERATOR_PROFILE_DATA, getOperatorProfileData)
  yield takeEvery(ADD_TRAINING, addTraining)
  yield takeEvery(REMOVE_TRAINING, removeTraining)
}
