import React, { useState, useEffect } from 'react'
import PerfectScrollbar from 'perfect-scrollbar'
import { Route, Switch } from 'react-router-dom'
import DemoNavbar from 'components/Navbars/DemoNavbar'
import Footer from 'components/Footer/Footer'
import Sidebar from 'components/Sidebar/Sidebar'
import FixedPlugin from 'components/FixedPlugin/FixedPlugin'
import ScrollButton from 'components/FixedPlugin/ScrollButton'
import { useTranslation } from 'react-i18next'
import { operatorRoutes, clientRoutes, adminRoutes, emptyRoutes } from '../routes'
import { useDispatch, useSelector } from 'react-redux'
import { callForHeader } from 'utils/HeadersCall'
import { loadUser, loadEkwData, loadPublicSettings, loadOperatTypes } from 'views/App/actions'
import { useMsal } from '@azure/msal-react'

let ps

function GeneralLayout (props) {
  const dispatch = useDispatch()
  const { accounts } = useMsal()
  const [backgroundColor, setBackgroundColor] = useState('black')
  const [mainBackgroundColor, setMainBackgroundColor] = useState('#f4f3ef')
  const [activeColor, setActiveColor] = useState('success')
  const mainPanel = React.createRef()
  const [routes, setRoutes] = useState([])
  const user = useSelector(state => state.global.user)
  const { t, i18n } = useTranslation('common')
  const [fixedPluginLabels, setFixedPluginLabels] = useState({
    mainBg:'',
    menuBg:'',
    activeColor:''
  })

  // eslint-disable-next-line
  useEffect(async () => {
    await callForHeader()
    dispatch(loadEkwData())
    dispatch(loadPublicSettings())
    dispatch(loadOperatTypes())
    user.userType === '' && dispatch(loadUser(accounts[0].idTokenClaims.oid))
    if (navigator.platform.indexOf('Win') > -1) {
      ps = new PerfectScrollbar('#wrapper')
    }
    chooseRouteByUserType()

    return function cleanup () {
      if (navigator.platform.indexOf('Win') > -1) {
        ps.destroy()
      }
    }
  // eslint-disable-next-line
  }, [])

  useEffect(() => {
    chooseRouteByUserType()
    const labels = {
      mainBg: t('fixedPlugin.mainBg'),
      menuBg: t('fixedPlugin.menuBg'),
      activeColor: t('fixedPlugin.activeColor')
    }
    setFixedPluginLabels(labels)
    // eslint-disable-next-line
  }, [user.userType, i18n.language])

  const handleActiveClick = (color) => {
    setActiveColor(color)
  }
  const handleBgClick = (color) => {
    setBackgroundColor(color)
  }
  const handleMainBgClick = (color) => {
    setMainBackgroundColor(color)
  }

  const chooseRouteByUserType = () => {
    switch (user.userType) {
      case 'admin':
        setRoutes(adminRoutes(t)); break
      case 'operator':
        setRoutes(operatorRoutes(t)); break
      case 'client':
        setRoutes(clientRoutes(t)); break
      default:
        setRoutes(emptyRoutes(t)); break
    }
  }

  return (
    <div className='wrapper' id='wrapper' style={{ backgroundColor: mainBackgroundColor }}>
      <Sidebar
        {...props}
        userType={user.userType}
        routes={routes}
        bgColor={backgroundColor}
        activeColor={activeColor}
      />

      <div className='main-panel' ref={mainPanel} style={{ backgroundColor: mainBackgroundColor }}>
        <DemoNavbar {...props} userType={user.userType} routes={routes} t={t} />
        <Switch>
          {routes.map((prop, key) => {
            const Component = prop.component
            return (
              <Route path={prop.path} key={key}>
                <Component />
              </Route>
            )
          })}
        </Switch>
        <Footer fluid />
      </div>
      <FixedPlugin
        labels={fixedPluginLabels}
        bgColor={backgroundColor}
        activeColor={activeColor}
        mainBgColor={mainBackgroundColor}
        handleActiveClick={handleActiveClick}
        handleBgClick={handleBgClick}
        handleMainBgClick={handleMainBgClick}
      />
      <ScrollButton />
    </div>
  )
}

export default GeneralLayout
