/* eslint-disable default-param-last */
import produce from 'immer'
import {
  EKW_DATA_LOADED,
  LOAD_OPERAT_TYPES,
  LOAD_OPERAT_TYPES_ERROR,
  LOAD_USER,
  LOAD_USER_ERROR,
  OPERAT_TYPES_LOADED,
  PUBLIC_SETTINGS_LOADED,
  USER_LOADED
} from './constants'

export const initialState = {
  operatTypes: {
    loading: false,
    error: false,
    operatTypes: []
  },
  user: {
    userType: '',
    loading: false,
    error: false,
    user: {}
  },
  ekw: {
    regions: [],
    weights: [],
    multipliers: []
  },
  publicSettings: {}
}

/* eslint-disable default-case, no-param-reassign */
const globalReducer = (state = initialState, action) =>
  produce(state, (draft) => {
    switch (action.type) {
      case LOAD_OPERAT_TYPES:
        draft.operatTypes.operatTypes = []
        draft.operatTypes.error = false
        draft.operatTypes.loading = true
        break
      case OPERAT_TYPES_LOADED:
        draft.operatTypes.operatTypes = action.payload
        draft.operatTypes.loading = false
        draft.operatTypes.error = false
        break
      case LOAD_OPERAT_TYPES_ERROR:
        draft.operatTypes.loading = false
        draft.operatTypes.error = action.payload
        break
      case LOAD_USER:
        draft.user.loading = true
        draft.user.error = false
        draft.user.user = {}
        draft.user.userType = ''
        break
      case USER_LOADED:
        draft.user.userType = action.payload.userType
        draft.user.user = action.payload.user
        draft.user.loading = false
        draft.user.error = false
        break
      case LOAD_USER_ERROR:
        draft.user.loading = false
        draft.user.error = action.payload
        break
      case EKW_DATA_LOADED:
        draft.ekw = action.payload
        break
      case PUBLIC_SETTINGS_LOADED:
        draft.publicSettings = action.payload
        break
    }
  })
export default globalReducer
