import React, { useEffect } from 'react'
import {
  Card,
  Row,
  Col,
  UncontrolledCollapse,
  Button,
  CardBody
} from 'reactstrap'
import ReadonlyTable from 'components/Tables/ReadonlyTable'
import OperatorTrainingForm from 'components/OperatorTrainingForm'
import { useTranslation } from 'react-i18next'
function OperatorTrainingsTab (props) {
  const { t } = useTranslation('common')
  useEffect(() => {
    props.handleLoadTrainings()
  // eslint-disable-next-line
  }, [])
  return (
    <>
      <Row>
        <Col md='8'>
          <Card>
            <CardBody>
              <Button color='success' id='show-operator-training-form-btn' style={{ width: '11rem' }}>{t('userProfile.buttons.createTraining')}</Button>
              <UncontrolledCollapse toggler='#show-operator-training-form-btn'>
                <Card style={{ boxShadow: '6px 6px 10px 2px rgb(0 0 0 / 15%)' }}>
                  <CardBody>
                    <OperatorTrainingForm onSubmit={props.handleAddTraining} />
                  </CardBody>
                </Card>
              </UncontrolledCollapse>
              <br />
              <Button color='success' id='show-operator-trainings-btn' style={{ width: '11rem' }}>{t('userProfile.buttons.showTrainings')}</Button>
              <UncontrolledCollapse toggler='#show-operator-trainings-btn'>
                <Card style={{ boxShadow: '6px 6px 10px 2px rgb(0 0 0 / 15%)' }}>
                  <CardBody>
                    {props.trainings.trainings && (props.trainings.trainings.length === 0
                      ? 'Brak szkoleń'
                      : <ReadonlyTable
                          headers={['Tytuł', 'Adres']}
                          data={props.trainings.trainings}
                          deleteAction={(id) => props.handleRemoveTraining(id)}
                        />)}
                  </CardBody>
                </Card>
              </UncontrolledCollapse>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </>
  )
}

export default OperatorTrainingsTab
