import confirm from 'reactstrap-confirm'

export const showConfirmBox = async (message, title, okFunction) => {
  const result = await confirm({
    title: <>{title}</>,
    message,
    confirmText: 'Usuń',
    cancelText: 'Anuluj',
    confirmColor: 'danger',
    cancelColor: 'link text-primary'
  })
  if (result) {
    okFunction()
  }
}
