import { modalTypeConstants } from '../../constants'
import { call, put, takeEvery } from 'redux-saga/effects'
import { callForHeader } from 'utils/HeadersCall'
import request from 'utils/request'
import { hideLoadingScreen, showLoadingScreen } from 'views/Modal/actions'
import { ekwDataLoaded, loadOperatTypesError, loadUserError, operatTypesLoaded, publicSettingsLoaded, userLoaded } from './actions'
import { LOAD_EKW_DATA, LOAD_OPERAT_TYPES, LOAD_PUBLIC_SETTINGS, LOAD_USER } from './constants'
import { handleRequestWithTimeout } from 'views/Modal/timeout'

export function * getOperatTypes () {
  const requestUrl = '/OperatType'
  const headers = yield call(callForHeader)
  const options = { method: 'GET', headers }
  try {
    const operatTypes = yield call(request, requestUrl, options)
    yield put(operatTypesLoaded(operatTypes))
  } catch (err) {
    yield put(loadOperatTypesError(err))
  }
}

export function * getUser (action) {
  const userId = action.id
  const headers = yield call(callForHeader)
  const options = { method: 'GET', headers }
  let requestUrl = ''
  try {
    const userType = yield call(request, `/User/id/${userId}`, options)
    switch (userType) {
      case 'admin':
        requestUrl = `/Admin/id/${userId}`
        break
      case 'client':
        requestUrl = `/Client/id/${userId}`
        break
      case 'operator':
        requestUrl = `/Operator/id/${userId}`
        break
      default:
    }
    if (requestUrl === '') {
      yield put(loadUserError('user not found'))
    } else {
      yield put(showLoadingScreen(modalTypeConstants.LOADING, { message: 'loadingData', translate: true }))

      const user = yield call(handleRequestWithTimeout, requestUrl, options)
      yield put(userLoaded({ user, userType }))
      yield put(hideLoadingScreen())
    }
  } catch (err) {
    yield put(hideLoadingScreen())
    yield put(loadUserError(err))
  }
}

export function * getEkwData () {
  const regionsUrl = '/LandRegister/regions'
  const weightsUrl = '/LandRegister/weights'
  const multipliersUrl = '/LandRegister/multipliers'
  const headers = yield call(callForHeader)
  const options = { method: 'GET', headers }
  try {
    const regions = yield call(request, regionsUrl, options)
    const weights = yield call(request, weightsUrl, options)
    const multipliers = yield call(request, multipliersUrl, options)
    yield put(ekwDataLoaded({ regions, weights, multipliers }))
  } catch (err) {
    console.log(err)
  }
}

export function * getPublicSettings () {
  const requestUrl = '/Info/PublicSettings'
  const headers = yield call(callForHeader)
  const options = { method: 'GET', headers }
  try {
    const publicSettings = yield call(request, requestUrl, options)
    yield put(publicSettingsLoaded(publicSettings))
  } catch (err) {
    console.log(err)
  }
}

export default function * watch () {
  yield takeEvery(LOAD_OPERAT_TYPES, getOperatTypes)
  yield takeEvery(LOAD_USER, getUser)
  yield takeEvery(LOAD_EKW_DATA, getEkwData)
  yield takeEvery(LOAD_PUBLIC_SETTINGS, getPublicSettings)
}
