import { callForHeader } from 'utils/HeadersCall'
const apiUrl = process.env.REACT_APP_API_URL

export const mapService = {
  get
}

async function get (address, type, zoom = 15) {
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/Map/img?address=${address}&type=${type}&zoom=${zoom}`, requestOptions).then(response => response.blob())
}
