import {
  LOAD_ORDERS, SET_ORDERS_PAGE_NUMBER,
  LOAD_ORDERS_ERROR,
  ORDERS_LOADED, ORDER_SAVED, SAVE_ORDER, SAVE_ORDER_ERROR,
  ORDER_REPLACED, LOAD_PROPOSITIONS, PROPOSITIONS_LOADED,
  LOAD_PROPOSITIONS_ERROR, SAVE_PROPOSITION,
  RESERVE_ORDER_AND_PROPOSITION, UNRESERVE_PROPOSITION, SET_PROPOSITION_ID,
  PROPOSITION_REPLACED, SET_ACTIVE_ORDER, REPLACE_ORDER,
  REMOVE_ORDER, ORDER_REMOVED, SET_ORDER_SORT_PARAMETERS, SET_ORDER_FILTERS, SET_PROPOSITIONS_SORT_PARAMETERS, SET_PROPOSITIONS_FILTER, CLEAR_PROPOSITION_FILTERS, PAY_FOR_OPERAT
} from './constants'

export function loadOrders () {
  return {
    type: LOAD_ORDERS
  }
}

export function ordersLoaded (orders) {
  return {
    type: ORDERS_LOADED,
    payload: orders
  }
}

export function loadOrdersError (error) {
  return {
    type: LOAD_ORDERS_ERROR,
    payload: error
  }
}

export function setOrdersPageNumber (pageNumber) {
  return {
    type: SET_ORDERS_PAGE_NUMBER,
    payload: pageNumber
  }
}

export function saveOrder (order) {
  return {
    type: SAVE_ORDER,
    payload: order
  }
}

export function orderSaved () {
  return {
    type: ORDER_SAVED
  }
}

export function saveOrderError (err) {
  return {
    type: SAVE_ORDER_ERROR,
    payload: err
  }
}

export function replaceOrder () {
  return {
    type: REPLACE_ORDER
  }
}

export function orderReplaced (order) {
  return {
    type: ORDER_REPLACED,
    payload: order
  }
}

export function loadPropositions () {
  return {
    type: LOAD_PROPOSITIONS
  }
}

export function propositionsLoaded (propositions) {
  return {
    type: PROPOSITIONS_LOADED,
    payload: propositions
  }
}

export function loadPropositionsError (error) {
  return {
    type: LOAD_PROPOSITIONS_ERROR,
    payload: error
  }
}

export function saveProposition () {
  return {
    type: SAVE_PROPOSITION
  }
}

export function reserveOrderAndProposition () {
  return {
    type: RESERVE_ORDER_AND_PROPOSITION
  }
}

export function unReserveProposition () {
  return {
    type: UNRESERVE_PROPOSITION
  }
}

export function setPropositionId (id) {
  return {
    type: SET_PROPOSITION_ID,
    payload: id
  }
}

export function propositionReplaced (proposition) {
  return {
    type: PROPOSITION_REPLACED,
    payload: proposition
  }
}

export function removeOrder () {
  return {
    type: REMOVE_ORDER
  }
}

export function setActiveOrder (order) {
  return {
    type: SET_ACTIVE_ORDER,
    payload: order
  }
}

export function orderRemoved (id) {
  return {
    type: ORDER_REMOVED,
    payload: id
  }
}

export function setOrderSortParameters (fieldName, sortDirection) {
  return {
    type: SET_ORDER_SORT_PARAMETERS,
    fieldName,
    sortDirection
  }
}

export function setOrderFilters (payload) {
  return {
    type: SET_ORDER_FILTERS,
    payload
  }
}

export function setPropositionsSortParameters (payload) {
  return {
    type: SET_PROPOSITIONS_SORT_PARAMETERS,
    payload
  }
}

export function setPropositionFilters (payload) {
  return {
    type: SET_PROPOSITIONS_FILTER,
    payload
  }
}

export function clearPropositionFilters () {
  return {
    type: CLEAR_PROPOSITION_FILTERS
  }
}

export function payForOperat () {
  return {
    type: PAY_FOR_OPERAT
  }
}
