import React, { memo } from 'react'
import { modalTypeConstants } from '../../constants'
import { Modal } from 'reactstrap'

import { Modals } from './Modals.collection'
import { hideLoadingScreen, showLoadingScreen } from './actions'
import { connect } from 'react-redux'
import { compose } from 'redux'
import { makeSelectData, makeSelectShowModal, makeSelectType } from './selectors'
import { createStructuredSelector } from 'reselect'
import { useInjectReducer } from 'utils/injectReducer'
import reducer from './reducer'
const key = 'modal'
const LoadingModal = (props) => {
  useInjectReducer({ key, reducer })
  const renderModal = () => {
    switch (props.type) {
      case modalTypeConstants.SHOW_NO_DATA:
        return <Modals.ShowNoData />
      case modalTypeConstants.CONFIRM_SEND_PROPOSITION:
        return <Modals.ConfirmSendProposition />
      case modalTypeConstants.TOO_MUCH_OPERATOR_FILES:
        return <Modals.ToMuchOperatorFiles />
      case modalTypeConstants.PROMOTE_USER_ERROR:
        return <Modals.PromoteUserError />
      case modalTypeConstants.SEND_OPERAT_CONFIRMATION:
        return <Modals.SendOperatConfirmation />
      case modalTypeConstants.SIGN_OPERAT:
        return <Modals.SignOperat />
      case modalTypeConstants.OWN_CERTIFICATE:
        return <Modals.OwnCertificate />
      case modalTypeConstants.NEW_CERTIFICATE:
        return <Modals.NewCertificate />
      case modalTypeConstants.ERROR_MESSAGE:
        return <Modals.ErrorMessage />
      case modalTypeConstants.LOADING:
        return <Modals.Loading />
      case modalTypeConstants.OPERATS_LIST_MODAL:
        return <Modals.OperatList />
      case modalTypeConstants.SEND_OPERAT_MODAL:
        return <Modals.SendOperat />
      case modalTypeConstants.PAY_FOR_OPERAT:
        return <Modals.PayForOperat />
      case modalTypeConstants.CREATE_SUBSCRIPTION_FORM:
        return <Modals.CreateSubscriptionForm />
      case modalTypeConstants.CREATE_SUBSCRIPTION_SUCCESSFUL:
        return <Modals.CreateSubscriptionSuccessful />
      case modalTypeConstants.CONFIRM_RESET_SUBSCRIPTIONS:
        return <Modals.ConfirmResetSubscriptions />
      case modalTypeConstants.RESET_SUBSCRIPTIONS_SUCCESSFUL:
        return <Modals.ResetSubscriptionsSuccessful />
      case modalTypeConstants.ADD_ATTACHMENTS:
        return <Modals.AddAttachments />
      case modalTypeConstants.ATTACHMENTS_OPTIONS:
        return <Modals.AttachmentsOptions />
      case modalTypeConstants.CONFIRM_SUCCESS:
        return <Modals.ConfirmSuccess />
      case modalTypeConstants.CONFIRM_ERROR:
        return <Modals.ConfirmError />
      default:
        break
    }
  }

  return (
    <div>
      <Modal isOpen={props.showModal}>{renderModal()}</Modal>
    </div>
  )
}

const mapStateToProps = createStructuredSelector({
  showModal: makeSelectShowModal(),
  type: makeSelectType(),
  data: makeSelectData()
})

export function mapDispatchToProps (dispatch) {
  return {
    handleShowLoadingScreen: () => dispatch(showLoadingScreen()),
    handleHideLoadingScreen: () => dispatch(hideLoadingScreen())
  }
}
const withConnect = connect(mapStateToProps, mapDispatchToProps)

export default compose(withConnect, memo)(LoadingModal)
