/* eslint-disable default-param-last */
import produce from 'immer'
import { toDateString } from 'utils/DateFormat'
import {
  LOAD_MESSAGES_WITH_USER,
  SET_CURRENTLY_CHAT_WITH,
  SET_CONNECTION,
  SET_MESSAGES_WITH_USER,
  LOAD_CONVERSATIONS,
  MARK_CONVERSATION_AS_READ,
  MARK_CONVERSATION_AS_UNREAD,
  SET_CHAT_IS_OPEN,
  TOGGLE_MESSAGE_VISIBLE,
  MARK_MESSAGES_AS_SEEN
} from './constants'

export const initialState = {
  connection: {},
  messages: {},
  currentlyChatWith: {},
  conversations: [],
  chatIsOpen: false
}

const markConversationAs = (draft, userId, status) => {
  const found = draft.conversations.find(x => x.client.id === userId)
  if (!found) return
  const index = draft.conversations.indexOf(found)
  found.unread = status
  draft.conversations[index] = found
}
/* eslint-disable default-case, no-param-reassign */
const chatReducer = (state = initialState, action) =>
  produce(state, (draft) => {
    switch (action.type) {
      case LOAD_MESSAGES_WITH_USER:
        draft.messages[action.userId] = action.messages.map(x => ({ ...x, date: toDateString(x.date, action.monthNames) }))
        break
      case SET_CURRENTLY_CHAT_WITH:
        draft.currentlyChatWith = action.payload
        break
      case SET_CONNECTION:
        draft.connection = action.payload
        break
      case SET_MESSAGES_WITH_USER:
        draft.messages[action.userId] = Object.prototype.hasOwnProperty.call(draft.messages, action.userId) ? draft.messages[action.userId] = [...draft.messages[action.userId], action.message] : [action.message]
        break
      case LOAD_CONVERSATIONS:
        draft.conversations = action.payload
        break
      case MARK_CONVERSATION_AS_READ:
        markConversationAs(draft, action.payload, false)
        break
      case MARK_CONVERSATION_AS_UNREAD:
        markConversationAs(draft, action.payload, true)
        break
      case SET_CHAT_IS_OPEN:
        draft.chatIsOpen = action.payload
        break
      case MARK_MESSAGES_AS_SEEN:
        for (let i = 0; i < draft.messages[action.userId].length; i++) {
          if (draft.messages[action.userId][i].author === 'me') {
            draft.messages[action.userId][i].status = 'seen'
          }
        }
        break
      case TOGGLE_MESSAGE_VISIBLE:
        for (let i = 0; i < draft.messages[action.userId].length; i++) {
          if (draft.messages[action.userId][i].id === action.messageId) { continue }
          draft.messages[action.userId][i].isStatusVisible = false
          draft.messages[action.userId][i].isDateVisible = false
        }
        var found = draft.messages[action.userId].find(x => x.id === action.messageId)
        if (!found) break
        found.isStatusVisible = !found.isStatusVisible
        found.isDateVisible = !found.isDateVisible
        break
    }
  })
export default chatReducer
